<template>
  <transition name="fade-in-linear">
    <div v-if="isShow" class="wrapper" @click.self="handleWrapperClick">
      <div class="container" ref="container">
        <a class="close" href="javascript:;" @click="show = false">
          <i class="el-icon-close"></i>
        </a>
        <div class="title change_bg_color">
          {{ $t("alertComponent.quickElectionAmount") }}
        </div>
        <div class="content">
          <p class="tips">
            {{ $t("alertComponent.BeCareful") }}
          </p>
          <div class="default-amount-list">
            <div
              class="default-amount-item"
              :key="item.key"
              v-for="item in amountArr"
            >
              <input
                type="text"
                @input="checkInputSelectAmount(item)"
                v-model="item.value"
              />
              <a
                class="change_btn"
                @click="clearAmount(item)"
                href="javascript:;"
                >{{ $t("alertComponent.empty") }}</a
              >
            </div>
          </div>
          <div class="btns">
            <a
              class="change_btn"
              @click="saveDefaultAmountArr"
              href="javascript:;"
              >{{ $t("alertComponent.confirm") }}</a
            >
            <a class="change_btn" href="javascript:;" @click="show = false">{{
              $t("alertComponent.cancel")
            }}</a>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { alertMixin } from "../mixin";
import { mapGetters } from "vuex";
import {
  SET_SELECT_DEFAULT_DATA,
  SET_HANDICAP_CHOICE_WAY
} from "../../../store/nameSpace";
import { isOpenAnimation } from "../../../config/game-default-config";

export default {
  name: "kxje-alert",
  mixins: [alertMixin],
  data() {
    return {
      name: "kxje-alert",
      bonusGroup: "",
      multiple: 0,
      defaultAmount01: 5,
      amountArr: []
    };
  },
  computed: {
    ...mapGetters(["quickSelectAmountData"])
  },
  methods: {
    clearAmount(item) {
      item.value = "";
    },
    checkInputSelectAmount(item) {
      item.value = item.value.replace(/[^(\d)]/g, "");
    },
    saveDefaultAmountArr() {
      if (
        this.amountArr.find(item => {
          return !Number.isInteger(+item.value) || +item.value <= 0;
        })
      ) {
        return this.$message({
          message: this.$t("errorMessage.errorMessage8"),
          type: "error"
        });
      }
      this.$store.commit(
        SET_SELECT_DEFAULT_DATA,
        this.amountArr.map(item => {
          return +item.value;
        })
      );
      this.show = false;
      this.$message({
        message: this.$t("successMessage.successMessage5"),
        type: "success"
      });
    }
  },
  watch: {
    show(val) {
      if (val) {
        this.isShow = val;
        if (isOpenAnimation) {
          setTimeout(() => {
            this.$refs.container.style.transition = "all 0.5s";
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        } else {
          setTimeout(() => {
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        }
      } else {
        this.$store.commit(SET_HANDICAP_CHOICE_WAY, 0);
        if (isOpenAnimation) {
          this.$refs.container.style.top = "40%";
          this.$refs.container.style.opacity = 0;
          setTimeout(() => {
            this.isShow = val;
          }, 500);
        } else {
          this.isShow = val;
        }
      }
    }
  },
  created() {
    this.$set(this, "amountArr", []);
    this.quickSelectAmountData.map((item, index) => {
      this.amountArr.push({
        key: index,
        value: item
      });
    });
  },
  mounted() {}
};
</script>

<style scoped>
@import "../statics/css/transition.css";
.container {
  height: 360px;
}
.container .content {
  padding: 15px 19px 19px 17px;
}
.container .content p.tips {
  color: #ff0000;
  line-height: 20px;
  font-size: 12px;
  margin-bottom: 24px;
}
.default-amount-list .default-amount-item {
  width: 205px;
  margin: 0 auto 10px auto;
}
.default-amount-list .default-amount-item > input {
  width: 150px;
  border: 1px solid #a9a9a9;
  text-align: center;
  height: 28px;
  color: #ff0000;
}
.default-amount-list .default-amount-item > a {
  display: inline-block;
  width: 50px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  color: #fff;
  border-radius: 4px;
}
.content .btns {
  width: 140px;
  height: 31px;
  margin: 10px auto 0 auto;
}
.content .btns > a {
  float: left;
  height: 100%;
  width: 60px;
  line-height: 31px;
  border-radius: 3px;
  text-align: center;
  color: #fff;
  font-weight: bold;
  margin: 5px;
}
</style>

<style></style>
