import axios from "axios";
import { GetApiFullUrl } from "../../config/config.js";

const request = data => {
  return new Promise((resolve, reject) => {
    let datas = {};
    let headers = {};
    let params = {};
    if (data.method === "post") {
      datas = data.data;
      if (datas === undefined) {
        console.warn(`请求${data.url}为post请求，但未提效任何数据`);
        datas = {};
      }
    } else if (data.method == "get") {
      params = data.data;
      if (params === undefined) params = {};
    }
    if (data.headers) {
      headers = data.headers;
    }
    axios
      .request({
        url: GetApiFullUrl(data.url),
        data: datas,
        params: params,
        method: data.method,
        headers: headers
      })
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err);
      });
  });
};
export default request;
