<template>
  <transition name="fade-in-linear">
    <div v-if="isShow" class="wrapper" @click.self="handleWrapperClick">
      <div class="container" ref="container">
        <a class="close" @click="closeAlertBox" href="javascript:;">
          <i class="el-icon-close"></i>
        </a>
        <div class="popup-title title change_bg_color">{{ titleName }}</div>
        <div
          v-if="titleName === `${$t('alertComponent.noteDetails')}`"
          class="popup-content content"
        >
          <div class="popup-info">
            <h4>{{ $t("alertComponent.NoteNumber") }}:{{ dataList.id }}</h4>
            <hr />
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.Username") }}：</div>
              <div class="row-r">{{ dataList.username }}</div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.issueNumber") }}：</div>
              <div class="row-r">{{ dataList.issue }}</div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.TypesOfPlay") }}：</div>
              <div class="row-r">{{ dataList.way_type }}</div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.lotteryTypes") }}：</div>
              <div class="row-r">{{ dataList.lottery_name }}</div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.lotteryWay") }}：</div>
              <div class="row-r">{{ dataList.way }}</div>
            </div>
            <div class="row" v-if="dataList.position">
              <div class="row-l">投注位置：</div>
              <div class="row-r">{{ dataList.position }}</div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.bettingTime") }}：</div>
              <div class="row-r">{{ dataList.bought_at }}</div>
            </div>
            <div class="row">
              <div class="row-l">
                {{ $t("alertComponent.lotteryNumber") }}：
              </div>
              <div class="row-r">
                <ul v-if="gameMenuType !== '3D' && gameMenuType !== 'LHC'">
                  <li v-for="(item, index) in windNum" :key="index">
                    {{ item }}
                  </li>
                </ul>
                <ul
                  v-if="
                    gameMenuType === '3D' && dataList.winning_number !== null
                  "
                >
                  <li>{{ windNum0 }}</li>
                  +
                  <li>{{ windNum1 }}</li>
                  +
                  <li>{{ windNum2 }}</li>
                  =
                  <li>{{ sumNum }}</li>
                </ul>
                <ul
                  v-if="
                    gameMenuType === 'LHC' && dataList.winning_number !== null
                  "
                >
                  <li
                    v-if="indexInner < 5"
                    v-for="(itemInner, indexInner) in windNum"
                    :key="indexInner"
                  >
                    {{ itemInner }}
                  </li>
                  <li
                    v-if="indexInner === 0"
                    v-for="(itemInner, indexInner) in windNum[5].split('+')"
                    :key="indexInner"
                  >
                    {{ itemInner }}
                  </li>
                  +
                  <li
                    v-if="indexInner === 1"
                    v-for="(itemInner, indexInner) in windNum[5].split('+')"
                    :key="indexInner"
                  >
                    {{ itemInner }}
                  </li>
                </ul>
              </div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.multiple") }}：</div>
              <div class="row-r">
                {{ dataList.multiple }} {{ $t("alertComponent.times")
                }}<i style="font-size: 13px;">({{ dataList.coefficient }})</i>
              </div>
            </div>
            <div class="row">
              <div class="row-l">
                {{ $t("alertComponent.injectionNumber") }}：
              </div>
              <div class="row-r">
                {{ dataList.bet_count }} {{ $t("alertComponent.notes") }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.amountOfNote") }}：</div>
              <div class="row-r">
                {{ amount }} {{ systemSettingConfig.currency }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.noteStatus") }}：</div>
              <div class="row-r" style="color: green;">
                {{ dataList.formatted_status }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">
                {{ $t("alertComponent.winningAmount") }}：
              </div>
              <div class="row-r" v-if="dataList.prize != null">
                {{ dataList.prize }} {{ $t("alertComponent.currency") }}
              </div>
              <div class="row-r" v-else>
                0 {{ systemSettingConfig.currency }}
              </div>
            </div>
          </div>
          <div class="popup-number">
            <h4>{{ $t("alertComponent.bettingContent") }}</h4>
            <hr />
            <div
              style="display:block;word-break: break-all;word-wrap: break-word;"
              class="bet-info"
              contenteditable="false"
            >
              {{ betNumber }}
            </div>
          </div>
        </div>
        <div
          v-if="titleName === `${$t('alertComponent.trackingNumberDetails')}`"
          class="popup-content content"
        >
          <div class="popup-info">
            <h4>{{ $t("alertComponent.TaskNumber") }}:{{ dataList.id }}</h4>
            <hr />
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.Username") }}：</div>
              <div class="row-r">{{ dataList.username }}</div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.lotteryTypes") }}：</div>
              <div class="row-r">{{ dataList.lottery_name }}</div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.lotteryWay") }}：</div>
              <div class="row-r">{{ dataList.way }}</div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.bettingTime") }}：</div>
              <div class="row-r">{{ dataList.bought_at }}</div>
            </div>
            <div class="row">
              <!--<div class="row-l">{{$t('alertComponent.injectionNumber')}}：</div>-->
              <!--<div class="row-r">{{dataList.bet_count}} {{$t('alertComponent.notes')}}</div>-->
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.amountOfNote") }}：</div>
              <div class="row-r">
                {{ dataList.amount }} {{ $t("alertComponent.currency") }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">
                {{ $t("alertComponent.trackingNumber") }}：
              </div>
              <div class="row-r">
                {{ dataList.total_issues }} {{ $t("alertComponent.stage") }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.cancelCounts") }}：</div>
              <div class="row-r">
                {{ dataList.canceled_issues }} {{ $t("alertComponent.stage") }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">
                {{ $t("alertComponent.completeCounts") }}：
              </div>
              <div class="row-r">
                {{ dataList.finished_issues }} {{ $t("alertComponent.stage") }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">
                {{ $t("alertComponent.RemainingPeriod") }}：
              </div>
              <div class="row-r">
                {{
                  dataList.total_issues -
                    dataList.finished_issues -
                    dataList.canceled_issues
                }}
                {{ $t("alertComponent.stage") }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.noteStatus") }}：</div>
              <div class="row-r" style="color: green;">
                {{ dataList.status }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">
                {{ $t("alertComponent.CumulativeWinning") }}：
              </div>
              <div class="row-r" v-if="dataList.prize != null">
                {{ dataList.prize | PrizeFilter }}
                {{ systemSettingConfig.currency }}
              </div>
            </div>
            <div class="row">
              <div class="row-l">{{ $t("alertComponent.NoteBonus") }}：</div>
              <div class="row-r">{{ dataList.prize | PrizeFilter }}</div>
            </div>
            <div class="row">
              <div class="row-l">
                {{ $t("alertComponent.AutomaticStop") }}：
              </div>
              <div class="row-r">{{ dataList.stop_on_won }}</div>
            </div>
          </div>
          <div class="popup-number">
            <h4>{{ $t("alertComponent.bettingContent") }}</h4>
            <hr />
            <div
              style="display:block;word-break: break-all;word-wrap: break-word;"
              class="bet-info"
              contenteditable="false"
            >
              {{ betNumber }}
            </div>
          </div>
        </div>
        <div
          v-if="titleName === `${$t('alertComponent.trackingNumberDetails')}`"
          class="popup-list dialog-seek-log-list"
        >
          <div class="tab-content">
            <div class="tab-pane fade active in">
              <div class="limitwidth">
                <table class="table table-colored dialog-seek-order-table">
                  <thead>
                    <tr>
                      <th>{{ $t("alertComponent.TaskNumber") }}</th>
                      <th class="seek-cycle">
                        {{ $t("alertComponent.issueNumber") }}
                      </th>
                      <th class="seek-multiple">
                        {{ $t("alertComponent.multiple") }}
                      </th>
                      <th class="seek-openball">
                        {{ $t("alertComponent.lotteryNumber") }}
                      </th>
                      <th class="seek-money">
                        {{ $t("alertComponent.winningAmount") }}
                      </th>
                      <th class="seek-money">
                        {{ $t("alertComponent.operation") }}
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, index) in issuesData.data" :key="index">
                      <td>{{ item.id }}</td>
                      <td class="seek-cycle">
                        {{ $t("alertComponent.the") }} {{ item.issue }}
                        {{ $t("alertComponent.stage") }}
                      </td>
                      <td class="seek-multiple">
                        {{ item.multiple }} {{ $t("alertComponent.times") }}
                      </td>
                      <td class="seek-openball">
                        <div
                          v-if="
                            gameMenuType === 'L115' ||
                              gameMenuType === 'KENO' ||
                              gameMenuType === 'KL12' ||
                              gameMenuType === 'PK10' ||
                              gameMenuType === 'KLSF'
                          "
                          v-for="(items, index) in item.wn_number.split(' ')"
                          :key="index"
                          class="result-ball"
                        >
                          <span>{{ items }}</span>
                        </div>
                        <div
                          v-if="gameMenuType === 'SSC' || gameMenuType === 'K3'"
                          v-for="(items, index) in item.wn_number"
                          :key="index"
                          class="result-ball"
                        >
                          <span>{{ items }}</span>
                        </div>
                        <div
                          v-if="
                            gameMenuType === '3D' && item.wn_number !== '--'
                          "
                          v-for="(items, index) in item.wn_number"
                          :key="index"
                          class="result-ball result-3D"
                        >
                          <span>{{ items }}</span
                          >&nbsp;&nbsp;
                        </div>
                        <div
                          class="numSum"
                          style="display: inline-block;"
                          v-if="
                            gameMenuType === '3D' && item.wn_number !== '--'
                          "
                        >
                          {{ item.wn_number | totalFilter }}
                        </div>

                        <!--<div v-if="item.wn_number.indexOf(' ') !== -1" v-for="(items, indexs) in item.wn_number.split(' ')" :key="indexs" class="result-ball">-->
                        <!--<span>{{items}}</span>-->
                        <!--</div>-->
                        <!--<div v-if="item.wn_number.indexOf('&#45;&#45;') !== -1">-->
                        <!--<span>{{item.wn_number}}</span>-->
                        <!--</div>-->
                        <!--<div v-if="item.wn_number.indexOf(' ') === -1 && item.wn_number.indexOf('&#45;&#45;') === -1" v-for="(items, indexs) in item.wn_number" :key="indexs" class="result-ball">-->
                        <!--<span>{{items}}</span>-->
                        <!--</div>-->
                      </td>
                      <td class="seek-money" v-if="item.prize !== '--'">
                        {{ item.prize | PrizeFilter }}
                        {{ systemSettingConfig.currency }}
                      </td>
                      <td class="seek-money" v-if="item.prize === '--'">
                        0{{ $t("alertComponent.currency") }}
                      </td>
                      <td class="changeColor" @click="CancelTraceReserve(item)">
                        {{
                          item.can_stop === 1
                            ? `${$t("alertComponent.cancelTheOrder")}`
                            : ""
                        }}
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import closeIcon from "../../../statics/img/close_icon_game_alert_zhtz.png";
import { alertMixin } from "../mixin";
import { CancelTraceReserve, GetTraceProjectDetail } from "@/api/game/game";
import { isOpenAnimation } from "../../../config/game-default-config";
import { mapGetters } from "vuex";

export default {
  name: "zdxq-alert",
  mixins: [alertMixin],
  data() {
    return {
      closeIcon,
      name: "zdxq-alert",
      dataList: "",
      titleName: "",
      gameMenuType: "" /* 彩种系列 */,
      issuesData: "",
      issuesDataList: [],
      windNum: "",
      windNum0: 0,
      windNum1: 0,
      windNum2: 0,
      sumNum: 0,
      betNumber: "",
      amount: 0,
      aa: 0,
      bb: 0,
      cc: 0
    };
  },
  computed: {
    ...mapGetters(["systemSettingConfig"])
  },
  methods: {
    CancelTraceReserve(item) {
      // 追号撤单
      if (item.can_stop === 0) return;
      let arr = [item.id];
      CancelTraceReserve(item.trace_id, JSON.stringify(arr))
        .then(res => {
          if (res.errno === 0) {
            this.$message({
              message: this.$t("successMessage.successMessage2"),
              type: "success"
            });
          }
          GetTraceProjectDetail(item.trace_id).then(res => {
            this.issuesData = res.data;
          });
        })
        .catch(() => {});
    }
  },
  mounted() {
    this.$nextTick(() => {
      /* 非追号 */
      if (this.dataList.amount !== null) {
        var amount = this.dataList.amount.split(".");
      }
      if (amount[1] === "0000") {
        this.amount = amount[0];
      } else {
        this.amount = Number(this.dataList.amount);
      }
      if (this.dataList.bet_number !== null) {
        var number = this.dataList.bet_number.split("|");
      }
      this.betNumber = number.join();
      let num = this.dataList.winning_number;
      if (this.titleName && num !== null && num !== undefined) {
        console.log(this.gameMenuType);
        switch (this.gameMenuType) {
          case "L115":
          case "KLSF":
          case "KL12":
          case "KENO":
            this.windNum = num.split(" ");
            break;
          case "LHC":
            this.windNum = num.split(" ");
            break;
          case "SSC":
          case "K3":
            this.windNum = num.split("");
            break;
          case "3D":
            this.windNum = num.split("");
            for (let key in this.windNum) {
              this.windNum0 = Number(this.windNum[0]);
              this.windNum1 = Number(this.windNum[1]);
              this.windNum2 = Number(this.windNum[2]);
              this.sumNum += Number(this.windNum[key]);
            }
            break;
          case "PK10":
            this.windNum = num.split(" ");
            for (let key in this.windNum) {
              if (this.windNum[key] < 10) {
                this.windNum[key] = this.windNum[key].split("")[1];
              }
            }
            break;
          default:
        }
      }
    });
  },
  filters: {
    PrizeFilter: function(val) {
      if (val === "--" || val === 0.0) {
        return "0";
      } else {
        return parseInt(val).toFixed(2);
      }
    },
    totalFilter: function(val) {
      if (val === "--") return false;
      var nono = val;
      var coco = 0;
      nono.split("");
      for (var i = 0; i < nono.length; i++) {
        coco += parseInt(nono[i]);
      }
      return coco;
    }
  },
  created() {
    /* this.$nextTick(() => {
      for (let key in this.issuesData) {
        for (let keyInner in this.issuesData[key]) {
          // this.issuesDataList = this.issuesData[key][keyInner]
          this.issuesWinNumber = this.issuesData[key][keyInner].wn_number.split(' ')
          for (let i in this.issuesWinNumber) {
            if (this.issuesWinNumber[i] < 10) {
              this.issuesWinNumber[i] = this.issuesWinNumber[i][1]
            }
          }
        }
      }
    }) */
  },
  watch: {
    show(val) {
      if (val) {
        this.isShow = val;
        if (isOpenAnimation) {
          setTimeout(() => {
            this.$refs.container.style.transition = "all 0.5s";
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        } else {
          setTimeout(() => {
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        }
      } else {
        if (isOpenAnimation) {
          this.$refs.container.style.top = "40%";
          this.$refs.container.style.opacity = 0;
          setTimeout(() => {
            this.isShow = val;
          }, 500);
        } else {
          this.isShow = val;
        }
      }
    }
  }
};
</script>

