<template>
  <transition name="fade-in-linear">
    <div
      v-if="isShow"
      class="wrapper"
      ref="wrapper"
      @click.self="handleWrapperClick"
    >
      <div class="container" ref="container">
        <a class="close" @click="closeAlertBox" href="javascript:;">
          <i class="el-icon-close"></i>
        </a>
        <div class="title change_bg_color">
          {{ $t("alertComponent.SystemTips") }}
        </div>
        <div class="content">
          <div class="table">
            <table v-if="mode === 'normal'">
              <tr>
                <th>{{ $t("alertComponent.Game") }}</th>
                <th>{{ $t("alertComponent.wanFa") }}</th>
                <th>{{ $t("alertComponent.BetContent") }}</th>
                <th>{{ $t("alertComponent.multiple") }}</th>
                <th>{{ $t("alertComponent.amountOfMoney") }}</th>
              </tr>
              <tr v-for="(item, index) in shoppingCarList" :key="index">
                <td>
                  <div>{{ item.lotteryName }}</div>
                </td>
                <td>
                  <div>{{ `${item.tabName}` }}</div>
                </td>
                <td>
                  <el-tooltip placement="top" :enterable="false">
                    <div slot="content">{{ item.labelStr }}</div>
                    <div class="choice-number-show">{{ item.labelStr }}</div>
                  </el-tooltip>
                </td>
                <td>
                  <div>{{ item.multiple }}({{ item.coefficientLabel }})</div>
                </td>
                <td>
                  <div>
                    {{ item.selectedMoney }}{{ systemSettingConfig.currency }}
                  </div>
                </td>
              </tr>
            </table>

            <table v-else-if="mode === 'handicap'">
              <tr>
                <th>{{ $t("alertComponent.Game") }}</th>
                <th>
                  <div>{{ $t("alertComponent.wanFa") }}</div>
                </th>
                <th>
                  <div>{{ $t("alertComponent.BetContent") }}</div>
                </th>
                <th>
                  <div>{{ $t("alertComponent.amountOfMoney") }}</div>
                </th>
              </tr>
              <tr v-for="(item, index) in shoppingCarList" :key="index">
                <td>
                  <div>{{ item.lotteryName }}</div>
                </td>
                <td>
                  <div>
                    {{ `${item.showOwnLabel ? item.ownLabel : item.tabName}` }}
                  </div>
                </td>
                <td>
                  <div>{{ item.label }}</div>
                </td>
                <td>
                  <div>
                    {{ item.betCount }}{{ $t("alertComponent.currency") }}
                  </div>
                </td>
              </tr>
            </table>
          </div>
          <p>
            {{ $t("alertComponent.TotalSum") }}:{{ allCountMoney
            }}{{ systemSettingConfig.currency }}
          </p>
          <div v-if="singlePickList.length" class="tips">
            {{ $t("alertComponent.WarmTip")
            }}{{ defaultSetting["challenge_rate"] * 100 }}%{{
              $t("alertComponent.TopPrize")
            }}[{{ defaultSetting["challenge_profit_limit"] }}]{{
              $t("alertComponent.currency")
            }}
          </div>
          <div class="btns clear-fix">
            <!--<a href="javascript:;">提交</a>-->
            <el-button
              class="change_btn"
              :loading="isSubmiting"
              @click="submitBetList"
              >{{ $t("alertComponent.confirm") }}</el-button
            >
            <el-button class="change_btn" @click="closeAlertBox">{{
              $t("alertComponent.cancel")
            }}</el-button>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import closeIcon from "../../../statics/img/close_icon_game_alert_zhtz.png";

import { showNumberList } from "../../../util";
import { alertMixin } from "../mixin";
import { mapGetters } from "vuex";
import {
  CLEAR_SHOPPING_CAR,
  CLOSE_CHASING_BOX,
  CLOSE_CREATE_NUMBER_BOX,
  DELETE_SHOPPING_ITEM,
  SET_IM_LOTTERY_NUMBER
} from "../../../store/nameSpace";
import { betInCommon, betImInCommon } from "../statics/js/util";
import { isOpenAnimation } from "../../../config/game-default-config";

export default {
  name: "qrzdxx-alert",
  mixins: [alertMixin],
  data() {
    return {
      closeIcon,
      name: "qrzdxx-alert",
      bonusGroup: "",
      multiple: 0,
      showNumberList,
      shoppingCarList: [],
      singlePickList: [],
      lotteryId: "",
      isClearShoppingCar: false,
      isChasingNumber: false,
      isCreateNumber: false,
      chasingInfo: null,
      mode: "normal",
      isSecondAlert: false,
      // 是否是追号弹窗中的追号item 如果是那么该值为要删除的shoppingCar中的删除item下标
      shoppingCarDeleteIndex: "",
      isSubmiting: false
    };
  },
  computed: {
    ...mapGetters(["defaultSetting", "systemSettingConfig", "isIm"]),
    allCountMoney() {
      if (!this.shoppingCarList) return 0;
      let count = 0;
      if (this.mode === "normal") {
        this.shoppingCarList.map(item => {
          count += item.selectedMoney;
        });
      } else if (this.mode === "handicap") {
        this.shoppingCarList.map(item => {
          count += Number(item.betCount);
        });
      }
      return count;
    }
  },
  watch: {
    shoppingCarList(val) {
      if (val && val.length) {
        // 系统最高奖金组
        const sysMaxGroup = this.defaultSetting["sys_max_prize_group"];
        // 单挑最高中奖金额
        const challengePrizeLimit = this.defaultSetting[
          "challenge_profit_limit"
        ];
        // 单挑判定比例
        const challengeRate = this.defaultSetting["challenge_rate"];
        this.singlePickList = [];
        val.map((item, index) => {
          // 投注金额
          const betMoney = item.selectedMoney;
          // 当前奖金组
          const bonusGroup = item.bonusGroup;
          // 当前奖金组的奖金标准
          const prize = item.prize;
          // 奖金
          const bonus = (+sysMaxGroup / +bonusGroup) * +prize;
          // 判定比例
          const singlePickRebate = betMoney / bonus;
          if (singlePickRebate < challengeRate) {
            this.singlePickList.push({
              bonus: challengePrizeLimit,
              count: index + 1
            });
          }
        });
      }
    },
    show(val) {
      if (val) {
        this.isShow = val;
        if (isOpenAnimation) {
          setTimeout(() => {
            this.$refs.wrapper.style.transition = "all 0.5s";
            this.$refs.container.style.transition = "all 0.5s";
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        } else {
          setTimeout(() => {
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        }
      } else {
        if (isOpenAnimation) {
          this.$refs.wrapper.style.background = "rgba(0, 0, 0, 0)";
          this.$refs.container.style.top = "40%";
          this.$refs.container.style.opacity = 0;
          setTimeout(() => {
            this.isShow = val;
          }, 500);
        } else {
          this.isShow = val;
        }
      }
    }
  },
  methods: {
    submitBetList() {
      this.isSubmiting = true;
      if (this.isIm) {
        return betImInCommon({
          lotteryId: this.lotteryId,
          isChasingNumber: this.isChasingNumber,
          shoppingCarList: this.shoppingCarList,
          mode: this.mode,
          chasingInfo: null
        })
          .then(res => {
            const { errno } = res;
            if (errno === 0) {
              this.$message({
                message: this.$t("successMessage.successMessage1"),
                type: "success"
              });
              this.$store.commit(SET_IM_LOTTERY_NUMBER, res.data);
            } else if (res.errno === 4063) {
              // 对玩法关闭的错误码进行单独的流程处理
              this.$message({
                message: res.message,
                type: "error"
              });
            } else {
              this.$message({
                message: res.message,
                type: "error"
              });
            }
            if (this.isClearShoppingCar) this.$store.commit(CLEAR_SHOPPING_CAR);
            this.$store.dispatch("clearSelectedBalls");
            if (this.isChasingNumber) {
              this.$store.commit(CLOSE_CHASING_BOX, false);
            }
            if (this.isCreateNumber) {
              this.$store.commit(CLOSE_CREATE_NUMBER_BOX, false);
            }
            if (this.shoppingCarDeleteIndex !== "") {
              this.$store.commit(
                DELETE_SHOPPING_ITEM,
                this.shoppingCarDeleteIndex
              );
            }
            this.isSubmiting = false;
            this.show = false;
          })
          .catch(err => {
            console.log(err);
            this.$message({
              message: err.message,
              type: "error"
            });
            this.$store.dispatch("getUserBalance");
            if (this.isClearShoppingCar) this.$store.commit(CLEAR_SHOPPING_CAR);
            // this.$store.commit(SET_SSC_SELECTED_COUNTS, 0)
            if (this.isChasingNumber) {
              this.$store.commit(CLOSE_CHASING_BOX, false);
            }
            if (this.isCreateNumber) {
              this.$store.commit(CLOSE_CREATE_NUMBER_BOX, false);
            }
            this.show = false;
            this.isSubmiting = false;
          });
      } else {
        betInCommon({
          lotteryId: this.lotteryId,
          isChasingNumber: this.isChasingNumber,
          shoppingCarList: this.shoppingCarList,
          mode: this.mode,
          chasingInfo: this.chasingInfo
        })
          .then(res => {
            const { errno } = res;
            if (errno === 0) {
              this.$message({
                message: this.$t("successMessage.successMessage1"),
                type: "success"
              });
            } else if (res.errno === 4063) {
              // 对玩法关闭的错误码进行单独的流程处理
              this.$message({
                message: res.message,
                type: "error"
              });
            } else {
              this.$message({
                message: res.message,
                type: "error"
              });
            }
            if (this.isClearShoppingCar) this.$store.commit(CLEAR_SHOPPING_CAR);
            this.$store.dispatch("clearSelectedBalls");
            if (this.isChasingNumber) {
              this.$store.commit(CLOSE_CHASING_BOX, false);
            }
            if (this.isCreateNumber) {
              this.$store.commit(CLOSE_CREATE_NUMBER_BOX, false);
            }
            if (this.shoppingCarDeleteIndex !== "") {
              this.$store.commit(
                DELETE_SHOPPING_ITEM,
                this.shoppingCarDeleteIndex
              );
            }
            this.isSubmiting = false;
            this.show = false;
          })
          .catch(err => {
            console.log(err);
            this.$message({
              message: err.message,
              type: "error"
            });
            this.$store.dispatch("getUserBalance");
            if (this.isClearShoppingCar) this.$store.commit(CLEAR_SHOPPING_CAR);
            // this.$store.commit(SET_SSC_SELECTED_COUNTS, 0)
            if (this.isChasingNumber) {
              this.$store.commit(CLOSE_CHASING_BOX, false);
            }
            if (this.isCreateNumber) {
              this.$store.commit(CLOSE_CREATE_NUMBER_BOX, false);
            }
            this.show = false;
            this.isSubmiting = false;
          });
      }
    }
  },
  created() {}
};
</script>

<style scoped>
@import "../statics/css/transition.css";
.choice-number-show {
  cursor: pointer;
  color: red;
  text-decoration: underline;
}
.container .content {
  padding: 32px 16px 20px 14px;
}
/*table begin*/
.container .content .table {
  padding: 10px;
  height: 227px;
  overflow: auto;
  border: 1px solid #eeeeee;
  margin-bottom: 15px;
}
.container .content .table table {
  width: 100%;
}
.container .content .table table tr:nth-of-type(1) {
  background-color: #eaeaea;
  height: 36px;
  line-height: 36px;
}
.container .content .table table tr th:nth-of-type(1),
.container .content .table table tr td:nth-of-type(1) div {
  width: 80px;
  text-align: left;
}
.container .content .table table tr:nth-of-type(odd) {
  background-color: #f5f5f5;
}
.container .content .table table tr:nth-of-type(even) {
  background-color: #fff;
}
.container .content .table table tr th:nth-of-type(2),
.container .content .table table tr th:nth-of-type(2) div,
.container .content .table table tr td:nth-of-type(2) div {
  width: 115px;
}

.container .content .table table tr td {
  text-align: center;
}
.container .content .table table tr th:nth-of-type(3),
.container .content .table table tr th:nth-of-type(3) div,
.container .content .table table tr td:nth-of-type(3) div {
  width: 105px;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  white-space: nowrap;
}
.container .content .table table tr th:nth-of-type(4),
.container .content .table table tr th:nth-of-type(4) div,
.container .content .table table tr td:nth-of-type(4) div {
  width: 115px;
}
.container .content .table table tr th:nth-of-type(5),
.container .content .table table tr td:nth-of-type(5) div {
  width: 115px;
}
.container .content .table table tr th,
.container .content .table table tr td div {
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  white-space: nowrap;
}
.container .content .table table tr {
  height: 34px;
  line-height: 34px;
}
.container .content .table table tr th:nth-of-type(1),
.container .content .table table tr td:nth-of-type(1) {
  padding-left: 15px;
}
/*table begin*/
.container .content > p {
  padding-top: 10px;
  border-top: 1px solid #f3f3f3;
  font-size: 13px;
  color: #777777;
  text-align: center;
  margin-bottom: 14px;
}
.container .content > .tips {
  text-align: center;
  color: #f17e2e;
  word-break: break-word;
  font-size: 12px;
}
.container .content > .btns {
  width: 130px;
  margin: 20px auto 0 auto;
}
.container .content > .btns a,
.container .content > .btns button {
  float: left;
  padding: 0;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: #fff;
  font-weight: bold;
  width: 60px;
  border-radius: 4px;
}
</style>

<style></style>
