<template>
  <transition name="fade-in-linear">
    <div class="wrapper" v-if="isShow" @click.self="handleWrapperClick">
      <div class="container width" ref="container">
        <a class="close" @click="closeAlertBox" href="javascript:;">
          <i class="el-icon-close"></i>
        </a>
        <div class="title change_bg_color">
          {{ $t("alertComponent.allGameLists") }}
        </div>
        <div class="content">
          <ul>
            <li v-for="(menuItem, index) in szcpMenuItemList" :key="index">
              <h4>{{ menuItem.name }}</h4>
              <div class="games clear-fix">
                <a
                  v-for="(innerLink, innerIndex) in menuItem['lotteries']"
                  @click="choiceLikeGame(innerLink)"
                  :key="innerIndex"
                  :class="{
                    red: innerLink.hot === 1,
                    blue: innerLink.new === 1,
                    selected: innerLink.isLike
                  }"
                  href="javascript:;"
                  >{{ innerLink.name }}</a
                >
              </div>
            </li>
          </ul>
          <el-button
            class="btn change_btn_bg"
            @click="saveLikeGameList"
            :loading="btnLoading"
            >{{ $t("alertComponent.Preservation") }}</el-button
          >
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import closeIcon from "../../../statics/img/close_icon_game_alert_zhtz.png";
import { alertMixin } from "../mixin";
import { mapGetters } from "vuex";
import { setFavoritesLottery } from "@/api/game/game";
import { isOpenAnimation } from "../../../config/game-default-config";
export default {
  name: "syyx-alert",
  mixins: [alertMixin],
  data() {
    return {
      closeIcon,
      name: "syyx-alert",
      bonusGroup: "",
      multiple: 0,
      btnLoading: false,
      szcpMenuItemList: [],
      isLikeNumber: 0
    };
  },
  methods: {
    choiceLikeGame(game) {
      if (this.isLikeNumber === 8 && game.isLike === false) {
        this.$message({
          message: "收藏彩种已达到上限（8）",
          type: "error"
        });
      } else {
        if (game.isLike) {
          this.isLikeNumber = this.isLikeNumber - 1;
        } else {
          this.isLikeNumber = this.isLikeNumber + 1;
        }
        let szcpMenuItemList = this.szcpMenuItemList;
        this.szcpMenuItemList = [];
        let ln = szcpMenuItemList.length;
        for (let i = 0; i < ln; i++) {
          let subLn = szcpMenuItemList[i]["lotteries"].length;
          for (let j = 0; j < subLn; j++) {
            if (
              szcpMenuItemList[i]["lotteries"][j]["lottery_id"] ===
              game["lottery_id"]
            ) {
              szcpMenuItemList[i]["lotteries"][j].isLike = !szcpMenuItemList[i][
                "lotteries"
              ][j].isLike;
            }
          }
        }
        this.szcpMenuItemList = szcpMenuItemList;
      }
    },
    saveLikeGameList() {
      this.btnLoading = true;
      const likeList = [];
      for (let i = 0; i < this.szcpMenuItemList.length; i++) {
        for (let j = 0; j < this.szcpMenuItemList[i]["lotteries"].length; j++) {
          const item = this.szcpMenuItemList[i]["lotteries"][j];
          if (item.isLike)
            likeList.push(
              this.szcpMenuItemList[i]["lotteries"][j]["lottery_id"]
            );
        }
      }
      setFavoritesLottery(likeList.join(","))
        .then(res => {
          if (res.errno === 0) {
            return this.$store.dispatch("getOftenPlayTypeList");
          }
        })
        .then(() => {
          this.btnLoading = false;
          this.show = false;
        });
    },
    setLotteryMenuList() {
      let ln = this.lotteryMenuList.length;
      this.isLikeNumber = this.oftenPlayTypes.length;
      for (let i = 0; i < ln; i++) {
        let subLn = this.lotteryMenuList[i]["lotteries"].length;
        for (let j = 0; j < subLn; j++) {
          this.lotteryMenuList[i]["lotteries"][j].isLike = false;
          for (let f = 0; f < this.oftenPlayTypes.length; f++) {
            if (
              this.lotteryMenuList[i]["lotteries"][j]["lottery_id"] ===
              this.oftenPlayTypes[f]["lottery_id"]
            ) {
              this.lotteryMenuList[i]["lotteries"][j].isLike = true;
              this.lotteryMenuList[i]["lotteries"][j].likeNumber = f + 1;
            }
          }
        }
      }
      this.szcpMenuItemList = this.lotteryMenuList;
    }
  },
  computed: {
    ...mapGetters(["lotteryMenuList", "oftenPlayTypes"])
  },
  watch: {
    show(val) {
      if (val) {
        this.isShow = val;
        if (isOpenAnimation) {
          setTimeout(() => {
            this.$refs.container.style.transition = "all 0.5s";
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        } else {
          setTimeout(() => {
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        }
      } else {
        if (isOpenAnimation) {
          this.$refs.container.style.top = "40%";
          this.$refs.container.style.opacity = 0;
          setTimeout(() => {
            this.isShow = val;
          }, 500);
        } else {
          this.isShow = val;
        }
      }
    }
  },
  mounted() {
    this.setLotteryMenuList();
    console.log(this.lotteryMenuList);
  }
};
</script>

<style scoped>
@import "../statics/css/transition.css";
.btn:hover {
  background: rgb(111, 118, 210);
}
.btn {
  display: block;
  width: 110px;
  margin: 0 auto;
  text-align: center;
  border-radius: 5px;
  border: none;
  background: #4babbf;
  color: #fff;
  transition: all 0.3s linear;
}
/*content begin*/
.content > ul:nth-of-type(1) > li {
  margin-bottom: 10px;
}
.content > ul:nth-of-type(1) > li > h4 {
  font-size: 18px;
  margin-bottom: 7px;
  font-weight: normal;
  font-family: "黑体";
}
.content > ul:nth-of-type(1) > li > .games a {
  float: left;
  transition: all 0.3s linear;
  width: 112px;
  height: 38px;
  position: relative;
  line-height: 38px;
  font-size: 12px;
  text-align: center;
  margin-bottom: 8px;
  margin-right: 8px;
  color: #545454;
  border: 1px solid #dcdcdc;
}
.content > ul:nth-of-type(1) > li > .games a.blue::after {
  content: "";
  display: block;
  position: absolute;
  right: -2px;
  top: -3px;
  width: 8px;
  height: 8px;
  background-color: #2ec1e0;
  border-radius: 50%;
}
.content > ul:nth-of-type(1) > li > .games a.selected {
  border: 1px solid #f63e3e;
}
.content > ul:nth-of-type(1) > li > .games a.red::after {
  content: "";
  display: block;
  position: absolute;
  right: -2px;
  top: -3px;
  width: 8px;
  height: 8px;
  background-color: #f63e3e;
  border-radius: 50%;
}
/*content end*/
.container .content {
  padding: 17px 14px 17px 16px;
}
.container .content ul {
  height: 600px;
  overflow-y: auto;
}
</style>

<style></style>
