      <!--<el-select style="width: 100px;"-->
      <!--v-model="seriesType"-->
      <!--placeholder="请选择彩系"-->
      <!--@change="seriesTypeChange">-->
      <!--<el-option v-for="item in seriesList"-->
      <!--:key="item.value"-->
      <!--:label="item.label"-->
      <!--:value="item"></el-option>-->
      <!--</el-select>-->
      <!--</div>-->
      <!--<div class="form-group lottery-name ds-inline">-->
      <!--<label class="group-label">彩种</label>-->
      <!--<el-select style="width: 150px;"-->
      <!--v-model="gameType"-->
      <!--placeholder="请选择彩种"-->
      <!--@change="gameTypeChange">-->
      <!--<el-option v-for="item in gameList"-->
      <!--:key="item.value"-->
      <!--:label="item.label"-->
      <!--:value="item"></el-option>-->
      <!--</el-select>-->
      <!--</div>-->
      <span class="check-box" v-if="tableHadeMsg.type !== 7">
        <el-checkbox v-model="lineChecked">{{
          $t("lotteryRecord.displayLine")
        }}</el-checkbox>
      </span>
      <span class="check-box">
        <el-checkbox v-model="omissionChecked">{{
          $t("lotteryRecord.withoutOmission")
        }}</el-checkbox>
      </span>
      <div class="btn-box ds-inline">
        <a
          href="javascript:;"
          :class="{ change_btn_bg: count === 30 }"
          @click="setCount(30)"
          >{{ $t("lotteryRecord.Last30issues") }}</a
        >
        <a
          href="javascript:;"
          :class="{ change_btn_bg: count === 50 }"
          @click="setCount(50)"
          >{{ $t("lotteryRecord.Last50issues") }}</a
        >
        <a
          href="javascript:;"
          :class="{ change_btn_bg: count === 100 }"
          @click="setCount(100)"
          >{{ $t("lotteryRecord.Last100issues") }}</a
        >
      </div>
    </div>
    <div
      class="trend-chart-tb"
      v-if="isInitData === true"
      :style="{ height: tableHeight }"
    >
      <table>
        <thead>
          <tr>
            <th
              v-for="(item, index) in tableHadeMsg.columnData"
              :key="index"
              :rowspan="item.rows"
              :colspan="item.cols"
            >
              {{ item.label }}
            </th>
          </tr>
          <tr>
            <th v-for="(item, index) in bottoms" :key="index">{{ item }}</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-if="gameSeriesId === 7"
            v-for="(item, index) in dataList"
            :key="index"
          >
            <td style="padding: 0 15px">{{ item.issue }}</td>
            <!-- 数组第一层为奖期号 -->
            <td v-for="(num, index2) in item.data" :key="index2 + '--'">
              <div v-if="typeof num === 'string'" class="ball_base">
                {{ num }}
              </div>
              <div v-else>{{ omissionChecked === true ? "" : num }}</div>
            </td>
          </tr>
          <tr
            v-if="gameSeriesId !== 7"
            v-for="(item, index) in dataList"
            :key="index"
          >
            <td style="padding: 0 15px">{{ item.issue }}</td>
            <!-- 数组第一层为奖期号 -->
            <td style="color: red; padding: 0 15px">
              {{ item.number }}
            </td>
            <td v-for="(num, index2) in item.data" :key="index2 + '--'">
              <div
                v-if="num.isShow"
                class="ans"
                :style="{
                  backgroundColor:
                    canvasLineColors[num.index % canvasLineColors.length]
                }"
              >
                {{ num.value }}
                <canvas
                  class="red canvas-line"
                  v-if="index !== dataList.length - 1"
                  :class="
                    num.value > dataList[index + 1].wnNumber[num.index]
                      ? 'left'
                      : num.value < dataList[index + 1].wnNumber[num.index]
                      ? 'right'
                      : 'center'
                  "
                  :data-color="
                    canvasLineColors[num.index % canvasLineColors.length]
                  "
                  height="22"
                  :width="
                    `${Math.abs(
                      dataList[index + 1].wnNumber[num.index] - num.value
                    ) *
                      tdWidth +
                      2}`
                  "
                >
                </canvas>
              </div>
              <div v-else>{{ omissionChecked === true ? "" : num }}</div>
            </td>
          </tr>
          <tr v-if="dataList.length === 0">
            <td :colspan="showTypeConfig[nowGameType].showCountsEveryRow"></td>
          </tr>
          <tr v-if="tableHadeMsg.type !== 7">
            <td style="color:#404447">{{ $t("lotteryRecord.totalNumber") }}</td>
            <td :colspan="tableHadeMsg.columnData[1].cols"></td>
            <td
              v-if="
                index <
                  showTypeConfig[nowGameType].showCountsEveryRow -
                    tableHadeMsg.columnData[1].cols -
                    dataList[0].wnNumber.length
              "
              v-for="(item, index) in statistics[0]"
              :key="index"
            >
              {{ item }}
            </td>
          </tr>
          <tr v-if="tableHadeMsg.type !== 7">
            <td style="color:#404447">
              {{ $t("lotteryRecord.averageMissingValue") }}
            </td>
            <td :colspan="tableHadeMsg.columnData[1].cols"></td>
            <td
