              bottoms: this.Initialization(0, 9),
              rows: 1,
              cols: 10,
              valueMap: "5"
            },
            {
              label: this.$t("lotteryRecord.sixthPlace"),
              value: "6",
              bottoms: this.Initialization(0, 9),
              rows: 1,
              cols: 10,
              valueMap: "6"
            },
            {
              label: this.$t("lotteryRecord.seventhPlace"),
              value: "7",
              bottoms: this.Initialization(0, 9),
              rows: 1,
              cols: 10,
              valueMap: "7"
            }
          ]
        }
      },
      nowGameType: "",
      isInitData: false,
      tableHadeMsg: {},
      bottoms: [],
      statistics: [],
      resetKey: 0,
      tdWidth: 0,
      isLongHu: false
    };
  },
  props: ["containerHeight"],
  computed: {
    ballColor: function() {
      return {
        ball01: this.color1 === true,
        ball02: this.color2 === true
      };
    },
    ...mapGetters(["lotteryId", "isLogin"])
  },
  methods: {
    setWidth(n) {
      if (n === 3) {
        this.tdWidth = 33;
      } else if (n === 4) {
        this.tdWidth = 48;
      } else {
        this.tdWidth = 21;
      }
    },
    // 获取彩种数据
    getGamelist() {
      this.$loadingShow();
      getAllGames().then(res => {
        if (res.errno === 0) {
          let allGames = [];
          for (let key in res.data) {
            allGames.push(res.data[key]);
          }
          this.$set(this, "allGames", allGames);
          this.initGameList();
        }
      });
    },
    initGameList() {
      let allGames = this.allGames;
      let len = allGames.length;
      for (let i = 0; i < len; i++) {
        let ai = allGames[i];
        if (ai.id === Number(this.lotteryId)) {
          this.seriesType.value = ai.series_id;
          this.gameSeriesId = ai.series_id;
          this.setWidth(ai.series_id);
        }
      }
      this.GetLotteryTrend();
      this.initData();
    },
    // 根据彩系初始化列表头数据
    initData() {
      for (let i in this.showTypeConfig) {
        if (this.showTypeConfig[i].type === this.gameSeriesId) {
          this.tableHadeMsg = this.showTypeConfig[i];
          this.nowGameType = this.showTypeConfig[i].name;
        }
      }
      let bottoms = [];
      this.tableHadeMsg.columnData.forEach((item, index) => {
        if (item.bottoms !== false) {
          bottoms = bottoms.concat(item.bottoms);
        }
      });
      this.bottoms = bottoms;
    },
    Initialization(num1, num2) {
      let numArr = [];
      for (let i = num1; i <= num2; i++) {
        numArr.push(i);
      }
      return numArr;
    },
    //
    getNumber(num) {
      if (this.showTypeConfig[this.nowGameType].isSpace) {
        if (this.gameSeriesId === 6) {
          let list = [];
          let numArr = [];
          numArr = num.split(" ");
          numArr.forEach((item, index) => {
            if (index === numArr.length - 1) {
              let itemArr = item.split("+");
              list = list.concat(itemArr);
            } else {
              list.push(item);
            }
          });
          return list;
        } else {
          return num.split(" ");
        }
      } else {
        return num.split("");
      }
    },
    // 设置请求条数
    setCount(e) {
      this.$loadingShow();
      this.count = e;
      this.GetLotteryTrend();
    },
    // 获取数据
    GetLotteryTrend() {
      this.$loadingShow();
      let data = {
        lotteryId: this.lotteryId,
        count: this.count
      };
      GetLotteryTrend(data).then(res => {
        if (res.errno === 0) {
          let dataList = [];
          for (let key in res.data.data) {
            dataList.push(res.data.data[key]);
          }
          if (this.gameSeriesId === 7) {
            this.dataList = dataList.map(item => {
              let columnData = [];
              for (let index in item) {
                if (index > 0) {
                  if (item[index] === 0) {
                    columnData.push(`${index}`);
                  } else {
                    columnData.push(item[index]);
                  }
                }
              }
              return {
                issue: item[0],
                data: columnData
              };
            });
            this.$nextTick(() => {
              setTimeout(() => {
                this.$loadingHide();
              }, 0);
            });
          } else {
            this.dataList = dataList.map(item => {
              const splitDataArr = this.getNumber(item[1]);
              let columnData = [];
              for (let index in item) {
                if (index > 1 && index < splitDataArr.length + 2) {
                  for (let subIndex in item[index]) {
                    if (item[index][subIndex][0] === 0) {
                      columnData.push({
                        isShow: true,
                        value: item[index][subIndex][1],
                        index: index - 2
                      });
                    } else {
                      columnData.push(item[index][subIndex][0]);
                    }
                  }
                }
              }
              return {
                issue: item[0],
                wnNumber: splitDataArr,
                number: splitDataArr.join(" "),
                data: columnData
              };
            });
            this.$set(this, "statistics", res.data.statistics);
          }
          this.isInitData = true;
          this.$nextTick(() => {
            if (this.lineChecked) {
              this.initLine();
            }
            setTimeout(() => {
              this.$loadingHide();
            }, 0);
          });
        } else {
          this.$set(this, "dataList", "");
          this.$set(this, "statistics", "");
          this.$loadingHide();
        }
      });
    },
    initLine() {
      const canvasLines = document.querySelectorAll(".canvas-line");
      let len = canvasLines.length;
      for (let i = 0; i < len; i++) {
        const canvasPen = canvasLines[i].getContext("2d");
        canvasPen.clearRect(0, 0, canvasLines[i].width, canvasLines[i].height);
        const xWidth = canvasLines[i].width;
        const isLeft = canvasLines[i].className.indexOf("left") !== -1;
        const isRight = canvasLines[i].className.indexOf("right") !== -1;
        canvasPen.save();
        canvasPen.strokeStyle = canvasLines[i].dataset["color"];
        canvasPen.lineWidth = 1.5;
        canvasPen.globalAlpha = 1;
        canvasPen.beginPath();
        if (isLeft) {
          canvasPen.moveTo(xWidth, 0);
          canvasPen.lineTo(0, 22);
        } else if (isRight) {
          canvasPen.moveTo(0, 0);
          canvasPen.lineTo(xWidth, 22);
        } else {
          canvasPen.moveTo(xWidth / 2, 0);
          canvasPen.lineTo(xWidth / 2, 22);
        }
        /* canvasPen.moveTo(xWidth, 0)
        canvasPen.lineTo(0, 32) */
        canvasPen.closePath();
        canvasPen.stroke();
        canvasPen.restore();
      }
    },
    isInitLine(val) {
      const canvasLines = document.querySelectorAll(".canvas-line");
      const len = canvasLines.length;
      if (val === false) {
        for (let i = 0; i < len; i++) {
          canvasLines[i].style.display = "none";
        }
      } else {
        for (let i = 0; i < len; i++) {
          canvasLines[i].style.display = "block";
        }
        this.initLine();
      }
    }
  },
  created() {
    this.getGamelist();
  },
  mounted() {
    setTimeout(() => {
      if (this.containerHeight) {
        this.tableHeight = parseFloat(this.containerHeight) - 134 + "px";
        console.log(this.tableHeight);
      }
    }, 100);
  },
  watch: {
    lineChecked(val) {
      this.isInitLine(val);
    }
  }
};
</script>

