import Vue from "vue";
import VueI18n from "vue-i18n";

import elementEnLocale from "element-ui/lib/locale/lang/en";
import elementCNLocale from "element-ui/lib/locale/lang/zh-CN";
import elementTWLocale from "element-ui/lib/locale/lang/zh-TW";
import elementEsLocale from "element-ui/lib/locale/lang/es";

import enLocale from "./en";
import zhCnLocale from "./zh-cn";
import zhTwLocale from "./zh-tw";
import esLocale from "./es";

import { getLang } from "../util/lang";
import { sBetSettings } from "../util/require";

Vue.use(VueI18n);

export const messages = {
  en: {
    ...enLocale,
    ...elementEnLocale
  },
  "zh-CN": {
    ...zhCnLocale,
    ...elementCNLocale
  },
  "zh-TW": {
    ...zhTwLocale,
    ...elementTWLocale
  },
  es: {
    ...esLocale,
    ...elementEsLocale
  }
};

const i18n = new VueI18n({
  locale: getLang() || sBetSettings.language,
  messages,
  silentTranslationWarn: true
});
export default i18n;
