import request from "@/util/request";
import terminalId from "../terminalId";

/**
 * 获取账户信息
 */
export function getAccount() {
  return request({
    url: "",
    method: "get",
    params: {
      action: "getAccount",
      terminal_id: terminalId
    }
  });
}
/**
 * 代理信息留言
 * @param data
 */
export function AgentApplication(data) {
  return request({
    url: "",
    method: "post",
    data: {
      action: "AgentApplication",
      terminalId: terminalId,
      username: data.username,
      country: data.country,
      title: data.title,
      mailbox: data.mailbox,
      phone: data.phone,
      project: data.project,
      communication: data.communication,
      communication_account: data.communication_account,
      captcha: data.captcha,
      news: data.news,
      remarks: data.remarks
    }
  });
}
/**
 * 获取总代注册的key值
 */
export function GetPlatformSpreadRegisterUrl() {
  return request({
    url: "",
    method: "get",
    params: {
      action: "GetPlatformSpreadRegisterUrl",
      terminal_id: terminalId
    }
  });
}
