export const __TOKEN_KEY__ = "__token__";
export const __SHOW_DRIVER__ = "__show_driver_token__";
export const __SHOW_HANDICAP_CHOICE_WAY__ = "__show_handicap_choice_way__";

export function getToken() {
  return window.localStorage.getItem(__TOKEN_KEY__);
}

export function saveToken(token) {
  return window.localStorage.setItem(__TOKEN_KEY__, token);
}
export function delToken() {
  return window.localStorage.removeItem(__TOKEN_KEY__);
}
export function logOut() {
  delToken();
}

