export function getIsShowDriver() {
  return window.localStorage.getItem(__SHOW_DRIVER__);
}

export function saveIsShowDriver(isShowDriver) {
  return window.localStorage.setItem(
    __SHOW_DRIVER__,
    JSON.stringify(isShowDriver)
  );
}

export function saveNowHandicapChoiceWay(nowWay) {
  return window.localStorage.setItem(
    __SHOW_HANDICAP_CHOICE_WAY__,
    JSON.stringify(nowWay)
  );
}

export function getNowHandicapChoiceWay() {
  return +window.localStorage.getItem(__SHOW_HANDICAP_CHOICE_WAY__);
}
export function getIsPC() {
  return window.sessionStorage.getItem("isPc");
}
export function seveIsPc(isPc) {
  return window.sessionStorage.setItem("isPc", isPc);
}
