<template>
  <transition name="fade-in-linear">
    <div v-if="isShow" class="wrapper" @click.self="handleWrapperClick">
      <div class="container" ref="container">
        <a class="close" @click="closeAlertBox" href="javascript:;">
          <i class="el-icon-close"></i>
        </a>
        <div class="title change_bg_color">
          {{ $t("alertComponent.followUpImmediately") }}
        </div>
        <div class="content">
          <div class="tabs clear-fix">
            <a
              v-for="(tab, index) in tabList"
              @click="showWhichTab = tab.type"
              :key="index"
              v-if="tab.isShow"
              :class="{ 'now-hov': showWhichTab === tab.type }"
              href="javascript:;"
              >{{ tab.label }}</a
            >
          </div>
          <div class="chasing-number-counts">
            <span>{{ $t("alertComponent.trackingNumber") }}</span>
            <el-select
              class="chasing-number-counts-select"
              size="mini"
              v-model="periodsNumber"
              placeholder=""
            >
              <el-option
                v-for="(item, index) in issueTraceGear"
                :key="index"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
          <div
            v-show="showWhichTab === 'profitMargin'"
            v-if="isShowProfitMargin"
            class="chasing-number-setting lrxzh"
          >
            <div>
              <span
                >{{ $t("alertComponent.TrackingPlan") }}：{{
                  $t("alertComponent.OnsetMultiple")
                }}</span
              >
              <input
                class="multiple-start"
                v-model="profitRateInfo.startMultiple"
                @input="checkInputToNumber('profitRateInfo', 'startMultiple')"
                type="text"
              />
            </div>
            <div>
              <span>{{ $t("alertComponent.MinimumRate") }}</span>
              <input
                class="min-date-return"
                v-model="profitRateInfo.lowestIncomeRate"
                @input="
                  checkInputToNumber('profitRateInfo', 'lowestIncomeRate')
                "
                type="text"
              />
              <span>%</span>
            </div>
            <div>
              <span>{{ $t("alertComponent.trackingNumber") }}</span>
              <input
                class="chasing-number"
                v-model="profitRateInfo.chasingPeriodsCounts"
                @input="
                  checkInputToNumber('profitRateInfo', 'chasingPeriodsCounts')
                "
                type="text"
              />
            </div>
            <div>
              <a
                class="change_btn"
                href="javascript:;"
                @click="profitRateCheckList"
              >
                {{ $t("alertComponent.followUpList") }}
                <img :src="nextIcon" alt="nextIcon" />
              </a>
            </div>
          </div>
          <div
            v-show="showWhichTab === 'sameMultiple'"
            class="chasing-number-setting tbzh"
          >
            <div>
              <span
                >{{ $t("alertComponent.TrackingPlan") }}：{{
                  $t("alertComponent.OnsetMultiple")
                }}</span
              >
              <input
                class="multiple-start"
                v-model="sameMultipleInfo.startMultiple"
                @input="checkInputToNumber('sameMultipleInfo', 'startMultiple')"
                type="text"
              />
            </div>
            <div>
              <span>{{ $t("alertComponent.trackingNumber") }}</span>
              <input
                class="chasing-number"
                v-model="sameMultipleInfo.chasingPeriodsCounts"
                @input="
                  checkInputToNumber('sameMultipleInfo', 'chasingPeriodsCounts')
                "
                type="text"
              />
            </div>
            <div>
              <a
                class="change_btn"
                href="javascript:;"
                @click="sameMultipleCheckList"
              >
                {{ $t("alertComponent.followUpList") }}
                <img :src="nextIcon" alt="nextIcon" />
              </a>
            </div>
          </div>
          <div
            v-show="showWhichTab === 'doubleMultiple'"
            class="chasing-number-setting fbzh"
          >
            <div>
              <span
                >{{ $t("alertComponent.TrackingPlan") }}：{{
                  $t("alertComponent.Partition")
                }}</span
              >
              <input
                class="interval-count"
                v-model="doubleMultipleInfo.interval"
                @input="checkInputToNumber('doubleMultipleInfo', 'interval')"
                type="text"
              />{{ $t("alertComponent.stage") }}
              ×
              <input
                class="multiple"
                v-model="doubleMultipleInfo.incrementMultiple"
                @input="
                  checkInputToNumber('doubleMultipleInfo', 'incrementMultiple')
                "
                type="text"
              />
              <span>{{ $t("alertComponent.times") }}</span>
            </div>
            <div>
              <span>{{ $t("alertComponent.trackingNumber") }}</span>
              <input
                class="chasing-number-count"
                v-model="doubleMultipleInfo.chasingPeriodsCounts"
                @input="
                  checkInputToNumber(
                    'doubleMultipleInfo',
                    'chasingPeriodsCounts'
                  )
                "
                type="text"
              />
            </div>
            <div>
              <a
                class="change_btn"
                href="javascript:;"
                @click="doubleMultipleCheckList"
              >
                {{ $t("alertComponent.followUpList") }}
                <img :src="nextIcon" alt="nextIcon" />
              </a>
            </div>
          </div>
          <p class="chasing-number-description">
            {{ $t("alertComponent.TotalNumber") }}: <i> {{ allPeriods }} </i>
            {{ $t("alertComponent.stage") }}
            {{ $t("alertComponent.TotalSum") }}:
            <i> {{ allCountMoney | formatPrice }} </i>
            {{ systemSettingConfig.currency }}
          </p>
          <div class="table">
            <table>
              <tr v-for="(issues, index) in issuesList" :key="index">
                <td><input v-model="issues.isSelected" type="checkbox" /></td>

                <td>{{ index + 1 }}</td>
                <td>{{ issues.issue }}</td>
                <td>
                  <input
                    :disabled="!issues.isSelected"
                    @input="checkMultipleInput(issues)"
                    v-model="issues.multiple"
                    type="text"
                  />{{ $t("alertComponent.times") }}
                </td>
                <td>
                  <span>{{
                    NP.times(
                      betInfo.betNoteCounts,
                      betInfo.coefficient,
                      issues.multiple,
                      betInfo.price
                    ) | formatPrice
                  }}</span>
                </td>
                <td>{{ issues.endTime }}</td>
              </tr>
            </table>
          </div>
          <p v-if="isShowPickTip" class="tips">
            {{ $t("alertComponent.WarmTip")
            }}{{ defaultSetting["challenge_rate"] * 100 }}％{{
              $t("alertComponent.TopPrize")
            }}{{ defaultSetting["challenge_profit_limit"]
            }}{{ $t("alertComponent.currency") }}
          </p>
          <div style="padding: 10px 0">
            <input type="checkbox" v-model="stopChasingWn" id="is-win-stop" />
            <label for="is-win-stop">{{
              $t("alertComponent.StopChasing")
            }}</label>
          </div>
          <div class="action">
            <a
              class="change_btn"
              href="javascript:;"
              @click="submitChasingNumber"
            >
              {{ $t("alertComponent.immediateBet") }}
              <img :src="rightIcon" alt="rightIcon" />
            </a>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import nextIcon from "../../../statics/img/next_icon.png";
import rightIcon from "../../../statics/img/right_icon_smp.png";
import { alertMixin } from "../mixin";
import { mapGetters } from "vuex";
import {
  chasingNumberDefault,
  isOpenAnimation,
  isShowProfitMargin,
  isShowPickTip
} from "../../../config/game-default-config";
import { shallowCopy } from "../../../util";
import { CLOSE_CHASING_BOX } from "../../../store/nameSpace";
import NP from "number-precision";
import { GetIssueCanTraceList } from "../../../api/game/game";

export default {
  name: "ljzh-alert",
  mixins: [alertMixin],
  data() {
    return {
      NP,
      rightIcon,
      isShowPickTip,
      nextIcon,
      name: "ljzh-alert",
      showWhichTab: "sameMultiple",
      bonusGroup: "",
      multiple: 0,
      defaultAmount01: 5,
      tabList: [
        {
          label: this.$t("alertComponent.ProfitMarginPursuit"),
          isShow: isShowProfitMargin,
          type: "profitMargin"
        },
        {
          label: this.$t("alertComponent.TongBeiHao"),
          isShow: true,
          type: "sameMultiple"
        },
        {
          label: this.$t("alertComponent.DoubleTrackingNumber"),
          isShow: true,
          type: "doubleMultiple"
        }
      ],
      issuesList: [],
      stopChasingWn: chasingNumberDefault.stopChasingWn,
      periodsNumber: chasingNumberDefault.periodsNumber,
      profitRateInfo: {
        startMultiple: chasingNumberDefault.profitRateInfo.startMultiple,
        lowestIncomeRate: chasingNumberDefault.profitRateInfo.lowestIncomeRate,
        chasingPeriodsCounts:
          chasingNumberDefault.profitRateInfo.chasingPeriodsCounts
      },
      sameMultipleInfo: {
        startMultiple: chasingNumberDefault.sameMultipleInfo.startMultiple,
        chasingPeriodsCounts:
          chasingNumberDefault.sameMultipleInfo.chasingPeriodsCounts
      },
      doubleMultipleInfo: {
        chasingPeriodsCounts:
          chasingNumberDefault.doubleMultipleInfo.chasingPeriodsCounts,
        interval: chasingNumberDefault.doubleMultipleInfo.interval,
        incrementMultiple:
          chasingNumberDefault.doubleMultipleInfo.incrementMultiple
      },
      // defaultSetting: null,
      betInfo: {},
      // 追号后删除的下标购物篮item
      shoppingCarDeleteIndex: "",
      issueTraceGear: [] // 追号期数下拉
    };
  },
  computed: {
    ...mapGetters([
      "lotteryId",
      "defaultSetting",
      "systemSettingConfig",
      "isChasingShow"
    ]),
    allPeriods() {
      return this.issuesList.filter(item => {
        return item.isSelected;
      }).length;
    },
    allCountMoney() {
      let betCount = 0;
      this.issuesList.map(item => {
        if (!item.isSelected) return true;
        betCount = NP.plus(
          betCount,
          NP.times(
            this.betInfo.betNoteCounts,
            this.betInfo.coefficient,
            item.multiple,
            this.betInfo.price
          )
        );
      });
      return betCount;
    }
  },
  methods: {
    times() {
      return NP.times.apply(this, arguments);
    },
    submitChasingNumber() {
      if (!this.issuesList.find(item => item.isSelected))
        return this.$message({
          message: this.$t("errorMessage.errorMessage5"),
          type: "error"
        });
      this.betInfo.isChasingNumber = true;
      this.betInfo.stopChasingWn = this.stopChasingWn;
      const dataArr = [];
      const order = {};
      this.issuesList.map(item => {
        if (item.isSelected) {
          this.betInfo.awardInfo = { issue: item.issue };
          this.betInfo.multiple = item.multiple;
          this.betInfo.selectedMoney =
            this.betInfo.multiple *
            this.betInfo.price *
            +this.betInfo.coefficient *
            this.betInfo.selectedCounts;
          dataArr.push(shallowCopy(this.betInfo));
          order[item.issue] = item.multiple;
        }
      });
      if (
        dataArr.find(item => {
          return !+item.multiple;
        })
      ) {
        return this.$message({
          message: this.$t("errorMessage.errorMessage6"),
          type: "error"
        });
      }
      // todo : 未添加追号中确认弹窗的配置字段
      this.$gameAlertQrzdxx({
        shoppingCarList: dataArr,
        chasingInfo: Object.assign(this.betInfo, { order }),
        lotteryId: this.lotteryId,
        isChasingNumber: true,
        rootEl: this.$root.$el,
        isClearShoppingCar: false,
        shoppingCarDeleteIndex: this.shoppingCarDeleteIndex
      });
    },
    checkInputToNumber(tabName, attrName) {
      this[tabName][attrName] = this[tabName][attrName].replace(/[^\d]/g, "");
    },
    profitRateCheckList() {
      this.resetList();
      const {
        // 价格系数
        price,
        // 单倍单次最大中奖金额
        prize,
        // 已选注数
        selectedCounts,
        // 价格档位系数
        coefficient,
        // 最大倍数
        maxMultiple,
        // 用户奖金组中最大的key值
        maxPrizeGroupKey
      } = this.betInfo;
      // 单倍单注最大中奖金额
      const maxPirizeGroup = this.defaultSetting["max_prize_group"];
      // 追号期数
      const chasingPeriodsCounts =
        +this.profitRateInfo.chasingPeriodsCounts > this.issuesList.length
          ? this.issuesList.length
          : +this.profitRateInfo.chasingPeriodsCounts;
      this.profitRateInfo.chasingPeriodsCounts = chasingPeriodsCounts;
      // 起始倍数
      const startMultiple = +this.profitRateInfo.startMultiple;
      if (startMultiple === 0 || chasingPeriodsCounts === 0) return;
      // 最低收益率
      const lowestIncomeRate = this.profitRateInfo.lowestIncomeRate;
      let multiple = startMultiple;
      this.issuesList.find((item, index) => {
        let i = 1;
        // 当前投注数
        const allSelectedCounts = selectedCounts * (index + 1);
        // 当前总投注金额
        const allBetMoney = allSelectedCounts * coefficient * price;
        while (i <= chasingPeriodsCounts) {
          // 奖金
          // const wnMoney = prize * price * coefficient * multiple
          const wnMoney =
            ((maxPirizeGroup * prize[1]) / maxPrizeGroupKey) *
            price *
            Number(coefficient) *
            multiple;
          if (maxMultiple <= multiple) {
            if ((wnMoney - allBetMoney) / allBetMoney < lowestIncomeRate) {
              // todo: 对数组进组重置  重置为初始值
              this.$message({
                message: this.$t("errorMessage.errorMessage7"),
                type: "error"
              });
              break;
            }
          }
          if ((wnMoney - allBetMoney) / allBetMoney < lowestIncomeRate) {
            multiple += 1;
            i++;
          } else {
            break;
          }
        }
        item.isSelected = true;
        item.multiple = multiple;
        if (index + 1 === chasingPeriodsCounts) {
          return true;
        }
      });
    },
    resetList() {
      this.issuesList.forEach(item => {
        item.multiple = "";
        item.isSelected = false;
      });
    },
    checkMultipleInput(issues) {
      issues.multiple = issues.multiple.replace(/[^\d]/g, "");
    },
    sameMultipleCheckList() {
      this.resetList();
      const { maxMultiple } = this.betInfo;
      const chasingPeriodsCounts =
        +this.sameMultipleInfo.chasingPeriodsCounts > this.issuesList.length
          ? this.issuesList.length
          : +this.sameMultipleInfo.chasingPeriodsCounts;
      this.sameMultipleInfo.chasingPeriodsCounts = chasingPeriodsCounts;
      +this.sameMultipleInfo.startMultiple === 0 &&
        (this.sameMultipleInfo.startMultiple = 1);
      let startMultiple = parseFloat(this.sameMultipleInfo.startMultiple);
      if (startMultiple > maxMultiple && maxMultiple > 0)
        startMultiple = maxMultiple;
      if (!chasingPeriodsCounts) {
        return;
      }
      this.issuesList.find((item, index) => {
        item.isSelected = true;
        item.multiple = startMultiple;
        if (index + 1 === chasingPeriodsCounts) {
          return true;
        }
      });
    },
    doubleMultipleCheckList() {
      this.resetList();
      const { maxMultiple } = this.betInfo;
      const chasingPeriodsCounts =
        +this.doubleMultipleInfo.chasingPeriodsCounts > this.issuesList.length
          ? this.issuesList.length
          : +this.doubleMultipleInfo.chasingPeriodsCounts;
      if (!chasingPeriodsCounts) return;
      this.doubleMultipleInfo.chasingPeriodsCounts = chasingPeriodsCounts;
      const interval = +this.doubleMultipleInfo.interval;
      if (interval === 0) return;
      +this.doubleMultipleInfo.incrementMultiple === 0 &&
        (this.doubleMultipleInfo.incrementMultiple = 1);
      let incrementMultiple = this.doubleMultipleInfo.incrementMultiple;
      this.issuesList.find((item, index) => {
        item.isSelected = true;
        const countArea = Math.ceil((index + 1) / interval);
        item.multiple = Math.pow(incrementMultiple, countArea - 1);
        if (item.multiple > maxMultiple && maxMultiple > 0)
          item.multiple = maxMultiple;
        if (index + 1 === chasingPeriodsCounts) {
          return true;
        }
      });
    },
    setIssuesList() {
      const dataArr = [];
      GetIssueCanTraceList(this.lotteryId).then(res => {
        if (res.errno === 0) {
          let issueTraceGear = [];
          for (let i in res.data.issue_trace_gear) {
            issueTraceGear.push(res.data.issue_trace_gear[i]);
          }
          this.$set(this, "issueTraceGear", issueTraceGear);
          for (let key in res.data.issues) {
            const issueInfo = res.data.issues[key];
            dataArr.push({
              issue: issueInfo.issue,
              endTime: issueInfo["end_time2"],
              multiple: "",
              isSelected: false
            });
          }
          this.$set(this, "issuesList", dataArr);
        } else {
          this.$set(this, "issuesList", dataArr);
        }
        this.$loadingHide();
      });
    }
  },
  watch: {
    showWhichTab() {
      this.resetList();
    },
    defaultSetting() {
      this.setIssuesList();
    },
    periodsNumber(val) {
      this.profitRateInfo.chasingPeriodsCounts = val;
      this.sameMultipleInfo.chasingPeriodsCounts = val;
      this.doubleMultipleInfo.chasingPeriodsCounts = val;
    },
    isChasingShow(val) {
      this.show = val;
    },
    show(val) {
      this.$store.commit(CLOSE_CHASING_BOX, val);
      if (val) {
        this.isShow = val;
        if (isOpenAnimation) {
          setTimeout(() => {
            this.$refs.container.style.transition = "all 0.5s";
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        } else {
          setTimeout(() => {
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        }
      } else {
        if (isOpenAnimation) {
          this.$refs.container.style.top = "40%";
          this.$refs.container.style.opacity = 0;
          setTimeout(() => {
            this.isShow = val;
          }, 500);
        } else {
          this.isShow = val;
        }
      }
    }
  },
  mounted() {},
  created() {
    this.setIssuesList();
  }
};
</script>

