<template>
  <div class="">
    <div class="title">{{ $t("top.withdraw") }}</div>
    <div class="user-withdraw">
      {{ $t("withdraw.userWithdrawInfo1") }}
      <i class="username">{{ defaultSetting.username }}</i
      >，{{ $t("withdraw.userWithdrawInfo2")
      }}<i class="amount">{{
        +parseFloat(accountInfo.userBalance).toFixed(3) || "Loading"
      }}</i>
      {{ $t("withdraw.userWithdrawInfo3") }}
    </div>

    <el-form ref="form" class="form-withdraw" :model="form" label-width="140px">
      <el-form-item :label="$t('withdraw.collectionAccount')" prop="resource">
        <el-radio-group v-model="form.resource">
          <el-radio label="GFG 账号: 5013223"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="$t('withdraw.withdrawAmount')">
        <el-input-number
          v-model="form.amount"
          :ref="`withdraw-input-number`"
          controls-position="right"
          @change="handleChange"
          :max="150000"
          :min="1"
        ></el-input-number>
        <div class="withdraw-range">{{ $t("withdraw.withdrawRange") }}</div>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">{{
          $t("withdraw.nextStep")
        }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  closeOnClickModal,
  closeOnPressEscape
} from "../../config/game-default-config";
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      form: {
        amount: "",
        resource: "GFG 账号: 5013223"
      }
    };
  },
  computed: {
    ...mapGetters(["defaultSetting", "accountInfo"])
  },
  methods: {
    ...mapActions(["getUserBalance"]),
    handleChange() {},
    onSubmit() {
      console.log("提交数据");
      this.$message_box
        .confirm(
          `提现账号: ${this.form.resource}, 提现金额: ${this.form.amount}`,
          `确认提现`,
          {
            confirmButtonText: this.$t("popup.confirm"),
            cancelButtonClass: "el-btn-close",
            closeOnClickModal: closeOnClickModal,
            closeOnPressEscape: closeOnPressEscape
          }
        )
        .then(() => {
          var f = document.createElement("form");
          f.action = `/user-withdrawals/withdraw?1`;
          f.method = "POST"; // 指定为post
          f.innerHTML = `<input type='hidden' name='amount' value='${this.form.amount}'/>
            <input type='hidden' name='step' value='2'/>
            <input type='hidden' name='dodespoit' value='1'/>`;
          document.body.appendChild(f);
          f.submit();
        });
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs[`withdraw-input-number`].currentValue = "";
      // this.$refs[`recharge-input-number`].currentValue = this.form.amount
    });
  },
  created() {
    this.getUserBalance();
  }
};
</script>

<style scoped>
.title {
  line-height: 40px;
  font-size: 18px;
  color: #666666;
  text-indent: 20px;
  font-weight: bold;
  background-color: rgb(242, 242, 242);
  border-bottom: 1px solid rgb(204, 204, 204);
}
.user-withdraw {
  margin-top: 20px;
  display: inline-block;
  font-size: 16px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgb(229, 237, 240);
  margin-left: 20px;
}
.username {
  font-style: normal;
  color: rgb(25, 79, 162);
}
.amount {
  font-style: normal;
  font-size: 20px;
  color: red;
  font-weight: 600;
}
.form-withdraw {
  margin-left: 40px;
  margin-top: 20px;
}
.withdraw-range {
  margin-top: 20px;
  font-size: 13px;
  color: rgb(193, 189, 181);
}
</style>
