import axios from "axios";
import { sBetSettings, siteDomain } from "./require";
import { getLang } from "./lang";
import $i18n from "../lang/index";
import { GetApiFullUrl } from "@/config/config.js";
import { getToken } from "./auth";
// import { Message } from 'element-ui'
// import store from '../store'
// import router from '../router'
// import noShowMessageErr from '../config/no-show-message-errno'
// import { Base64 } from 'js-base64'

// todo : 需要按照打包时候的命令行进行变动而不是定义在文件内
const isTest = true;

console.log(siteDomain);

const request = axios.create({
  // baseURL: process.env.BASE_API,
  baseURL: "/api",
  timeout: 40000,
  headers: {
    "Content-Type": "application/x-www-form-urlencoded"
  },
  // responseType: 'json',
  transformRequest: [
    function(data) {
      /*eslint-disable*/
      let ret = ''
      for (let it in data) {
        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
      }
      return ret
    }
  ]
})

request.interceptors.request.use(
  config => {
    // todo : 不需要登录状态则不需要在每个请求添加token字段 待确认
    if (config.method === 'get') {
      // config.params.merchant = sBetSettings.merchant
      // config.params.username = sBetSettings.username
      // config.params.prize_group = sBetSettings.prize_group
      config.params = config.params || {};
      config.params.lang =
        $i18n.locale || getLang() || sBetSettings.language
    } else {
      // config.data.merchant = sBetSettings.merchant
      // config.data.username = sBetSettings.username
      // config.data.prize_group = sBetSettings.prize_group
      config.data.lang = $i18n.locale || getLang() || sBetSettings.language
    }
    if (process.env.NODE_ENV === 'development') {
      // 线上环境下需要对后台返回参数进行处理
      if (config.method === 'get') {
        config.params.develop = 1
      } else {
        config.data.develop = 1
      }
    }
    config.headers = config.headers || {};
    config.headers.token = getToken();
    if (config.url && config.url.startsWith("/"))
      config.baseURL = "";
    config.baseURL = GetApiFullUrl(config.baseURL);
    return config
  },
  err => console.log(err)
)

request.interceptors.response.use(
  response => {
    if (process.env.NODE_ENV === 'production' && !isTest) {
      // 线上环境下需要对后台返回参数进行处理
      response.data = JSON.parse(Base64.decode(response.data))
    }
    if (response.data.errno === 3004 || response.data.errno === 2020) {
      window.reload()
    }
    return response.data
  },
  err => console.log(err)
)

export default request
