<template>
  <div :class="rootCss">
    <div :class="'logo ' + logoCss"></div>
    <div class="top_prize">
      {{ $t("home.topPrize") }}: <span>￡{{ prize }}</span>
    </div>
    <div :class="lotteryNameOnTop ? 'hd' : 'hd hd-cen'">
      <h3>{{ lotteryName }}</h3>
      <p>{{ lotteryType === "lhc" ? "Lotto Results - " + issue : issue }}</p>
    </div>

    <div v-if="lotteryType === 'pk10'" class="car">
      <div :class="'car-' + item" v-for="(item, index) in codes" :key="index">
        {{ item }}
      </div>
    </div>
    <div v-else-if="lotteryType === 'lhc'" class="balls">
      <div
        :class="colors[index]"
        v-for="(item, index) in codes.slice(0, 6)"
        :key="index"
      >
        {{ item }}
      </div>
      <span>+</span>
      <div :class="colors[6]">{{ codes[6] }}</div>
    </div>
    <div v-else class="balls">
      <div v-for="(item, index) in codes" :key="index">
        {{ item }}
      </div>
    </div>

    <div class="time" v-show="!timeOnButtom">
      <div v-if="lotteryId === gold60s.lotteryId" class="desc">
        <h3>{{ $t("home.timeTo") }}</h3>
        <p>{{ $t("home.nextDraw") }}</p>
      </div>
      <div class="desc" v-else>
        {{ $t("home.timeTo") }}{{ $t("home.nextDraw") }}
      </div>
      <countDown :day="day" :hour="hour" :minute="minute" :second="second" />
    </div>

    <div class="operation" v-show="showOperation">
      <div class="desc" v-if="lotteryType === 'lhc'">
        <p>{{ $t("home.over") }}<span>900,000</span></p>
        <p>{{ $t("home.overUnit") }}</p>
      </div>
      <a :href="'#/game-record/' + lotteryId" class="button-white">{{
        $t("home.moreResults")
      }}</a>
      <a :href="'#/'" class="button-white">{{ $t("home.prizeBreakdown") }}</a>
    </div>
    <lotteryFooter
      :type="footType"
      :button-type="buttonType"
      :show-time="timeOnButtom"
      :url="'#/game/' + lotteryId"
      :day="day"
      :hour="hour"
      :minute="minute"
      :second="second"
    />
  </div>
</template>

<script>
import lotteryFooter from "@/views/home/Footer.vue";
import countDown from "@/views/home/CountDown.vue";

export default {
  components: {
    lotteryFooter,
    countDown
  },
  data() {
    return {
      gold60s: 12
    };
  },
  props: [
    "day",
    "hour",
    "minute",
    "second",
    "footType",
    "buttonType",
    "rootCss",
    "logoCss",
    "lotteryName",
    "issue",
    "codes",
    "timeOnButtom",
    "lotteryType",
    "showOperation",
    "lotteryNameOnTop",
    "colors",
    "prize",
    "lotteryId"
  ],
  mounted: function() {}
};
</script>
