import { getToken } from "../util/auth";
import {
  getCreditMenu,
  getMenu,
  getTypeList,
  getLotteryMenu
} from "../util/menu";
import { getSelectAmountData, getMerchantConfig } from "../util/user";
import {
  liveLottery,
  isOpenAnimation,
  isShowMenuBtnsInHezhi
} from "../config/game-default-config";
import { sBetSettings } from "../util/require";

const getters = {
  token: state => state.user.token || getToken() || "",
  userInfo: state => state.user.userInfo,
  isLogin: state => state.user.isLogin,
  lang: state => state.common.lang,
  playTypeList: state => state.menu.playTypeList || getTypeList(),
  showMenuList: state => state.menu.showMenuList,
  menu: state => state.menu.menu || getMenu() || null,
  creditMenu: state => state.menu.creditMenu || getCreditMenu() || null,
  szcpMenuList: state => state.menu.szcpMenuList,
  lotteryId: state => state.game.lotteryId,
  availablePrizeGroups: state => state.game.availablePrizeGroups,
  isChoiceShow: state => state.game.isChoiceShow,
  shoppingCarList: state => state.game.shoppingCarList,
  showYlPlay: state => state.game.showYlPlay,
  showLrPlay: state => state.game.showLrPlay,
  sscSelectedCounts: state => state.ssc.selectedCounts,
  sscAwardInfo: state => state.ssc.awardInfo,
  sscLastWnNumber: state => state.ssc.lastWnNumber,
  recentWnNumberList: state => state.game.recentWnNumberList,
  // 接口模式
  // defaultSetting: state => state.game.defaultSetting,
  // 模板配置模式
  defaultSetting: state => state.game.defaultSetting,
  isChasingShow: state => state.menu.isChasingShow,
  isCreateNumber: state => state.menu.isCreateNumber,
  oftenPlayTypes: state => state.game.oftenPlayTypes,
  isGotOftenPlayTypeList: state => state.game.isGotOftenPlayTypeList,
  gameDetail: state => state.game.gameDetail,
  bettingRecordShow: state => state.common.nowShowTab,
  recordTableDataList: state => state.common.recordTableDataList,
  rankingDataShow: state => state.common.rankingDataShow,
  handicapQuickAmount: state => state.common.handicapQuickAmount,
  handicapChoiceWay: state => state.common.handicapChoiceWay,
  quickSelectAmountData: state =>
    state.common.quickSelectAmountData || getSelectAmountData(),
  pk10ShowStatus: state => state.game.pk10ShowStatus,
  isGettingData: state => state.common.isGettingData,
  handicapChoiceBonus: state => state.common.handicapChoiceBonus,
  accountInfo: state => state.user.accountInfo,
  clearFlag: state => state.ssc.clearFlag,
  lotteryNumberLastImInfo: state => state.game.lotteryNumberLastImInfo,
  isImShowBox: state => state.game.isImShowBox,
  imLastShowHeight: state => state.game.imLastShowHeight,
  imGameIsOpening: state => state.game.imGameIsOpening,
  imNowGameTypeName: state => state.game.imNowGameTypeName,
  imNowOpenCounts: state => state.game.imNowOpenCounts,
  prizeVoicePath: state => state.game.prizeVoicePath,
  stopVoiceEnd: state => state.game.stopVoiceEnd,
  stopVoiceEndTwo: state => state.game.stopVoiceEndTwo,
  customerServiceInfo: state => state.common.customerServiceInfo,
  getBonusGroupInGame: state => state.game.getBonusGroupInGame,
  getMultipleInGame: state => state.game.getMultipleInGame,
  getCoefficientInGame: state => state.game.getCoefficientInGame,
  isFirstEnter: state => state.common.isFirstEnter,
  nowScreenStatus: state => state.common.nowScreenStatus,
  ylData: state => state.ssc.ylData,
  lrData: state => state.ssc.lrData,
  device: state => state.common.device,
  isGetDataInImGame: state => state.common.isGetDataInImGame,
  lrylBtnControl: state => state.ssc.lrylBtnControl,
  showDataFlag: state => state.game.gameDetail["analysis"] || false,
  // showDataFlag: state => true,
  marQueeIntervalFlag: state => state.common.marQueeIntervalFlag,
  getAnDataInRankFlag: state => state.common.getAnDataInRankFlag,
  showOwnBonusInComponent: state => state.common.showOwnBonusInComponent,
  isLive: state =>
    !!liveLottery.find(item => +item.lotteryId === +state.game.lotteryId),
  isHttp: () => document.location.protocol === "http:",
  topUserKey: state => state.user.topUserKey,
  winTipFlag: state => state.common.winTipFlag,
  winAudioFlag: state => state.game.winAudioFlag,
  isOpenAnimation: () => isOpenAnimation,
  isShowMenuBtnsInHezhi: () => isShowMenuBtnsInHezhi,
  // 获取当前的彩种名字
  nowActiveGameName: state => state.game.nowActiveGameName,

  // 更新在投注中需要的json配置文件
  // sscWaysInFirst: state => JSON_CONFIG.sscWaysInFirst
  sscWaysInFirst: state => state.game.sscWaysInFirst,
  // nowActiveWays: state => state.game[`${state.game.nowActiveTypeName}WaysInFirst`],
  nowActiveWays: state => state.game.nowActiveWays,
  // nowWayInfoDetail: state => state.game[`${state.game.nowActiveTypeName}WayInfoDetail`],
  nowWayInfoDetail: state => state.game.nowWayInfoDetail,
  lotteryMenuList: state =>
    (state.game.lotteryMenuList &&
      state.game.lotteryMenuList.length > 0 &&
      state.game.lotteryMenuList) ||
    getLotteryMenu() ||
    [],
  // 当前是盘口玩法还是标准玩法 标准： normal 盘口： handicap
  nowBetMode: state => state.game.nowBetMode,
  // 当前的彩系名称
  nowGameType: state => state.game.nowGameType,
  // 当前彩种的盘口玩法菜单列表
  nowActiveHandicapWays: state => state.game.nowActiveHandicapWays,
  nowRankingHandicapWays: state => state.game.nowRankingHandicapWays,
  nowActiveHandicapWayDetail: state => state.game.nowActiveHandicapWayDetail,
  handicapOddsInfo: state => state.game.oddInfo,
  prizeGroup: state =>
    (state.game.defaultSetting &&
      state.game.defaultSetting["default_prize_group"]) ||
    sBetSettings[`default_prize_group`],
  // 当前彩种的最高派奖金额
  nowMaxGetPrize: state => {
    return state.game.gameDetail["high_frequency"]
      ? state.game.systemSettingConfig["high_frequency_bet_max_prize"]
      : state.game.systemSettingConfig["low_frequency_bet_max_prize"];
  },
  nowHighestPrizeLevel: state => state.game.nowHighestPrizeLevel,
  systemSettingConfig: state => state.game.systemSettingConfig,
  openPromptVoice: state =>
    state.common.openPromptVoice ||
    state.game.systemSettingConfig["voice_reminder"] ||
    0,
  // 标识当前彩种是否为即开型彩种
  isIm: state => {
    let nowShowLottery;
    state.game.lotteryMenuList.find(item => {
      return item["lotteries"].find(itemInner => {
        if (itemInner.identifier === state.game.nowActiveGameName) {
          nowShowLottery = itemInner;
          return true;
        }
      });
    });
    return nowShowLottery && !!nowShowLottery[`is_instant`];
  },
  isStopWin: state => state.game.isStopWin,
  configInfoList: state => state.game.configInfoList,
  nowActiveWayInfoDetail: state => state.game.nowActiveWayInfoDetail,
  // 商户自定义配置文件
  merchantConfigInfoList: state =>
    (state.game.merchantConfigInfoList &&
      state.game.merchantConfigInfoList.length > 0 &&
      state.game.merchantConfigInfoList) ||
    getMerchantConfig() ||
    [],
  merchantName: state => state.game.merchantName,
  coefficientListInfo: state => state.game.coefficientList,
  closeWaysList: state => state.game.closeWaysList,
  noticeList: state => state.game.noticeList,
  nowLotteryInfo: state => state.game.nowLotteryInfo,
  allGameInfo: state => state.game.allGameInfo,
  // 当前选择的行数b
  nowSelectedLineCounts: state => state.game.nowSelectedLineCounts,

  //新增
  bindingBankCard: state => state.user.bindingBankCard
};

export default getters;
