import {
  GetAvailablePrizeGroups,
  getFavoritesLottery,
  getBetSettings
} from "@/api/game/game";
import {
  getLotteryList,
  getWaysInfo,
  getHandicapWaysInfo,
  getHandicaOddsInfo,
  getUpWayInfo,
  getOddsInfo,
  getSystemSettingConfig,
  getConfigInfoList,
  getMerchantConfigInfoList,
  getCoefficientList,
  getCloseWaysList,
  getWnNumberInfoList,
  getNoticeList,
  getGameDetail
} from "@/api/json/json";
//
import {
  topTypeNameMap,
  processWayInFirst,
  processCreditWayInFirst,
  processLotteryMenuFirst
} from "../../server-config";
import {
  SET_LOTTERY_ID,
  SET_PRIZE_GROUPS,
  SWITCH_CHOICE_SHOW,
  ADD_NEW_SHOPPING_CAR_ITEM,
  SET_YL_PLAY,
  SET_LR_PLAY,
  CLEAR_SHOPPING_CAR,
  DELETE_SHOPPING_ITEM,
  SET_RECENT_WN_NUMBER_LIST,
  SET_DEFAULT_SETTING,
  SET_OFTEN_PLAY_TYPES,
  SET_LIKE_GAMES_IN_ALL_GAME,
  SET_GAME_DETAIL_INFO,
  SET_PK10_SHOW_STATUS,
  SET_LAST_WN_NUMBER,
  SET_IM_LOTTERY_NUMBER,
  SET_IM_LOTTERY_BOX_SHOW,
  SET_IM_SHOW_BOX_HEIGHT,
  SET_IM_GAME_OPENING_STATUS,
  SET_IM_NOW_GAME_TYPE,
  SET_IM_NOW_OPEN_COUNTS,
  SET_PRIZE_PATH,
  SET_STOP_VOICE_END,
  SET_STOP_VOICE_END_TWO,
  SET_COEFFICIENT_IN_GAME,
  SET_MULTIPLE_IN_GAME,
  SET_BONUS_GROUP_IN_GAME,
  SET_GETTED_OFTEN_PLAY_TYPE_LIST,
  SET_WIN_AUDIO_FLAG,
  SET_NOW_ACTIVE_GAME,
  SELECT_TOP_GAME_TYPE,
  SELECT_SECOND_GAME_TYPE,
  START_GAME_TYPE_NAME_MAP,
  SET_NOW_ACTIVE_WAYS,
  SET_NOW_WAY_INFO_DETAIL,
  SET_NOW_BET_MODE,
  SET_GETTING_DATA_STATUS,
  SET_PLAY_TYPE_LIST,
  SET_NOW_GAME_TYPE,
  SET_NOW_ACTIVE_HANDICAP_WAYS,
  SET_NOW_RANKING_HANDICAP_WAYS,
  SELECT_TOP_HANDICAP_GAME_TYPE,
  SET_ODDS_INFO,
  SET_NOW_ACTIVE_HANDICAP_WAY_DETAIL,
  SET_HIGHEST_PRIZE_LEVEL,
  SET_LOTTERY_LIST,
  SET_SYSTOM_CONFIG_INFO,
  SET_NOW_PLAY_NAME,
  SET_IS_STOP_WIN,
  SET_CONFIG_INFO_LIST,
  SET_NOW_ACTIVE_WAYS_INFO,
  SET_MERCHANT_CONFIG_INFO_LIST,
  SET_COEFFICIENT_LIST,
  SET_CLOSE_WAYS_LIST,
  SET_NOTICE_LIST,
  SET_NOW_LOTTERY_INFO,
  SET_ALL_GAME_INFO,
  SET_SELECTED_LINE_COUNTS,
  SET_LANG,
  SET_SZCP_MENU_LIST
} from "../nameSpace";
import { saveLotteryId, getLotteryId, saveLotteryMenu } from "../../util/menu";
import { saveMerchantConfig } from "../../util/user";
// showWnNumberCount 开奖记录显示条数
import {
  playModeNormal,
  playModeHandicap,
  getConfigInfoListInterval
} from "../../config/game-default-config";
import { prizeVoice } from "../../config/audio-config";
import {
  nowGameName,
  sBetSettings,
  initIsFirstEnter,
  setAllPath
} from "../../util/require";
import {
  getLotteryName,
  saveLotteryName,
  saveNowPlayName
} from "../../util/cache";
import {
  LOTTERY_LIST,
  WAY_GROUP,
  TOP_LIST,
  CREDIT_WAYS,
  CREDIT_ODD,
  SYSTEM_CONFIG,
  getInfoMapList,
  prefix,
  ODD_INDO,
  COEFFICIENT_LIST,
  CLOSE_WAYS,
  WN_NUMBER_LIST,
  NOTICE,
  updateConfigVersionList,
  LOTTERY_INFO
} from "../../config/configVersionConfig";
import lotteryTypeNameMap from "../../config/route-name-map";
import Vm from "../../main";
import { shallowCopy } from "../../util";
import { Message } from "element-ui";
import { getLang } from "../../util/lang";
// import router from '../../router'
// import { getRouteNameById } from '../../common/js/util'
let likeGameTimeBegin = {};
let getLastWnNumberTimer;
// 持续获取配置文件的循环定时器标识
let getConfigInfoListTimer;
// 获取系统配置文件的promise对象
let getConfigPromise = null;
// 获取商户自定义配置对象的Promise对象
let getMerchantPromise = null;
// 持续获取商户配置文件的循环定时器标识
let getMerchantConfigInfoListTimer;

const startGameTypeName = "SSC";
setAllPath();

export default {
  state: {
    lotteryId: getLotteryId() || "",
    availablePrizeGroups: [],
    isChoiceShow: false,
    shoppingCarList: [],
    showYlPlay: false,
    showLrPlay: false,
    recentWnNumberList: [],
    defaultSetting: sBetSettings,
    oftenPlayTypes: [],
    gameDetail: {},
    // 0 待开奖 1 马上开奖 2 已开奖
    pk10ShowStatus: null,
    lotteryNumberLastImInfo: "",
    isImShowBox: false,
    imLastShowHeight: "",
    imGameIsOpening: false,
    imNowGameTypeName: "",
    imNowOpenCounts: 0,
    prizeVoicePath: prizeVoice[12].path,
    stopVoiceEnd: false,
    stopVoiceEndTwo: false,
    getBonusGroupInGame: "",
    getMultipleInGame: 1,
    getCoefficientInGame: "",
    // 当前是否已经获取了最新的常玩彩种列表
    isGotOftenPlayTypeList: false,
    winAudioFlag: false,
    // 当前的彩种名称
    nowActiveGameName: initIsFirstEnter()
      ? nowGameName
      : getLotteryName() || nowGameName,

    // 时时彩的玩法列表
    // sscWaysInFirst: JSON_CONFIG.sscWaysInFirst,
    // 六合彩的玩法列表
    // lhcWaysInFirst: JSON_CONFIG.lhcWaysInFirst,

    // 各个玩法的详细信息
    // 时时彩的玩法详情
    // sscWayInfoDetail: JSON_CONFIG.sscWayInfoDetail,
    // 六合彩的玩法列表
    // lhcWayInfoDetail: JSON_CONFIG.lhcWayInfoDetail,

    // 彩种列表
    lotteryMenuList: [],

    // 当前选中的彩系名称
    nowActiveTypeName: topTypeNameMap[startGameTypeName],

    // 用于映射的当前彩系的名称
    startGameTypeNameMap: "SSC",

    // 当前选中的菜单列表
    nowActiveWays: [],

    // 当前选中彩种的盘口玩法菜单列表
    nowActiveHandicapWays: [],

    // 当前选中彩种的盘口玩法排行匹配数据
    nowRankingHandicapWays: [],

    // 当前的玩法详情
    nowWayInfoDetail: [],

    // 当前的投注模式
    nowBetMode: "normal",

    // 当前彩系
    nowGameType: "SSC",

    // 当前盘口玩法的详情信息
    nowActiveHandicapWayDetail: [],

    // 当前彩种的赔率信息数据
    oddInfo: {},
    // 当前存在于全局中的最高奖级信息
    nowHighestPrizeLevel: "",
    // 当前的系统设置信息
    systemSettingConfig: {},

    // 当前玩法名称
    nowPlayName: [],

    // 即开型彩种中奖后是否停止开奖
    isStopWin: true,

    // 版本控制对象
    configInfoList: {
      isRight: false
    },
    // 当前彩种玩法信息（处理为可用形式后的数据）
    nowActiveWayInfoDetail: {},
    // 商户标识
    merchantName: sBetSettings[`merchant`].toLowerCase(),
    // 商户的版本配置文件
    merchantConfigInfoList: {
      isRight: false
    },
    // 价格模式的列表
    coefficientList: null,
    // 当前关闭的玩法列表信息
    closeWaysList: [],
    // 公告信息
    noticeList: [],
    // 当前的彩种信息
    nowLotteryInfo: null,
    // 所有彩种的配置信息
    allGameInfo: {
      isRight: false
    },
    // 当前选择投注号码的行数
    nowSelectedLineCounts: 0
  },
  mutations: {
    // 设置选择的行数
    [SET_SELECTED_LINE_COUNTS](state, nowSelectedLineCounts) {
      state.nowSelectedLineCounts = nowSelectedLineCounts;
    },
    [SET_ALL_GAME_INFO](state, allGameInfo) {
      if (!allGameInfo) allGameInfo = false;
      state.allGameInfo = {
        isRight: !!allGameInfo,
        data: allGameInfo
      };
    },
    // 保存当前选中的彩种的彩种信息
    [SET_NOW_LOTTERY_INFO](state, nowLotteryInfo) {
      state.nowLotteryInfo = nowLotteryInfo;
    },
    [SET_NOTICE_LIST](state, noticeList) {
      state.noticeList = noticeList;
    },
    // 获取被关闭的玩法列表
    [SET_CLOSE_WAYS_LIST](state, closeWaysList) {
      state.closeWaysList = closeWaysList;
    },
    // 设置价格列表的值
    [SET_COEFFICIENT_LIST](state, coefficientList) {
      state.coefficientList = coefficientList;
    },
    // 设置当前的商户配置文件列表
    [SET_MERCHANT_CONFIG_INFO_LIST](state, configInfoList) {
      state.merchantConfigInfoList = configInfoList;
      saveMerchantConfig(configInfoList);
    },
    // 设置当前的版本文件
    [SET_CONFIG_INFO_LIST](state, configInfoList) {
      state.configInfoList = configInfoList;
    },
    // 设置当前的玩法名称
    [SET_IS_STOP_WIN](state, isStopWin) {
      state.isStopWin = isStopWin;
    },
    // 设置当前的玩法名称
    [SET_NOW_PLAY_NAME](state, nowPlayName) {
      state.nowPlayName = nowPlayName;
      saveNowPlayName(nowPlayName);
    },
    // 设置当前的系统配置信息文件
    [SET_SYSTOM_CONFIG_INFO](state, systemSettingConfig) {
      state.systemSettingConfig = systemSettingConfig;
    },
    // 设置当前的彩种信息
    [SET_LOTTERY_LIST](state, lotteryMenuList) {
      state.lotteryMenuList = lotteryMenuList;
      saveLotteryMenu(lotteryMenuList);
    },
    // 设置当前的最高奖级信息
    [SET_HIGHEST_PRIZE_LEVEL](state, nowHighestPrizeLevel) {
      state.nowHighestPrizeLevel = nowHighestPrizeLevel;
    },
    // 设置当前的投注模式 即: 标准盘还是盘口玩法
    [SET_NOW_BET_MODE](state, nowBetMode) {
      state.nowBetMode = nowBetMode;
    },
    // 获取某个彩种的顶级玩法列表
    [SET_NOW_WAY_INFO_DETAIL](state, nowWayInfoDetail) {
      state.nowWayInfoDetail = nowWayInfoDetail;
    },
    // 获取某个彩种的盘口玩法对应赔率
    [SET_ODDS_INFO](state, oddInfo) {
      state.oddInfo = oddInfo;
    },
    // 获取某个彩种的盘口玩法列表
    [SET_NOW_ACTIVE_HANDICAP_WAYS](state, nowActiveHandicapWays) {
      state.nowActiveHandicapWays = nowActiveHandicapWays;
    },
    [SET_NOW_RANKING_HANDICAP_WAYS](state, nowRankingHandicapWays) {
      state.nowRankingHandicapWays = nowRankingHandicapWays;
    },
    // 获取当前玩法的详情信息
    [SET_NOW_ACTIVE_WAYS](state, nowActiveWays) {
      state.nowActiveWays = nowActiveWays;
    },
    [START_GAME_TYPE_NAME_MAP](state, startGameTypeNameMap) {
      state.startGameTypeNameMap = startGameTypeNameMap;
    },
    [SELECT_SECOND_GAME_TYPE](state, gameInfo) {
      const { secondGameType, itemClick } = gameInfo;
      if (!secondGameType || !itemClick) return false;
      const nowActiveGame = state.nowActiveWays.find(item => item.isSelected);
      nowActiveGame &&
        nowActiveGame.children.map(item => {
          item.children.map(itemInner => {
            itemInner.isSelected = false;
          });
        });
      itemClick.isSelected = true;
    },
    // 选中某一个顶级标准玩法的彩单
    [SELECT_TOP_GAME_TYPE](state, type) {
      // todo:  在选择顶级玩法的同时还需要设置一个默认选中的子玩法 应在此处调用 待补全
      state.nowActiveWays.map(gameType => {
        gameType.isSelected = false;
        gameType.children.map(item => {
          item.children.map(itemInner => {
            itemInner.isSelected = false;
          });
        });
      });
      // const nowPlayName = []
      type.isSelected = true;
      // nowPlayName.push(type.label)
      type.children[0].children[0].isSelected = true;
      // nowPlayName.push(type.children[0].children[0].label)
      // this.commit(SET_NOW_PLAY_NAME, JSON.stringify(nowPlayName))
    },
    [SELECT_TOP_HANDICAP_GAME_TYPE](state, type) {
      state.nowActiveHandicapWays.map(gameType => {
        gameType.isSelected = false;
      });
      type.isSelected = true;
    },
    [SET_NOW_ACTIVE_GAME](state, nowActiveGameName) {
      state.nowActiveGameName = nowActiveGameName;
      saveLotteryName(nowActiveGameName);
    },
    [SET_GETTED_OFTEN_PLAY_TYPE_LIST](state, isGotOftenPlayTypeList) {
      state.isGotOftenPlayTypeList = isGotOftenPlayTypeList;
    },
    [SET_PK10_SHOW_STATUS](state, pk10ShowStatus) {
      state.pk10ShowStatus = pk10ShowStatus;
    },
    [SET_LOTTERY_ID](state, lotteryId) {
      console.log(lotteryId, "set lotteryId");
      state.lotteryId = lotteryId;
      saveLotteryId(lotteryId);
    },
    [SET_PRIZE_GROUPS](state, availablePrizeGroups) {
      availablePrizeGroups.groups = availablePrizeGroups.groups.map(
        item => +item
      );
      state.availablePrizeGroups = availablePrizeGroups;
    },
    [SWITCH_CHOICE_SHOW](state) {
      state.isChoiceShow = true;
    },
    [ADD_NEW_SHOPPING_CAR_ITEM](state, shoppingItem) {
      state.shoppingCarList.unshift(shoppingItem);
    },
    [SET_YL_PLAY](state) {
      state.showYlPlay = !state.showYlPlay;
    },
    [SET_LR_PLAY](state) {
      state.showLrPlay = !state.showLrPlay;
    },
    [CLEAR_SHOPPING_CAR](state) {
      state.shoppingCarList = [];
    },
    [DELETE_SHOPPING_ITEM](state, index) {
      state.shoppingCarList.splice(index, 1);
    },
    [SET_RECENT_WN_NUMBER_LIST](state, recentWnNumberList) {
      state.recentWnNumberList = recentWnNumberList;
      for (let i = 0; i < state.recentWnNumberList.length; i++) {
        Vm.$set(state.recentWnNumberList[i], "ball-color", "");
      }
    },
    [SET_DEFAULT_SETTING](state, defaultSetting) {
      state.defaultSetting = defaultSetting;
      window["sBetSettings"] = defaultSetting;
    },
    [SET_OFTEN_PLAY_TYPES](state, oftenPlayTypeList) {
      state.oftenPlayTypes = oftenPlayTypeList;
    },
    [SET_GAME_DETAIL_INFO](state, gameDetail) {
      state.gameDetail = gameDetail;
    },
    [SET_IM_LOTTERY_NUMBER](state, wnNumberInfo) {
      state.lotteryNumberLastImInfo = wnNumberInfo;
    },
    [SET_IM_LOTTERY_BOX_SHOW](state, isImShowBox) {
      state.isImShowBox = isImShowBox;
    },
    [SET_IM_SHOW_BOX_HEIGHT](state, height) {
      state.imLastShowHeight = height;
    },
    [SET_IM_GAME_OPENING_STATUS](state, imGameIsOpening) {
      state.imGameIsOpening = imGameIsOpening;
    },
    [SET_IM_NOW_GAME_TYPE](state, imNowGameTypeName) {
      state.imNowGameTypeName = imNowGameTypeName;
    },
    [SET_IM_NOW_OPEN_COUNTS](state, imNowOpenCounts) {
      state.imNowOpenCounts = imNowOpenCounts;
    },
    [SET_PRIZE_PATH](state, prizeVoicePath) {
      state.prizeVoicePath = prizeVoicePath;
    },
    [SET_STOP_VOICE_END](state, stopVoiceEnd) {
      state.stopVoiceEnd = stopVoiceEnd;
    },
    [SET_STOP_VOICE_END_TWO](state, stopVoiceEndTwo) {
      state.stopVoiceEndTwo = stopVoiceEndTwo;
    },
    [SET_COEFFICIENT_IN_GAME](state, getCoefficientInGame) {
      state.getCoefficientInGame = getCoefficientInGame;
    },
    [SET_MULTIPLE_IN_GAME](state, getMultipleInGame) {
      state.getMultipleInGame = getMultipleInGame;
    },
    [SET_BONUS_GROUP_IN_GAME](state, getBonusGroupInGame) {
      state.getBonusGroupInGame = getBonusGroupInGame;
    },
    [SET_WIN_AUDIO_FLAG](state, winAudioFlag) {
      state.winAudioFlag = winAudioFlag;
    },
    [SET_NOW_GAME_TYPE](state, nowGameType) {
      state.nowGameType = nowGameType;
    },
    [SET_NOW_ACTIVE_HANDICAP_WAY_DETAIL](state, nowActiveHandicapWayDetail) {
      state.nowActiveHandicapWayDetail = nowActiveHandicapWayDetail;
    },
    [SET_NOW_ACTIVE_WAYS_INFO](state, nowActiveWayInfoDetail) {
      state.nowActiveWayInfoDetail = nowActiveWayInfoDetail;
    }
  },
  actions: {
    getNoticeList({ commit, getters }) {
      const noticeVersion =
        getters[getInfoMapList.noticeList][`${prefix}-${NOTICE}`];
      getNoticeList(`${prefix}-${NOTICE}-${noticeVersion}.json`).then(res => {
        const headerNoticeList = [];
        for (let key in res) {
          headerNoticeList.push({
            title: res[key][`title`]
          });
        }
        commit(SET_NOTICE_LIST, headerNoticeList);
      });
    },
    getCloseWaysList({ commit, getters }) {
      return new Promise((resolve, reject) => {
        const closeWaysVersion =
          getters[getInfoMapList.closeWayList][
            `${getters.merchantName}-${CLOSE_WAYS}`
          ];
        getCloseWaysList(
          `${getters.merchantName}-${CLOSE_WAYS}-${closeWaysVersion}.json`,
          getters.merchantName
        )
          .then(res => {
            commit(SET_CLOSE_WAYS_LIST, res);
            resolve(res);
          })
          .catch(err => reject(err));
      });
    },
    getCoefficientList({ commit, getters }) {
      return new Promise((resolve, reject) => {
        const coefficientListVersion =
          getters[getInfoMapList.coefficientList][
            `${getters.merchantName}-${COEFFICIENT_LIST}`
          ];
        getCoefficientList(
          `${getters.merchantName}-${COEFFICIENT_LIST}-${coefficientListVersion}.json`,
          getters.merchantName
        )
          .then(res => {
            commit(SET_COEFFICIENT_LIST, res);
            resolve(res);
          })
          .catch(err => reject(err));
      });
    },
    // isGetConfigInfo
    getAllConfigInfoList({ dispatch }) {
      return Promise.all([
        dispatch("isGetMerchantConfigInfo")
        // dispatch('isGetConfigInfo')
      ]);
    },
    isGetMerchantConfigInfo({ dispatch, getters }) {
      return new Promise((resolve, reject) => {
        if (getters.merchantConfigInfoList.isRight) {
          resolve(getters.merchantConfigInfoList);
        } else {
          dispatch("getMerchantConfigInfoList")
            .then(res => resolve(res))
            .catch(err => reject(err));
        }
      });
    },
    // 获取商户自定义版本配置对象
    getMerchantConfigInfoList({ commit, dispatch, getters }) {
      if (getMerchantPromise) return getMerchantPromise;
      if (getMerchantConfigInfoListTimer) {
        clearTimeout(getMerchantConfigInfoListTimer);
      }
      getMerchantPromise = new Promise((resolve, reject) => {
        getMerchantConfigInfoList(1, getters.merchantName.toLowerCase())
          .then(res => {
            const beforeConfigInfo = shallowCopy(
              getters.merchantConfigInfoList
            );
            if (beforeConfigInfo.isRight) {
              res.isRight = true;
              commit(SET_MERCHANT_CONFIG_INFO_LIST, res);
              for (let key in updateConfigVersionList) {
                const fieldInfo = updateConfigVersionList[key];
                let fieldStr = `${fieldInfo.prefix}-${key}`;
                fieldInfo.isDetailLotteryName &&
                  (fieldStr += getters.nowActiveGameName.toLowerCase());
                fieldInfo.isDetailLotteryType &&
                  (fieldStr += lotteryTypeNameMap[getters.nowGameType]);
                if (res[fieldStr] !== beforeConfigInfo[fieldStr]) {
                  dispatch(fieldInfo.actionName, {
                    ...fieldInfo.params,
                    isUpdate: true
                  });
                }
              }
              getMerchantConfigInfoListTimer = setTimeout(() => {
                dispatch("getMerchantConfigInfoList");
              }, getConfigInfoListInterval);
              getMerchantPromise = null;
              return resolve(res);
            }
            res.isRight = true;
            commit(SET_MERCHANT_CONFIG_INFO_LIST, res);
            getMerchantConfigInfoListTimer = setTimeout(() => {
              dispatch("getMerchantConfigInfoList");
            }, getConfigInfoListInterval);
            getMerchantPromise = null;
            resolve(res);
          })
          .catch(err => {
            getMerchantPromise = null;
            reject(err);
          });
      });
      return getMerchantPromise;
    },
    // 判定当前是否成功获取到版本控制文件
    isGetConfigInfo({ dispatch, getters }) {
      return new Promise((resolve, reject) => {
        if (getters.configInfoList.isRight) {
          resolve(getters.configInfoList);
        } else {
          dispatch("getConfigInfoList")
            .then(res => resolve(res))
            .catch(err => reject(err));
        }
      });
    },
    // 获取版本控制文件
    getConfigInfoList({ commit, dispatch }) {
      if (getConfigPromise) return getConfigPromise;
      if (getConfigInfoListTimer) clearTimeout(getConfigInfoListTimer);
      getConfigPromise = new Promise((resolve, reject) => {
        getConfigInfoList(1)
          .then(res => {
            // todo : 此处进行比对 查看配置文件的差异
            res.isRight = true;
            commit(SET_CONFIG_INFO_LIST, res);
            getConfigInfoListTimer = setTimeout(() => {
              dispatch(`getConfigInfoList`);
            }, getConfigInfoListInterval);
            getConfigPromise = null;
            resolve(res);
          })
          .catch(err => {
            getConfigPromise = null;
            reject(err);
          });
      });
      return getConfigPromise;
    },
    // 获取系统的设置信息
    getSystemSettingConfig({ commit, getters }) {
      return new Promise((resolve, reject) => {
        const systemConfigVersion =
          getters[getInfoMapList.systemConfig][`${prefix}-${SYSTEM_CONFIG}`];
        const path = `/${prefix}/${prefix}-${SYSTEM_CONFIG}-${systemConfigVersion}.json`;
        getSystemSettingConfig(path)
          .then(res => {
            // commit(SET_LANG, res['user_default_language'])
            if (
              getters.systemSettingConfig &&
              !getters.systemSettingConfig["user_default_language"] &&
              !getLang()
            ) {
              commit(SET_LANG, res["user_default_language"]);
            }
            commit(SET_SYSTOM_CONFIG_INFO, res);
            resolve(res);
          })
          .catch(err => reject(err));
      });
    },
    // 获取当前的所有彩种列表
    getLotteryMenuList({ commit, getters }) {
      return new Promise((resolve, reject) => {
        const lotteryListVersion =
          getters[getInfoMapList.lotteryMenuList][
            `${getters.merchantName}-${LOTTERY_LIST}-${getters.lang}`
          ];
        getLotteryList(
          `${getters.merchantName}-${LOTTERY_LIST}-${getters.lang}-${lotteryListVersion}.json`,
          getters.merchantName
        )
          .then(res => {
            const nowLotteryList = processLotteryMenuFirst(res);
            commit(SET_LOTTERY_LIST, nowLotteryList);
            commit(SET_SZCP_MENU_LIST, nowLotteryList);
            resolve(nowLotteryList);
          })
          .catch(err => reject(err));
      });
    },
    getOddsMap({ commit, getters }, gameNameInfo) {
      return new Promise((resolve, reject) => {
        if (!gameNameInfo || !gameNameInfo["gameName"]) {
          gameNameInfo = {
            gameName: getters.nowActiveGameName.toLowerCase()
          };
        }
        const oddInfoVersion =
          getters[getInfoMapList.oddInfo][
            `${prefix}-${ODD_INDO}-${gameNameInfo["gameName"].toLowerCase()}`
          ];
        const path = `/${prefix}/${prefix}-${ODD_INDO}-${gameNameInfo["gameName"]}-${oddInfoVersion}.json`;
        getOddsInfo(path)
          .then(res => {
            commit(SET_ODDS_INFO, res);
            resolve(res);
          })
          .catch(err => reject(err));
      });
    },
    // 获取当前彩种的标准玩法列表
    setNowActiveGame({ commit, getters }, getWayInfo) {
      return new Promise((resolve, reject) => {
        if (!getWayInfo || !getWayInfo.wayTypeName) {
          getWayInfo = {
            wayTypeName: getters.nowLotteryInfo.upperName
          };
        }

        const lotteryNormalGroup =
          getters[getInfoMapList.normalGameList][
            `${prefix}-${WAY_GROUP}-${topTypeNameMap[getWayInfo.wayTypeName]}-${
              getters.lang
            }`
          ];
        const path = `/${prefix}/${prefix}-${WAY_GROUP}-${
          topTypeNameMap[getWayInfo.wayTypeName]
        }-${getters.lang}-${lotteryNormalGroup}.json`;
        getWaysInfo(path)
          .then(res => {
            // ----测试数据
            // const nowWayInfoTest = [{"name":"\u6240\u6709","children":[{"name":"\u6240\u6709","children":[[566,"\u4e00\u5b9a\u4f4d"],[567,"\u4e8c\u5b9a\u4f4d"],[568,"\u4e09\u5b9a\u4f4d"],[569,"\u56db\u5b9a\u4f4d"],[570,"\u4e8c\u5b57\u73b0"],[571,"\u4e09\u5b57\u73b0"],[575,"\u56db\u5b57\u73b0"]]}]}]
            // const nowWayInfo = processWayInFirst(nowWayInfoTest)
            const nowWayInfo = processWayInFirst(
              res,
              getters.gameDetail["default_way_id"]
            );
            commit(SET_NOW_ACTIVE_WAYS, nowWayInfo);
            resolve(nowWayInfo);
          })
          .catch(err => reject(err));
      });
    },
    // 获取当前盘口玩法列表
    setNowCreditActiveGame({ commit, getters }, getWayInfo) {
      return new Promise((resolve, reject) => {
        if (!getWayInfo || !getWayInfo["creditWayTypeName"]) {
          getWayInfo = {
            creditWayTypeName: getters.nowLotteryInfo.upperName,
            gameType: getters.nowLotteryInfo[`series_identifier`]
          };
        }
        const creditWaysVersion =
          getters[getInfoMapList.handicapGameList][
            `${prefix}-${CREDIT_WAYS}-${
              topTypeNameMap[getWayInfo["creditWayTypeName"]]
            }-${getters.lang}`
          ];
        const path = `/${prefix}/${prefix}-${CREDIT_WAYS}-${
          topTypeNameMap[getWayInfo.creditWayTypeName]
        }-${getters.lang}-${creditWaysVersion}.json`;

        const creditOddVersion =
          getters[getInfoMapList.creditWayGroups][
            `${prefix}-${CREDIT_ODD}-pc-${
              topTypeNameMap[getWayInfo["creditWayTypeName"]]
            }-${getters.lang}`
          ];
        const creditOddPath = `/${prefix}/${prefix}-${CREDIT_ODD}-pc-${
          topTypeNameMap[getWayInfo.creditWayTypeName]
        }-${getters.lang}-${creditOddVersion}.json`;

        // const creditOddVersionH5 = getters[getInfoMapList.creditWayGroups][`${prefix}-${CREDIT_ODD}-${topTypeNameMap[getWayInfo['creditWayTypeName']]}`]
        // const creditOddPathH5 = `/${prefix}/${prefix}-${CREDIT_ODD}-${topTypeNameMap[getWayInfo.creditWayTypeName]}-${creditOddVersionH5}.json`
        Promise.all([
          getHandicapWaysInfo(path),
          getHandicaOddsInfo(creditOddPath)
        ])
          .then(res => {
            res.push(getWayInfo.gameType);
            const nowHandicapWaysInfo = processCreditWayInFirst.apply(
              this,
              res
            );
            commit(SET_NOW_RANKING_HANDICAP_WAYS, res[1]);
            commit(SET_NOW_ACTIVE_HANDICAP_WAYS, nowHandicapWaysInfo);
            resolve(nowHandicapWaysInfo);
          })
          .catch(err => {
            console.log(err);
            reject(err);
          });
        if (!(getWayInfo && getWayInfo["creditWayTypeName"])) {
          reject(
            new Error(
              "未检测到有效参数getWayInfo, getWayInfo.creditWayTypeName"
            )
          );
        }
      });
    },
    setNowWayInfoDetail({ commit, getters }, detailWayInfo) {
      return new Promise((resolve, reject) => {
        if (!detailWayInfo || !detailWayInfo.detailWayStr) {
          detailWayInfo = {
            detailWayStr: getters.nowLotteryInfo.upperName
          };
        }
        const topTypeListVersion =
          getters[getInfoMapList.normalWayGroups][
            `${prefix}-${TOP_LIST}-${
              topTypeNameMap[detailWayInfo["detailWayStr"]]
            }-${getters.lang}`
          ];
        const path = `/${prefix}/${prefix}-${TOP_LIST}-${
          topTypeNameMap[detailWayInfo["detailWayStr"]]
        }-${getters.lang}-${topTypeListVersion}.json`;
        getUpWayInfo(path)
          .then(res => {
            commit(SET_NOW_WAY_INFO_DETAIL, res);
            resolve(res);
          })
          .catch(err => reject(err));
        if (!(detailWayInfo && detailWayInfo.detailWayStr)) {
          reject(
            new Error("未检测到有效参数getWayInfo, detailWayInfo.detailWayStr")
          );
        }
      });
    },

    getBetSettings({ commit, getters }, params) {
      let queryLotteryId = getters.lotteryId;
      if (params && params.queryLotteryId) {
        queryLotteryId = params.queryLotteryId;
      }
      return new Promise((resolve, reject) => {
        getBetSettings(queryLotteryId)
          .then(res => {
            const { errno, data } = res;
            if (errno === 0) {
              if (!data.username) data.username = sBetSettings.username;
              commit(SET_DEFAULT_SETTING, data);
              resolve(res);
            } else {
              reject(new Error(res.message));
            }
          })
          .catch(err => reject(err));
      });
    },
    getAvailablePrizeGroups({ commit, getters }) {
      return new Promise((resolve, reject) => {
        GetAvailablePrizeGroups(+getters.lotteryId)
          .then(res => {
            const { errno, data } = res;
            if (errno === 0) {
              /* const arr = []
              const { max, min, step } = data
              for (let i = +max; i >= min; i -= step) {
                arr.push(i)
              }
              data.groups = arr
              commit(SET_PRIZE_GROUPS, data)
              resolve(res) */
              data.groups = [res.data.min];
              commit(SET_PRIZE_GROUPS, data);
            } else {
              reject(new Error(res));
            }
          })
          .catch(err => reject(err));
      });
    },
    getLastWnNumberList({ commit, getters, dispatch }, params = {}) {
      const { firstEnter } = params;
      return new Promise((resolve, reject) => {
        const path = `${prefix}-${WN_NUMBER_LIST}-${getters.nowActiveGameName.toLowerCase()}.json`;
        getWnNumberInfoList(path)
          .then(res => {
            let data = [];
            for (let i = 0; i < res.length; i++) {
              data.push(res[i]);
            }
            if (
              !data[0]["wn_number"] ||
              data[0][`issue`] !== getters["sscAwardInfo"].lastIssue
            ) {
              if (getLastWnNumberTimer) clearTimeout(getLastWnNumberTimer);
              getLastWnNumberTimer = setTimeout(() => {
                dispatch("getLastWnNumberList", { firstEnter });
              }, 3000);
              // 封单声音
              commit(SET_STOP_VOICE_END_TWO, true);
            }
            if (
              typeof getters.pk10ShowStatus === "number" &&
              firstEnter &&
              data[0]["wn_number"]
            ) {
              commit(SET_RECENT_WN_NUMBER_LIST, data);
              commit(SET_LAST_WN_NUMBER, data[0]);
              commit(SET_PK10_SHOW_STATUS, 2);
              return resolve(data);
            }
            if (
              typeof getters.pk10ShowStatus === "number" &&
              !firstEnter &&
              !data[0]["wn_number"]
            ) {
              commit(SET_RECENT_WN_NUMBER_LIST, data);
              commit(SET_LAST_WN_NUMBER, data[0]);
              commit(SET_PK10_SHOW_STATUS, 0);
              return resolve(data);
            }
            if (
              typeof getters.pk10ShowStatus === "number" &&
              !firstEnter &&
              data[0]["wn_number"]
            ) {
              commit(SET_PK10_SHOW_STATUS, 1);
              commit(SET_LAST_WN_NUMBER, { issue: data[0].issue });
              setTimeout(() => {
                commit(SET_PK10_SHOW_STATUS, 2);
                commit(SET_LAST_WN_NUMBER, data[0]);
                commit(SET_RECENT_WN_NUMBER_LIST, data);
                if (getters.pk10ShowStatus === 2) {
                  commit(SET_STOP_VOICE_END_TWO, false);
                  dispatch("getBettingRecordList", { isClick: false });
                  // 拿到最新开奖号码派发中奖提示接口
                  dispatch("getUserRecentPrizeList");
                  console.log("設置pk10播音状态为false line 190");
                }
              }, 5000);
              return resolve(data);
            }
            commit(SET_RECENT_WN_NUMBER_LIST, data);
            // dispatch('getBettingRecordList', { isClick: false }) //优化请求次数,改为获取到最新号码后发
            commit(SET_LAST_WN_NUMBER, data[0]);
            if (
              getters.sscLastWnNumber["wn_number"] !== "" &&
              data[0][`issue`] === getters["sscAwardInfo"].lastIssue
            ) {
              // 拿到最新开奖号码派发中奖提示接口
              console.log("拿到最新开奖号码");
              dispatch("getUserRecentPrizeList");
              commit(SET_STOP_VOICE_END_TWO, false); // 判断当获取到最新的开奖号码后开始播报开奖号码语音
              dispatch("getBettingRecordList", { isClick: false });
              dispatch("getLrylNumberList", {
                nowGameName: getters.nowActiveGameName.toLowerCase()
              });
            }
            resolve(data);
          })
          .catch(err => reject(err));
      });
    },
    getOftenPlayTypeList({ commit }) {
      return new Promise((resolve, reject) => {
        getFavoritesLottery().then(res => {
          if (typeof likeGameTimeBegin === "object") {
            for (let key in likeGameTimeBegin) {
              clearInterval(likeGameTimeBegin[key]);
            }
          }
          const { errno } = res;
          if (errno === 0) {
            let data = [];
            for (let key in res.data) {
              data.push(res.data[key]);
            }
            commit(SET_OFTEN_PLAY_TYPES, data);
            commit(SET_LIKE_GAMES_IN_ALL_GAME, data);
            commit(SET_GETTED_OFTEN_PLAY_TYPE_LIST, true);
            resolve(data);
            return;
          }
          reject(new Error(res));
        });
      });
    },
    getNowGameDetail({ commit, getters }, params) {
      let { lotteryId, nowGameName } = params || {};
      if (!nowGameName) {
        nowGameName = getters.gameDetail.identifier;
      }
      nowGameName = nowGameName.toLowerCase();
      if (!lotteryId) lotteryId = getters.lotteryId;
      return new Promise((resolve, reject) => {
        if (getters.allGameInfo.isRight) {
          resolve(getters.allGameInfo.data[lotteryId]);
        } else {
          const lotteryInfoVersion =
            getters[getInfoMapList.lotteryInfo][
              `${prefix}-${LOTTERY_INFO}-${nowGameName}-${getters.lang}`
            ];
          console.log(
            `${prefix}-${LOTTERY_INFO}-${nowGameName}-${getters.lang}`,
            "lotteryInfoVersionKey"
          );
          const path = `/${prefix}/${prefix}-${LOTTERY_INFO}-${nowGameName}-${getters.lang}-${lotteryInfoVersion}.json`;
          getGameDetail(path)
            .then(res => {
              commit(SET_GAME_DETAIL_INFO, res);
              resolve(res);
              /* if (res.errno === 0) {
              } else {
                reject(new Error(res['message']))
              } */
            })
            .catch(err => reject(err));
        }
      });
    },
    closeOftenGameList({ commit }) {
      if (typeof likeGameTimeBegin === "object") {
        for (let key in likeGameTimeBegin) {
          clearInterval(likeGameTimeBegin[key]);
        }
      }
      commit(SET_OFTEN_PLAY_TYPES, []);
      commit(SET_GETTED_OFTEN_PLAY_TYPE_LIST, false);
    },
    resetImGame({ commit }) {
      return new Promise(resolve => {
        // isImShowBox 当前是否显示投注盒模型
        commit(SET_IM_LOTTERY_BOX_SHOW, false);

        // imLastShowHeight 当前子玩法对应的投注盘的高度
        commit(SET_IM_SHOW_BOX_HEIGHT, "");

        // imNowGameTypeName 当前子玩法的名称
        commit(SET_IM_NOW_GAME_TYPE, "");

        // imNowOpenCounts 当前的开奖次数
        commit(SET_IM_NOW_OPEN_COUNTS, 0);

        // lotteryNumberLastImInfo 当前开奖信息与之前开奖信息的变化指向字段
        commit(SET_IM_LOTTERY_NUMBER, "");
        setTimeout(() => {
          resolve();
        }, 0);
      });
    },

    getLotteryInfoById({ getters }, lotteryInfo) {
      return new Promise(resolve => {
        const lotteryId = lotteryInfo.lotteryId;
        const lotteryMenuList = getters.lotteryMenuList;
        // let flag = false
        let upperName, nowGameName, menuLink;
        lotteryMenuList.find(item => {
          return item.lotteries.find(itemInner => {
            if (+itemInner["lottery_id"] === +lotteryId) {
              upperName = itemInner["series_identifier"];
              nowGameName = itemInner.identifier.toLowerCase();
              menuLink = itemInner;
              return true;
            }
          });
        });
        /* for (let key in lotteryMenuList) {
          if (flag) break
          for (let keyInner in lotteryMenuList[key].lotteries) {
            if (+keyInner === +lotteryId) {
              upperName = lotteryMenuList[key]['identifier']
              nowGameName = lotteryMenuList[key].lotteries[keyInner].identifier.toLowerCase()
              menuLink = lotteryMenuList[key].lotteries[keyInner]
              flag = true
              break
            }
          }
        } */
        resolve({
          lotteryId,
          upperName,
          nowGameName,
          menuLink
        });
      });
    },
    selectLottery({ commit, dispatch, getters }, selectInfo) {
      return new Promise(resolve => {
        const { lotteryId, upperName, nowGameName, menuLink } = selectInfo;
        const oldLotteryId = getters.lotteryId;
        // 在此处获取当前的彩种详情信息
        Promise.all([
          dispatch("getNowGameDetail", { lotteryId, nowGameName }),
          dispatch("getLrylNumberList", {
            nowGameName: nowGameName.toLowerCase()
          })
        ])
          .then(() => {
            const {
              status,
              is_open_normal: isOpenNormal,
              is_open_handicap: isOpenHandicap
            } = getters.gameDetail;
            dispatch("clearCurrentIssue");
            if (status === 3 || status === 1) {
              Vm &&
                Vm.$nextTick(() => {
                  commit(SET_LAST_WN_NUMBER, {
                    issue: "loading...",
                    wn_number: ""
                  });
                });
              if (isOpenNormal === 1 && isOpenHandicap === 1) {
                return Promise.all([
                  // 获取当前玩法的顶级菜单列表
                  // 获取当前玩法的二级玩法列表
                  dispatch("setNowActiveGame", { wayTypeName: upperName }),
                  dispatch("setNowWayInfoDetail", { detailWayStr: upperName }),
                  // 获取盘口玩法菜单列表
                  dispatch("setNowCreditActiveGame", {
                    creditWayTypeName: upperName,
                    gameType: menuLink[`series_identifier`]
                  }),
                  // 获取盘口玩法的赔率列表
                  dispatch("getOddsMap", { gameName: nowGameName })
                  // 获取当前玩法的默认设置
                  // dispatch('getBetSettings', { queryLotteryId: lotteryId })
                ]);
              } else if (isOpenNormal === 1 && isOpenHandicap !== 1) {
                return Promise.all([
                  dispatch("setNowActiveGame", { wayTypeName: upperName }),
                  dispatch("setNowWayInfoDetail", { detailWayStr: upperName }),
                  dispatch("getOddsMap", { gameName: nowGameName })
                  // dispatch('setNowCreditActiveGame', { creditWayTypeName: upperName, gameType: menuLink[`series_identifier`] }),
                  // dispatch('getBetSettings', { queryLotteryId: lotteryId })
                ]);
              } else if (isOpenNormal !== 1 && isOpenHandicap === 1) {
                return Promise.all([
                  // 获取盘口玩法菜单列表
                  dispatch("setNowCreditActiveGame", {
                    creditWayTypeName: upperName,
                    gameType: menuLink[`series_identifier`]
                  }),
                  // 获取盘口玩法的赔率列表
                  dispatch("getOddsMap", { gameName: nowGameName })
                  // dispatch('getBetSettings', { queryLotteryId: lotteryId })
                ]);
              } else {
                commit(SET_GETTING_DATA_STATUS, false);
                return Promise.reject(
                  new Error(Vm.$t("errorMessage.errorMessage11"))
                );
              }
            } else {
              commit(SET_GETTING_DATA_STATUS, false);
              return Promise.reject(new Error(`试玩账号无法访问该游戏`));
            }
          })
          .catch(err => {
            dispatch("getLotteryInfoById", { lotteryId: oldLotteryId }).then(
              res => {
                dispatch("selectLottery", res);
                Message({
                  message: err.message,
                  type: "error"
                });
                return Promise.reject(new Error(err));
              }
            );
            return Promise.reject(err);
          })
          .then(() => {
            // 修改当前的彩系名称
            commit(SET_NOW_GAME_TYPE, upperName);
            // 修改状态管理中彩种id的值
            commit(SET_LOTTERY_ID, lotteryId);
            // 保存状态管理中 当前彩种的信息
            commit(SET_NOW_ACTIVE_GAME, nowGameName.toUpperCase());

            menuLink.upperName = upperName;
            // 保存当前选择彩种的相关信息
            commit(SET_NOW_LOTTERY_INFO, menuLink);
            const {
              is_open_normal: isOpenNormal,
              is_open_handicap: isOpenHandicap
            } = getters.gameDetail;
            const typeArr = [];
            const nowActiveWays = getters.nowActiveWays;
            const nowActiveCreditWays = getters.nowActiveHandicapWays;
            const creditMenuInfo =
              isOpenHandicap === 1 &&
              nowActiveCreditWays &&
              nowActiveCreditWays.length;
            const menuInfo =
              isOpenNormal === 1 && nowActiveWays && nowActiveWays.length;
            /* if ((!menuInfo || menuInfo.errno !== 0) && (!creditMenuInfo || creditMenuInfo.errno !== 0)) {
              return Promise.reject(new Error('该彩种已关闭'))
            } */
            // playModeNormal, playModeHandicap
            menuInfo &&
              typeArr.push({
                label: "gameInfo.standard",
                value: playModeNormal,
                toName: `${upperName}Normal`,
                icon: "icon-16"
              });
            creditMenuInfo &&
              typeArr.push({
                label: "gameInfo.handicap",
                value: playModeHandicap,
                toName: `${upperName}Handicap`,
                icon: "icon-33"
              });
            commit(SET_PLAY_TYPE_LIST, typeArr);
            // 如果要进入的即开型彩种
            if (getters.gameDetail["is_instant"]) {
              Vm &&
                Vm.$nextTick(() => {
                  commit(SET_LAST_WN_NUMBER, {
                    issue: "loading...",
                    wn_number: ""
                  });
                });
              return Promise.resolve("isIm");
            }
            Vm &&
              Vm.$nextTick(() => {
                commit(SET_LAST_WN_NUMBER, {
                  issue: "loading...",
                  wn_number: ""
                });
                if (typeArr.length) {
                  commit(SET_NOW_BET_MODE, typeArr[0].value);
                  commit(SET_HIGHEST_PRIZE_LEVEL, ``);
                } else {
                  return Promise.reject(
                    new Error(Vm.$t("errorMessage.errorMessage9"))
                  );
                }
                Vm && Vm.$loadingHide();
              });
          })
          .then(res => {
            if (res) {
              return resolve(res);
            }
            Promise.all([
              dispatch("getCurrentIssue", { lotteryId: lotteryId }),
              dispatch("getLastWnNumberList", { firstEnter: true })
              // dispatch('getLrylNumberList', { nowGameName: nowGameName.toLowerCase() })
            ]);
          })
          .then(res => {
            resolve(res);
          });
      });
    }
  }
};
