<template>
  <transition name="fade-in-linear">
    <div v-if="isShow" class="wrapper" @click.self="handleWrapperClick">
      <div class="container" ref="container">
        <a class="close" @click="closeAlertBox" href="javascript:;">
          <i class="el-icon-close"></i>
        </a>
        <div class="title change_bg_color" v-if="!isSpecimen">
          {{ $t("fundAlertComponents.checkBankCard") }}
        </div>
        <div class="modal-body" :class="{ 'padding14-5': !isSpecimen }">
          <el-form ref="form" :model="form" label-width="100px">
            <el-form-item
              :label="$t('fundAlertComponents.selectBoundBankCard')"
            >
              <el-select
                v-model="form.bank"
                @change="selectedBankCardChanged"
                :placeholder="$t('fundAlertComponents.selectBoundBankCard')"
              >
                <el-option
                  v-for="(item, index) in bindingBankCard"
                  :key="index"
                  :label="`${item.accountName}(${item.bankCardNumber})`"
                  :value="item.cardId"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item :label="$t('personalCenter.accountName')">
              <el-input
                v-model="form.accountName"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item :label="$t('personalCenter.bankCardNumber')">
              <el-input v-model="form.cardNumber" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item :label="$t('personalCenter.fundPassword')">
              <el-input
                type="password"
                v-model="form.fundPwd"
                autocomplete="off"
              ></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div class="modal-footer">
          <el-button
            size="mini"
            class="submit"
            :loading="isSubmiting"
            @click="submitBetList"
            >{{ $t("alertComponent.confirm") }}</el-button
          >
          <el-button size="mini" class="cancel" @click="closeAlertBox">{{
            $t("alertComponent.cancel")
          }}</el-button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { alertMixin } from "../../game-alert/mixin";
import { SET_MARQUEE_INTERVAL_FLAG } from "../../../store/nameSpace";
import { isOpenAnimation } from "../../../config/game-default-config";
import { mapGetters, mapActions } from "vuex";

import { checkBankCard } from "../../../api/userCenter/personalCenter/personal-center";

export default {
  name: "check-bank-card-alert",
  mixins: [alertMixin],
  data() {
    return {
      form: {
        bank: "",
        accountName: "",
        cardNumber: "",
        fundPwd: ""
      },
      type: 1, //校验类型,1:绑卡2:修改卡3:删除卡
      isSpecimen: false,
      isSubmiting: false
    };
  },
  computed: {
    ...mapGetters(["bindingBankCard"])
  },
  methods: {
    submitBetList() {
      checkBankCard({
        cardId: this.form.bank,
        fundPwd: this.form.fundPwd,
        accountName: this.form.accountName,
        account: this.form.cardNumber,
        type: this.type
      }).then(data => {
        if (data.errno === 0) {
          this.show = false;
          this.$eventHub.$emit("check-bank-card-success", {
            checkedToken: data.data.checked_token,
            fundPwd: this.form.fundPwd,
            cardId: this.form.bank
          });
        } else {
          this.$message({
            type: "error",
            message: data.message
          });
        }
      });
    },
    selectedBankCardChanged() {
      const card = this.bindingBankCard.filter(
        item => item.cardId === this.form.bank
      );
      this.form.accountName = card[0].accountName || "";
    }
  },
  watch: {
    show(val) {
      if (val) {
        this.$store.commit(SET_MARQUEE_INTERVAL_FLAG, false);
        this.isShow = val;
        if (isOpenAnimation) {
          setTimeout(() => {
            this.$refs.container.style.transition = "all 0.5s";
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        } else {
          setTimeout(() => {
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        }
      } else {
        this.$store.commit(SET_MARQUEE_INTERVAL_FLAG, true);
        if (isOpenAnimation) {
          this.$refs.container.style.top = "40%";
          this.$refs.container.style.opacity = 0;
          setTimeout(() => {
            this.isShow = val;
          }, 500);
        } else {
          this.isShow = val;
        }
      }
    }
  },
  created() {}
};
</script>

