<template>
  <transition name="fade-in-linear">
    <div
      v-if="show"
      class="wrapper"
      ref="wrapper"
      @click.self="handleWrapperClick"
    >
      <div
        class="container width"
        ref="container"
        :style="{ height: containerHeight }"
      >
        <a class="close" @click="closeAlertBox">
          <i class="el-icon-close"></i>
        </a>
        <ul class="title change_bg_color">
          <li
            :class="{ text_color: alertType === 'gameRecord' }"
            @click="clickList('gameRecord')"
          >
            {{ $t("lotteryRecord.gameRecord") }}
          </li>
          <li
            :class="{ text_color: alertType === 'trackingRecord' }"
            @click="clickList('trackingRecord')"
          >
            {{ $t("lotteryRecord.TrackingRecord") }}
          </li>
          <li
            :class="{ text_color: alertType === 'accountChange' }"
            @click="clickList('accountChange')"
          >
            {{ $t("lotteryRecord.accountChanges") }}
          </li>
          <li
            :class="{ text_color: alertType === 'accountProfit' }"
            @click="clickList('accountProfit')"
          >
            {{ $t("lotteryRecord.accountProfit") }}
          </li>
          <li
            :class="{ text_color: alertType === 'lotteryRecord' }"
            v-if="isShowLotteryRecord"
            @click="clickList('lotteryRecord')"
          >
            {{ $t("lotteryRecord.lotteryRecord") }}
          </li>
          <li
            :class="{ text_color: alertType === 'trendChart' }"
            v-if="isShowTrendChart"
            @click="clickList('trendChart')"
          >
            {{ $t("lotteryRecord.trendChart") }}
          </li>
          <li
            v-if="nowGameType === 'SSC'"
            :class="{ text_color: alertType === 'dragonTigerSum' }"
            @click="clickList('dragonTigerSum')"
          >
            {{ $t("lotteryRecord.dragonTigerSum") }}
          </li>
          <!--<li :class="{ text_color: alertType === 'recharge'}" @click="clickList('recharge')">{{$t('top.recharge')}}</li>-->
          <!--<li :class="{ text_color: alertType === 'withdraw'}" @click="clickList('withdraw')">{{$t('top.withdraw')}}</li>-->
        </ul>
        <template v-if="alertType === 'gameRecord'">
          <!--<div class="title change_bg_color">游戏记录</div>-->
          <div class="content">
            <gameRecord :containerHeight="containerHeight"></gameRecord>
          </div>
        </template>
        <template v-else-if="alertType === 'trackingRecord'">
          <!--<div class="title change_bg_color">追号记录</div>-->
          <div class="content">
            <chaseRecord :containerHeight="containerHeight"></chaseRecord>
          </div>
        </template>
        <template v-else-if="alertType === 'accountChange'">
          <!--<div class="title change_bg_color">账变明细</div>-->
          <div class="content">
            <accountChange :containerHeight="containerHeight"></accountChange>
          </div>
        </template>
        <template v-else-if="alertType === 'accountProfit'">
          <!--<div class="title change_bg_color">个人报表</div>-->
          <div class="content">
            <accountProfit :containerHeight="containerHeight"></accountProfit>
          </div>
        </template>
        <template v-else-if="alertType === 'lotteryRecord'">
          <!--<div class="title change_bg_color">开奖记录</div>-->
          <div class="content">
            <lotteryRecord :containerHeight="containerHeight"></lotteryRecord>
          </div>
        </template>
        <template v-else-if="alertType === 'trendChart' && isShowTrendChart">
          <!--<div class="title change_bg_color">走势图</div>-->
          <div class="content">
            <trendChart :containerHeight="containerHeight"></trendChart>
          </div>
        </template>
        <template v-else-if="alertType === 'recharge'">
          <div class="content">
            <recharge :containerHeight="containerHeight"></recharge>
          </div>
        </template>
        <template v-else-if="alertType === 'withdraw'">
          <div class="content">
            <withdraw :containerHeight="containerHeight"></withdraw>
          </div>
        </template>
        <template v-else-if="alertType === 'dragonTigerSum'">
          <!--<div class="title change_bg_color">龙虎和</div>-->
          <div class="content">
            <lhChart :containerHeight="containerHeight"></lhChart>
          </div>
        </template>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from "vuex";
import { alertMixin } from "../mixin";
import {
  isOpenAnimation,
  isShowTrendChart,
  isShowLotteryRecord
} from "../../../config/game-default-config";
import gameRecord from "../../record/gameRecord";
import chaseRecord from "../../record/chaseRecord";
import accountChange from "../../record/accountChange";
import accountProfit from "../../record/accountProfit";
import lotteryRecord from "../../record/lotteryRecord";
import trendChart from "../../record/trendChart";
import recharge from "../../record/recharge";
import withdraw from "../../record/withdraw";
import lhChart from "../../record/lhChart";

export default {
  name: "alert-record",
  mixins: [alertMixin],
  components: {
    gameRecord,
    chaseRecord,
    accountChange,
    accountProfit,
    lotteryRecord,
    trendChart,
    lhChart,
    withdraw,
    recharge
  },
  data() {
    return {
      isShowLotteryRecord,
      isShowTrendChart,
      alertType: "gameRecord",
      wrapperHeight: "",
      containerHeight: ""
    };
  },
  computed: {
    ...mapGetters(["nowGameType"])
  },
  watch: {
    show(val) {
      if (val) {
        this.isShow = val;
        if (isOpenAnimation) {
          setTimeout(() => {
            this.$refs.container.style.transition = "all 0.5s";
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        } else {
          setTimeout(() => {
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        }
      } else {
        if (isOpenAnimation) {
          this.$refs.container.style.top = "40%";
          this.$refs.container.style.opacity = 0;
          setTimeout(() => {
            this.isShow = val;
          }, 500);
        } else {
          this.isShow = val;
        }
      }
    }
  },
  methods: {
    clickList(str) {
      this.alertType = str;
    },
    getHeight() {
      setTimeout(() => {
        this.wrapperHeight = this.$refs["wrapper"].clientHeight;
        if (this.wrapperHeight < 800) {
          this.containerHeight = this.wrapperHeight * 0.9;
          let height = Math.round(this.containerHeight);
          if (height % 2 === 0) {
            this.containerHeight = height + "px";
          } else {
            this.containerHeight = height + 1 + "px";
          }
        }
      }, 100);
    }
  },
  mounted() {
    this.getHeight();
  },
  created() {
    console.log(this.nowGameType);
  }
};
</script>

<style scoped>
@import "../statics/css/transition.css";
.container {
  height: 700px;
}
.container .title li {
  padding: 0 20px;
  height: 40px;
  line-height: 40px;
  margin-top: 16px;
  float: left;
  text-align: center;
  cursor: pointer;
}
.container .title .text_color {
  background: #fff;
}
.content {
  overflow-y: auto;
}
</style>
