import Vue from "vue";
import App from "./App.vue";
import router from "./router";
//import ElementUI from "element-ui";
import axios from "./common/js/axios.js";
import store from "./store/index.js";
import Promise from "promise-polyfill";
import promise from "es6-promise";
import i18n from "./lang";
import * as filters from "./filters";
import gameAlert from "./components/index";
import { show, hide } from "./util/loading";
import { handleUnhandledException } from "./common/js/util";
import {
  Carousel,
  CarouselItem,
  Slider,
  InputNumber,
  Dropdown,
  DropdownMenu,
  DropdownItem,
  Select,
  Option,
  DatePicker,
  Badge,
  Message,
  MessageBox,
  Form,
  FormItem,
  Input,
  Switch,
  Tooltip,
  Button,
  Table,
  TableColumn,
  Radio,
  RadioGroup,
  Cascader,
  Pagination,
  Checkbox,
  RadioButton,
  Autocomplete,
  Loading,
  Dialog,
  Container,
  Header,
  Aside,
  Main,
  Menu,
  Submenu,
  MenuItem,
  Notification,
  Col,
  Row,
  Image,
  Card,
  Link,
  Avatar,
  Rate,
  Divider,
  Timeline,
  TimelineItem
} from "element-ui";

Vue.use(Container);
Vue.use(MenuItem);
Vue.use(Submenu);
Vue.use(Menu);
Vue.use(Main);
Vue.use(Aside);
Vue.use(Header);
Vue.use(Carousel);
