<template>
  <div>
    <div class="account-gamerecord centerBox">
      <div class="condition">
        <div class="selects">
          <!--TODO:第三方游戏记录无彩种选项-->
          <div class="label">{{ $t("gameRecord.lotteryTypes") }}:</div>
          <el-select
            v-model="gameValue"
            :placeholder="$t('gameRecord.isSelected')"
            @change="changeGame"
          >
            <el-option
              v-for="(gameItem, index) in allGames"
              :key="index"
              :label="gameItem.label"
              :value="gameItem.id"
            >
            </el-option>
          </el-select>
        </div>
        <DateTimePicker
          :begin_date.sync="begin_date"
          :end_date.sync="end_date"
        ></DateTimePicker>
        <el-button class="change_btn_bg" @click="getRecord(1)">{{
          $t("gameRecord.query")
        }}</el-button>
      </div>
      <div class="content">
        <UserTable
          class="table1"
          :tablehade="tableHade"
          :userActive="userActive"
          style="overflow-y: auto;"
          :style="{ height: userTableHeight }"
        >
          <li v-for="(item, index) in tableMsg" :key="index">
            <div>
              <p>{{ item.serial_number }}</p>
            </div>
            <!-- 注单编号-->
            <!--<div><p></p></div> &lt;!&ndash; 种类&ndash;&gt;-->
            <div>
              <p>
                {{
                  item.is_traced === 0
                    ? `${$t("popup.no")}`
                    : `${$t("popup.yes")}`
                }}
              </p>
            </div>
            <!-- 是否追号-->
            <div>
              <p>{{ item.lottery_name }}</p>
              <p>{{ item.way }}</p>
            </div>
            <!-- 彩种玩法-->
            <div>
              <p>{{ item.issue }}</p>
            </div>
            <!-- 期号-->
            <div style="width: 160px;">
              <p>{{ item.bought_at }}</p>
            </div>
            <!-- 投注时间-->
            <div>
              <p
                class="actions text_color"
                @click="detailsNotes(item.id, item.series)"
              >
                {{ $t("gameRecord.bettingDetails") }}
              </p>
            </div>
            <!-- 投注内容-->
            <div>
              <p>{{ item.multiple }}</p>
            </div>
            <!-- 倍数-->
            <div>
              <p>{{ item.prize_group }}</p>
            </div>
            <!--奖金组-->
            <div>
              <p>{{ item.single_count }}</p>
            </div>
            <!-- 注数-->
            <div>
              <p>{{ item.amount }}</p>
            </div>
            <!-- 投注金额-->
            <div>
              <!-- 中奖金额-->
              <p v-if="item.prize !== '--'">{{ item.prize }}</p>
              <p v-if="item.prize === '--'">0</p>
            </div>
            <div>
              <!-- 注单状态-->
              <p :class="{ red: item.status === 3 }">
                {{ item.formatted_status }}
              </p>
            </div>
            <div>
              <p
                v-if="item.can_drop !== 0"
                class="actions"
                @click="cancelOrder(item.id)"
              >
                {{ $t("gameRecord.cancelTheOrder") }}
              </p>
            </div>
            <!-- 操作-->
          </li>
          <li v-show="tableMsg.length !== 0" style="background: #d8eaf6">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div style="width: 160px;"></div>
            <div></div>
            <div></div>
            <div></div>
            <div>
              <p :title="$t('gameRecord.subtotal')">
                {{ $t("gameRecord.subtotal") }}
              </p>
            </div>
            <div>
              <p>{{ getTotalNum(tableMsg, "amount", 4) }}</p>
            </div>
            <div>
              <p>{{ getTotalNum(tableMsg, "prize", 4) }}</p>
            </div>
            <div></div>
            <div></div>
          </li>
          <li v-show="tableMsg.length !== 0" style="background: #dddddd">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div style="width: 160px;"></div>
            <div></div>
            <div></div>
            <div></div>
            <div>
              <p :title="$t('gameRecord.total')">
                {{ $t("gameRecord.total") }}
              </p>
            </div>
            <div>
              <p>{{ total_turnover }}</p>
            </div>
            <div>
              <p>{{ total_prize }}</p>
            </div>
            <div></div>
            <div></div>
          </li>
          <li v-show="tableMsg.length === 0">
            <span class="nomsg">{{ $t("gameRecord.noData") }}</span>
          </li>
        </UserTable>
        <Pagination :pageDate="pageDate" @jump="jump"></Pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getProjectList } from "@/api/game/game";
import { Message } from "element-ui";
import DateTimePicker from "./component/date-time-picker";
import UserTable from "./component/user-table";
import Pagination from "./component/pagination";
import {
  getAllGames,
  getProjectDetail,
  dropProject
} from "../../api/userCenter/personalCenter/personal-center";
import { getTotalNum } from "../../common/js/operation";
import {
  closeOnClickModal,
  closeOnPressEscape
} from "../../config/game-default-config";

export default {
  name: "account-gamerecord",
  data() {
    return {
      begin_date: "", // 开始时间
      end_date: "", // 结束时间
      gameTypes: [], // 游戏种类
      gameType: 1,
      allGames: [{ value: "", label: this.$t("gameRecord.all") }], // 彩种
      gameValue: "",
      tableHade: [
        { title: this.$t("gameRecord.number") },
        { title: this.$t("gameRecord.isTracking") },
        { title: this.$t("gameRecord.lotteryWay") },
        { title: this.$t("gameRecord.issueNumber") },
        { title: this.$t("gameRecord.bettingTime") },
        { title: this.$t("gameRecord.bettingContent") },
        { title: this.$t("gameRecord.multiple") },
        { title: this.$t("gameRecord.bonusGroup") },
        { title: this.$t("gameRecord.injectionNumber") },
        { title: this.$t("gameRecord.bettingAmount") },
        { title: this.$t("gameRecord.winningAmount") },
        { title: this.$t("gameRecord.noteStatus") },
        { title: this.$t("gameRecord.operation") }
      ],
      userActive: 4, // 宽度较宽的表头
      tableMsg: [], // 游戏列表数据
      pageDate: {
        // 分页参数
        page: 1, // 当前页
        pageSize: 11, // 页尺寸
        pageCount: 0, // 总页数
        totalCount: 0 // 总数量
      },
      notesDetails: {}, // 注单详情
      userData: {}, // 账户信息
      canDrop: "", // 是否可以撤单
      noteId: "", // 注单ID
      errMsg: "",
      total_turnover: "0.0000", // 总投注
      total_prize: "0.0000", // 总中奖
      userTableHeight: "508px"
    };
  },
  props: ["containerHeight"],
  components: {
    DateTimePicker,
    UserTable,
    Pagination
  },
  mounted() {
    if (this.containerHeight) {
      this.userTableHeight = parseFloat(this.containerHeight) - 180 + "px";
      let height = parseFloat(this.containerHeight) - 260;
      this.pageDate.pageSize = Math.floor(height / 40);
    }
    this.begin_date = this.getTime(0) + " 00:00:00";
    this.end_date = this.getTime(0) + " 23:59:59";
    this.getGamelist1();
    this.getRecord(1);
  },
  created() {
    let user = this.$store.state.user;
    this.userData = user;
  },
  methods: {
    changeGameType() {
      this.tableMsg = [];
      this.gameValue = "";
      this.pageDate.pageCount = 1;
    },
    changeGame(value) {
      this.gameValue = value;
    },
    handleClose() {
      this.dialogVisible = false;
    },
    getTime(e) {
      return (function(e) {
        const date = new Date();
        date.setTime(date.getTime() - 3600 * 1000 * 24 * e);
        return date.Format("yyyy-MM-dd");
      })(e);
    },
    // 获取游戏记录数据
    getRecord(page) {
      this.$loadingShow();
      getProjectList(
        this.gameValue,
        page,
        this.pageDate.pageSize,
        "",
        this.begin_date,
        this.end_date
      ).then(res => {
        this.$loadingHide();
        if (res.errno === 0) {
          let tableMsg = [];
          for (let key in res.data.data) {
            tableMsg.push(res.data.data[key]);
          }
          this.$set(this, "tableMsg", tableMsg);
          this.total_turnover = res.data.total_turnover;
          this.total_prize = res.data.total_prize;
          this.pageDate.page = res.data.page;
          this.pageDate.pageSize = res.data.pagesize;
          this.pageDate.totalCount = res.data.total_count;
          if (res.data.page_count === 0) {
            this.pageDate.pageCount = 1;
          } else {
            this.pageDate.pageCount = res.data.page_count;
          }
        }
      });
    },
    // 获取注单详情
    detailsNotes(e, type) {
      getProjectDetail(e).then(res => {
        if (res.errno === 0) {
          this.$gameAlertZdxq({
            dataList: res.data,
            titleName: this.$t("gameRecord.noteDetails"),
            gameMenuType: type
          });
        }
      });
    },
    // 撤单
    cancelOrder(e) {
      this.rootEl =
        this.rootEl ||
        document.querySelector("body > div:nth-of-type(1)") ||
        document.getElementsByTagName("div")[0];
      this.rootEl.className += " blur";
      this.$message_box
        .confirm(this.$t("popup.isCancelTheOrder"), this.$t("popup.title1"), {
          confirmButtonText: this.$t("popup.confirm"),
          cancelButtonText: this.$t("popup.cancel"),
          cancelButtonClass: "el-btn-close",
          closeOnClickModal: closeOnClickModal,
          closeOnPressEscape: closeOnPressEscape
        })
        .then(() => {
          this.$loadingShow();
          getProjectDetail(e).then(res => {
            if (res.errno === 0) {
              this.noteId = res.data.id;
              dropProject(this.noteId).then(res => {
                this.$loadingHide();
                if (res.errno === 0) {
                  Message({
                    message: this.$t("successMessage.successMessage2"),
                    type: "success"
                  });
                  this.getRecord(1);
                }
              });
            }
          });
          this.rootEl.className = this.rootEl.className.replace("blur", "");
        })
        .catch(() => {
          this.rootEl.className = this.rootEl.className.replace("blur", "");
        });
    },
    // 获取所有彩种数据
    getGamelist1() {
      getAllGames().then(res => {
        if (res.errno === 0) {
          for (let key in res.data) {
            if (res.data[key].status === 3) {
              this.allGames.push({
                value: res.data[key].game_type,
                label: res.data[key].name,
                id: res.data[key].id
              });
            }
          }
        }
      });
    },
    // 点击跳转页数
    jump(pageNum) {
      this.getRecord(pageNum);
    },
    // 本页小结
    getTotalNum(data, str, num) {
      return getTotalNum(data, str, num);
    }
  }
};
</script>

<style scoped>
@import "../../statics/css/userCenterCommon.css";
.condition .table1.tableHade2 li div {
  width: 16.5%;
}
.account-gamerecord {
  /*height: 600px;*/
}
.dialog-body {
  overflow: hidden;
}
.dialog-body .titles {
  font-size: 19px;
  font-weight: 500;
  color: #333;
  height: 40px;
  border-bottom: 1px solid #eee;
}
.dialog-body .left {
  width: 340px;
  padding: 0 20px;
  float: left;
}
.dialog-body .left .text {
  padding: 20px 0;
  color: #777;
  line-height: 30px;
  font-weight: 300;
}
.dialog-body .left .text div {
  overflow: hidden;
}
.dialog-body .left .text div p {
  float: left;
}
.dialog-body .left .text div p:nth-child(1) {
  width: 80px;
  text-align: right;
}
.dialog-body .right {
  width: 220px;
  padding: 0 20px;
  float: left;
}
.dialog-body .right .text {
  width: 100%;
  height: 360px;
  margin-top: 20px;
  border: 1px solid #ddd;
  color: #777;
  text-align: center;
  line-height: 30px;
}
</style>
