<template>
  <div>
    <div class="account-change centerBox">
      <div class="condition">
        <div class="selects">
          <div class="label">{{ $t("alertComponent.changeType") }}</div>
          <el-select
            v-model="changeValue"
            :placeholder="$t('alertComponent.isSelected')"
          >
            <el-option
              v-for="item in changeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <DateTimePicker
          :begin_date.sync="begin_date"
          :end_date.sync="end_date"
        ></DateTimePicker>
        <el-button class="change_btn_bg" @click="query(1)">{{
          $t("gameRecord.query")
        }}</el-button>
      </div>
      <div class="content">
        <UserTable
          class="table1"
          :tablehade="tableHade"
          :userActive="userActive"
          style="overflow-y: auto;"
          :style="{ height: userTableHeight }"
        >
          <li v-for="(item, index) in tableMsg" :key="index">
            <div>
              <p>{{ item.serial_number }}</p>
            </div>
            <div>
              <p>{{ item.username }}</p>
            </div>
            <div>
              <p
                class="actions text_color"
                @click="getProjectDetail(item.lottery_id, item.project_id)"
              >
                {{ $t("alertComponent.noteDetails") }}
              </p>
            </div>
            <div>
              <p>{{ item.description }}</p>
            </div>
            <div style="width: 160px">
              <p>{{ item.created_at }}</p>
            </div>
            <div>
              <p class="green">{{ item.add_amount }}</p>
            </div>
            <div>
              <p class="red">{{ item.deduct_amount }}</p>
            </div>
          </li>
          <li v-show="tableMsg.length !== 0" style="background: #d8eaf6">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div style="width: 160px">
              <p>{{ $t("alertComponent.subtotal") }}</p>
            </div>
            <div>
              <p class="green">{{ getTotalNum(tableMsg, "add_amount", 3) }}</p>
            </div>
            <div>
              <p class="red">{{ getTotalNum(tableMsg, "deduct_amount", 3) }}</p>
            </div>
          </li>
          <!--<li v-show="tableMsg.length !== 0" style="background: #dddddd">-->
          <!--<div></div>-->
          <!--<div></div>-->
          <!--<div><p>{{$t('alertComponent.total')}}</p></div>-->
          <!--<div><p class="green">{{totalMoney1 | numFilter}}</p></div>-->
          <!--<div><p class="red">{{totalMoney2 | numFilter}}</p></div>-->
          <!--<div></div>-->
          <!--<div></div>-->
          <!--</li>-->
          <li v-show="tableMsg.length === 0">
            <span class="nomsg">{{ $t("alertComponent.noData") }}</span>
          </li>
        </UserTable>
        <Pagination :pageDate="pageDate" @jump="jump"></Pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import DateTimePicker from "./component/date-time-picker";
import UserTable from "./component/user-table";
import Pagination from "./component/pagination";
import {
  getTransactionTypeList,
  getTransactionList,
  getProjectDetail
} from "../../api/userCenter/personalCenter/personal-center";
import { getTotalNum } from "../../common/js/operation";
export default {
  name: "account-change",
  data() {
    return {
      begin_date: "", // 开始时间
      end_date: "", // 结束时间
      gameTypes: [{ value: "", label: this.$t("alertComponent.all") }], // 产品类型
      gameType: "",
      changeList: [], // 账变类型
      changeValue: "",
      tableHade: [
        { title: this.$t("alertComponent.changeNumber") },
        { title: this.$t("alertComponent.UserName") },
        { title: this.$t("alertComponent.bettingContent") },
        { title: this.$t("alertComponent.changeType") },
        { title: this.$t("alertComponent.changeTime") },
        { title: this.$t("alertComponent.AmountOfIncrease") },
        { title: this.$t("alertComponent.Deduction") }
      ],
      userActive: 4,
      tableMsg: [],
      pageDate: {
        // 分页参数
        page: 1, // 当前页
        pageSize: 14, // 页尺寸
        pageCount: 0, // 总页数
        totalCount: 0 // 总数量
      },
      userTableHeight: "516px"
    };
  },
  props: ["containerHeight"],
  components: {
    DateTimePicker,
    UserTable,
    Pagination
  },
  computed: {
    ...mapGetters(["lotteryMenuList"])
  },
  created() {
    this.$loadingShow();
    this.getTransactionTypeList();
    this.$loadingHide();
  },
  mounted() {
    this.begin_date = this.getTime(0) + " 00:00:00";
    this.end_date = this.getTime(0) + " 23:59:59";
    this.getTransactionList(1);
    if (this.containerHeight) {
      this.userTableHeight = parseFloat(this.containerHeight) - 180 + "px";
      let height = parseFloat(this.containerHeight) - 270;
      this.pageDate.pageSize = Math.floor(height / 30);
    }
  },
  methods: {
    getTime(e) {
      return (function(e) {
        const date = new Date();
        date.setTime(date.getTime() - 3600 * 1000 * 24 * e);
        return date.Format("yyyy-MM-dd");
      })(e);
    },
    /* 获取追号彩种名 */
    getGameInfoById(szcpList, id) {
      let flag = false;
      let typeName = "";
      if (!szcpList || !id) return null;
      for (let i = 0; i < szcpList.length; i++) {
        if (flag) break;
        for (let j = 0; j < szcpList[i]["lotteries"].length; j++) {
          if (+szcpList[i]["lotteries"][j]["lottery_id"] === +id) {
            // typeName = key
            typeName = szcpList[i]["lotteries"][j]["series_identifier"];
            flag = true;
            break;
          }
        }
      }
      if (!flag) return null;
      return typeName;
    },
    getProjectDetail(lotteryId, projectId) {
      getProjectDetail(projectId)
        .then(res => {
          let { data, errno } = res;
          if (errno === 0) {
            this.$gameAlertZdxq({
              dataList: data,
              titleName: this.$t("alertComponent.noteDetails"),
              gameMenuType: this.getGameInfoById(
                this.lotteryMenuList,
                lotteryId
              )
            });
          }
        })
        .catch(() => {});
    },
    // 获取账变类型
    getTransactionTypeList() {
      getTransactionTypeList().then(res => {
        if (res.errno === 0) {
          let changeList = [
            { value: "", label: this.$t("alertComponent.all") }
          ];
          for (let key in res.data) {
            changeList.push({
              value: res.data[key].id,
              label: res.data[key].description
            });
          }
          this.$set(this, "changeList", changeList);
        }
      });
    },
    // 获取账变明细列表
    getTransactionList(page) {
      this.$loadingShow();
      let data = {
        typeId: this.changeValue,
        beginTime: this.begin_date,
        endTime: this.end_date,
        page: page,
        pageSize: this.pageDate.pageSize,
        searchTeam: 0,
        userName: ""
      };
      getTransactionList(data).then(res => {
        if (res.errno === 0) {
          let tableMsg = [];
          for (let key in res.data.data) {
            tableMsg.push(res.data.data[key]);
          }
          this.$set(this, "tableMsg", tableMsg);
          this.pageDate.page = res.data.page;
          this.pageDate.pageSize = res.data.pagesize;
          this.pageDate.totalCount = res.data.total_count;
          if (res.data.page_count === 0) {
            this.pageDate.pageCount = 1;
          } else {
            this.pageDate.pageCount = res.data.page_count;
          }
          this.$loadingHide();
        }
      });
    },
    // 点击查询
    query() {
      this.getTransactionList(1);
    },
    // 点击跳转页数
    jump(pageNum) {
      this.getTransactionList(pageNum);
    },
    // 本页小结
    getTotalNum(data, str, num) {
      return getTotalNum(data, str, num);
    }
  }
};
</script>

<style scoped>
@import "../../statics/css/userCenterCommon.css";
.account-change .content .user-table .table-body li {
  padding: 6px 0;
}
</style>
