<template>
  <div class="toolbar">
    <div class="toolbar-container">
      <div class="tool-left">
        <el-menu class="tool-bar" mode="horizontal" @select="handleSelect">
          <el-menu-item index="1" disabled>
            <router-link :to="{ name: 'Home' }">
              <el-image :src="logoUrl" fit="scale-down" class="logo">
                <div slot="error" class="image-slot"></div>
              </el-image>
            </router-link>
          </el-menu-item>
          <el-menu-item index="7">
            {{ $t("top.home") }}
          </el-menu-item>
          <el-submenu index="2">
            <template slot="title">{{ $t("top.playOnline") }}</template>
            <el-menu-item
              :index="2 + '-' + (index + 1)"
              v-for="(item, index) in allGame"
              :key="item.name"
              :class="+item.hot === 1 ? 'menu-hot' : ''"
              >{{ item.name }}</el-menu-item
            >
          </el-submenu>
          <el-menu-item index="3">{{ $t("top.result") }}</el-menu-item>
          <!-- <el-submenu index="4">
            <template slot="title">{{ $t("top.lotteryInfo") }}</template>
            <el-menu-item
              :index="4 + '-' + (index + 1)"
              v-for="(item, index) in allGame"
              :key="item.name"
              >{{ item.name }}</el-menu-item
            >
          </el-submenu> -->
          <el-submenu index="5">
            <template slot="title">{{ $t("top.changeLang") }}</template>
            <el-menu-item
              :index="5 + '-' + (index + 1)"
              v-for="(item, index) in lang"
              :key="item.key"
              >{{ item.value }}</el-menu-item
            >
          </el-submenu>
          <el-submenu index="6">
            <template slot="title">{{ $t("top.theme") }}</template>
            <el-menu-item
              :index="6 + '-' + (index + 1)"
              v-for="(item, index) in themes"
              :key="item.key"
              >{{ item.value }}</el-menu-item
            >
          </el-submenu>
        </el-menu>
      </div>
      <div class="tool-right">
        <el-row v-if="!isLogin" type="flex" class="toolbar-right">
          <el-col :span="24">
            <el-button type="success" round @click="goSignIn">{{
              $t("top.signIn")
            }}</el-button>
            <el-button type="warning" round>{{ $t("top.signUp") }}</el-button>
          </el-col>
        </el-row>
        <el-row v-else type="flex" class="toolbar-right" :gutter="10">
          <el-col :span="6">
            <el-dropdown
              class="user-nick"
              :hide-on-click="false"
              @command="selectUserInfo"
            >
              <span class="el-dropdown-link warning">
                {{ userInfo.nickname }}
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="myAccount">{{
                  $t("top.myAccount")
                }}</el-dropdown-item>
                <!-- <el-dropdown-item command="myOrder">{{
                  $t("top.myOrder")
                }}</el-dropdown-item> -->
                <el-dropdown-item command="myBet">{{
                  $t("top.myBet")
                }}</el-dropdown-item>
                <el-dropdown-item command="myTask">{{
                  $t("top.myTask")
                }}</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
          <el-col :span="6">
            <div class="balance">
              <strong>{{ $t("top.balance") }}:</strong
              ><strong class="warning" style="max-width:80px;overflow:hidden">{{
                currentBalance
              }}</strong
              ><i
                :class="
                  'el-icon-refresh el-icon--right hand ' +
                    (currentBalance === '...' || loading
                      ? 'circle-loading'
                      : '')
                "
                @click="refreshBalance"
              ></i>
            </div>
          </el-col>
          <el-col :span="12">
            <div>
              <el-button
                type="success"
                class="el-icon-money"
                circle
                :title="$t('top.recharge')"
                @click="goDeposit()"
              ></el-button>
              <el-button
                type="warning"
                class="el-icon-s-cooperation"
                circle
                :title="$t('top.withdraw')"
                @click="goWithdraw()"
              ></el-button>
              <el-button
                type="primary"
                class="el-icon-position"
                circle
                :title="$t('top.signOut')"
                @click="signOutSystem()"
              ></el-button>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import router from "@/router/index";
import restoreState from "../common/js/utils";
import { SET_LANG } from "../store/nameSpace";
import { getToken } from "../util/auth";
import { SET_LOGIN_STATUS } from "../store/nameSpace";
import auxiliary from "../common/Auxiliary/auxiliary/index";
import baseUrl from "../config/config";

export default {
  name: "header-bar",
  data() {
    return {
      loading: false
    };
  },
  computed: {
    ...mapGetters({
      menu: "lotteryMenuList"
    }),
    ...mapGetters([
      "isLogin",
      "userInfo",
      "nowBetMode",
      "lotteryId",
      "systemSettingConfig"
    ]),
    allGame: function() {
      if (this.menu.length > 0) {
        let array = [];
        this.menu.slice(1).forEach(item =>
          item.lotteries.forEach(inner => {
            array.push(inner);
          })
        );
        array.sort((i1, i2) => {
          return i2.hot - i1.hot;
        });
        return array;
      } else {
        return [];
      }
    },
    lang: function() {
      let langs = [];
      for (let prop in this.systemSettingConfig.user_support_languages) {
        langs.push({
          key: prop,
          value: this.systemSettingConfig.user_support_languages[prop]
        });
      }
      return langs;
    },
    logoUrl() {
      if (this.systemSettingConfig.logo)
        return `${baseUrl}${this.systemSettingConfig.logo}`;
      else return undefined;
    },
    themes: function() {
      return [
        { key: "red", value: this.$t("theme.red") },
        { key: "gol", value: this.$t("theme.orange") },
        { key: "vio", value: this.$t("theme.blue") },
        { key: "gre", value: this.$t("theme.violet") }
      ];
    },
    currentBalance() {
      return this.userInfo.abalance || "...";
    }
  },
  methods: {
    ...mapActions([
      "loginOut",
      "getLotteryMenuList",
      "selectLottery",
      "updateUserInfo",
      "getNowGameDetail"
    ]),
    handleSelect: function(index) {
      console.log(index);
      var array = index.split("-");
      if (array[0] === "5") {
        const lang = this.lang[parseInt(array[1]) - 1];
        this.changeLanguage(lang);
      } else if (array[0] === "6") {
        const theme = this.themes[parseInt(array[1]) - 1];
        this.changeTheme(theme);
      } else if (array[0] === "3") {
        router.push({
          name: "GameRecord",
          params: { lotteryId: this.lotteryId }
        });
      } else if (array[0] === "2") {
        const lottery = this.allGame[array[1] - 1];
        if (this.isLogin) {
          router.push({
            name: "Game",
            params: { lotteryId: lottery.lottery_id }
          });
        } else {
          this.$message_box(
            this.$t("top.warnginContent"),
            this.$t("top.warnging"),
            {
              confirmButtonText: this.$t("alertComponent.confirm")
            }
          );
        }
      } else if (array[0] === "7") {
        router.push({
          name: "Home"
        });
      }
    },
    changeLanguage: function(lang) {
      this.$store.commit(SET_LANG, lang.key);
      this.$parent.reload();
    },
    changeTheme: function(theme) {
      document.getElementsByTagName("body")[0].className = "theme-" + theme.key;
      localStorage.setItem(
        "theme",
        document.getElementsByTagName("body")[0].className
      );
    },
    selectUserInfo: function(command) {
      let path = "";
      switch (command) {
        case "myAccount":
          path = "/user/account";
          break;
        case "myOrder":
          path = "/user/order";
          break;
        case "myBet":
          path = "/user/bets";
          break;
        case "myTask":
          path = "/user/tasks";
          break;
        default:
          path = "/user/account";
          break;
      }
      router.push({ path: path });
    },
    goSignIn: function() {
      router.push({ name: "SignIn" });
    },
    goDeposit() {
      router.push({ path: "/financial/deposits" });
    },
    goWithdraw() {
      router.push({ path: "/financial/withdrawals" });
    },
    refreshBalance: function() {
      this.loading = true;
      this.updateUserInfo()
        .then(res => {
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    },
    signOutSystem: function() {
      this.$store.commit(SET_LOGIN_STATUS, false);
    }
  },
  created() {
    //恢复会话
    restoreState(this);

    let vm = this;
    let exitToHome = auxiliary.throttle(() => {
      vm.$message_box(vm.$t("top.warnginContent"), vm.$t("top.warnging"), {
        confirmButtonText: vm.$t("alertComponent.confirm")
      }).then(action => {
        router.push({ name: "Home" });
      });
    }, 3000);
    window.reload = () => {
      console.log("监听到异常退出");
      this.$loadingHide();
      if (this.isLogin) {
        this.$store.commit(SET_LOGIN_STATUS, false);
      } else {
        if (router.history.current.name === "Home") return;

        exitToHome();
      }
    };
  },
  watch: {
    isLogin(newVal, oldVal) {
      if (!newVal) {
        this.loginOut();
        router.push({ name: "Home" });
      }
    }
  }
};
</script>

<style>
@keyframes slidein {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.circle-loading {
  animation: slidein 0.5s linear infinite;
}
</style>

<style lang="sass">
@import "../common/css/color.sass"

#app
  .layout
    .toolbar
      background-color: var(--primary)
      position: fixed
      top: 0
      left: 0
      right: 0
      height: 60px
      color: #000
      font-size: 14px
      z-index: 100
      .toolbar-container
        background-color: transparent
        width: 1200px
        margin: 0 auto
        height: 100%
        display: flex
        flex-direction: row
        justify-content: space-between
        .tool-left
          display: flex
          position: relative
          .el-image__placeholder
            background-color: transparent
          .image-slot
            background-color: transparent
          .el-menu
            background-color: transparent
            &.el-menu--horizontal
              >.el-menu-item
                color: #fff
                &:not(.is-disabled)
                  &:hover,&:focus
                    color: #333
              >.el-submenu
                .el-submenu__title
                  color: #fff
                  i
                    color: #fff
                  &:hover,&:focus
                    background-color: #fff
                    color: #333
                    i
                      color: #333


        .tool-right
          height: 100%
          font-weight: 700
          .toolbar-right
            height: 100%
            >div
              height: 100%
              display: flex
              align-items: center
              width: auto
            .el-dropdown
              color: #fff
            .user-nick
              height: 100%
              display: flex
              align-items: center
            .balance
              height: 100%
              display: flex
              align-items: center
              color: #FFD22B
              >strong:nth-child(1)
                color: #fff
              i
                color: #fff
      .logo
          display: flex
          align-items: center
          height: 60px
          width: 200px
      .el-menu
        border-bottom: 0
        >li
          font-weight: 700
        .el-menu-item.is-disabled
          opacity: 100
          cursor: pointer
.el-menu--horizontal
  .el-menu
    .el-menu-item
      &.menu-hot
        color: #aa0000
        &:hover
          color: #ff0000
        &.is-active
          color: #ff0000


          LOTTERY_LIST
</style>
