/* eslint-disable */

import { Message, Loading } from 'element-ui'
import Vm from '../../main'
import router from '../../router'
import {
  SET_BETTING_RECORD_COUNT,
  SET_RECORD_DATA_LIST,
  SET_RANKING_DATA_SHOW,
  SET_QUICK_SELECT_AMOUNT,
  SET_HANDICAP_CHOICE_WAY,
  SET_SELECT_DEFAULT_DATA,
  SET_PLAY_TYPE_LIST,
  SET_LAST_WN_NUMBER,
  SET_PK10_SHOW_STATUS,
  SET_GETTING_DATA_STATUS,
  SET_HANDICAP_CHOICE_BONUS,
  SET_CUSTOMER_SERVICE_INFO,
  SET_DEVICE,
  SET_IS_FIRST_ENTER,
  SET_SCREEN_STATUS,
  SET_YL_DATAS,
  SET_LR_DATAS,
  SET_SZCP_MENU_LIST,
  SET_IS_GET_IM_GAME,
  SET_LOTTERY_ID,
  SET_MARQUEE_INTERVAL_FLAG,
  GET_AN_DATA_IN_RANK_FLAG,
  SET_SHOW_OWN_BONUS_IN_COMPONENT,
  WIN_TIP_FLAG,
  SET_LANG,
  SET_OPEN_PROMPT_VOICE
} from '../nameSpace'
// getLotteryAnData
import {
  getProjectList,
  getTraceList,
  GetGameMenu,
  getUserRecentPrizeList
} from '@/api/game/game'
import { getLotteryAnData } from '@/api/json/json'
import { GetClientServices } from '@/api/basic/basic'
import {
  bettingRecordTablePageSize,
  loaddingOptions,
  isOpenPromptVoice,
  winTipFlag
} from '../../config/game-default-config'
// import lrYlMapConfigInfo from '../../config/lrYl-map-config'
import { saveSelectAmountData } from '../../util/user'
import { getRouteNameById } from '../../common/js/util'
import { upperRouterNameMap } from '../../config/route-name-map'
import {
  saveNowHandicapChoiceWay,
  getNowHandicapChoiceWay
} from '../../util/auth'
import { getLang, saveLang } from '../../util/lang'
import i18n from '../../lang'
import { sBetSettings } from '../../util/require'
import { winVoice } from '../../config/audio-config'
import { prefix, OMMISSIONS } from '../../config/configVersionConfig'
import { setLrylDataFace } from '../../../src/server-config/index'

let isGettingCustomerServiceInfo = false
export default {
  state: {
    lang: getLang() || sBetSettings.language,
    nowShowTab: 0,
    recordTableDataList: [],
    rankingDataShow: 0,
    handicapQuickAmount: 2,
    handicapChoiceWay: getNowHandicapChoiceWay() || 0,
    quickSelectAmountData: null,
    isGettingData: false,
    handicapChoiceBonus: 0,
    customerServiceInfo: null,
    // 当前的屏幕状态尺寸控制变量
    // 0 - > 最大尺寸 即大于 1200
    // 1 - > 二号尺寸 即大于 992 且 小于 1200
    // 2 - > 三号尺寸 即大于 767 且 小于 992
    // 3 - > 四号尺寸 即小于 767
    nowScreenStatus: 0,
    device: 'PC',
    isGetDataInImGame: false,
    isFirstEnter: true,
    marQueeIntervalFlag: true,
    getAnDataInRankFlag: 0,

    // 在组件内部自定义的预计奖金的管理对象
    showOwnBonusInComponent: {
      isRightData: false,
      expectedProfitCount: null
    },
    winTipFlag: winTipFlag,
    openPromptVoice: isOpenPromptVoice
  },
  mutations: {
    [SET_SHOW_OWN_BONUS_IN_COMPONENT](state, showOwnBonusInComponent) {
      state.showOwnBonusInComponent = showOwnBonusInComponent
    },
    [GET_AN_DATA_IN_RANK_FLAG](state) {
      state.getAnDataInRankFlag++
    },
    [SET_IS_GET_IM_GAME](state, isGetDataInImGame) {
      state.isGetDataInImGame = isGetDataInImGame
    },
    [SET_BETTING_RECORD_COUNT](state, nowShowTab) {
      state.nowShowTab = nowShowTab
    },
    [SET_RECORD_DATA_LIST](state, recordTableDataList) {
      state.recordTableDataList = recordTableDataList
    },
    [SET_RANKING_DATA_SHOW](state, rankingDataShow) {
      state.rankingDataShow = rankingDataShow
    },
    [SET_QUICK_SELECT_AMOUNT](state, handicapQuickAmount) {
      state.handicapQuickAmount = handicapQuickAmount
    },
    [SET_HANDICAP_CHOICE_WAY](state, handicapChoiceWay) {
      state.handicapChoiceWay = handicapChoiceWay
      saveNowHandicapChoiceWay(handicapChoiceWay)
    },
    [SET_SELECT_DEFAULT_DATA](state, quickSelectAmountData) {
      saveSelectAmountData(quickSelectAmountData)
      state.quickSelectAmountData = quickSelectAmountData
    },
    [SET_GETTING_DATA_STATUS](state, isGettingData) {
      state.isGettingData = isGettingData
    },
    [SET_HANDICAP_CHOICE_BONUS](state, handicapChoiceBonus) {
      state.handicapChoiceBonus = handicapChoiceBonus
    },
    [SET_CUSTOMER_SERVICE_INFO](state, customerServiceInfo) {
      state.customerServiceInfo = customerServiceInfo
    },
    [SET_DEVICE](state, deviceInfo) {
      state.device = deviceInfo
    },
    [SET_IS_FIRST_ENTER](state, isFirstEnter) {
      state.isFirstEnter = isFirstEnter
    },
    [SET_SCREEN_STATUS](state, nowScreenStatus) {
      state.nowScreenStatus = nowScreenStatus
    },
    [SET_MARQUEE_INTERVAL_FLAG](state, marQueeIntervalFlag) {
      state.marQueeIntervalFlag = marQueeIntervalFlag
    },
    [WIN_TIP_FLAG](state, winTipFlag) {
      state.winTipFlag = winTipFlag
    },
    [SET_LANG](state, lang) {
      state.lang = lang
      saveLang(lang)
      i18n.locale = lang
    },
    [SET_OPEN_PROMPT_VOICE](state, openPromptVoice) {
      state.openPromptVoice = openPromptVoice
    }
  },
  actions: {
    updateOwnBonusInComponent({ commit }, showOwnBonusInComponent) {
      commit(SET_SHOW_OWN_BONUS_IN_COMPONENT, showOwnBonusInComponent)
    },
    isUpdateSZCPList({ getters, dispatch }) {
      return new Promise((resolve, reject) => {
        if (!getters.szcpMenuList) {
          dispatch('getSZCPGameList')
            .then(res => {
              resolve(res)
            })
            .catch(err => reject(err))
        } else {
          resolve()
        }
      })
    },
    getSZCPGameList({ commit }) {
      return new Promise((resolve, reject) => {
        GetGameMenu()
          .then(res => {
            const { data, errno } = res
            if (errno === 0) {
              for (let i = 0; i < data.length; i++) {
                for (let j = 0; j < data[i]['lotteries'].length; j++) {
                  data[i]['lotteries'][j].isLike = false
                }
              }
              commit(SET_SZCP_MENU_LIST, res.data)
              resolve(res)
            } else {
              reject(new Error(Vm.$t('errorMessage.errorMessage10')))
            }
          })
          .catch(err => reject(err))
      })
    },
    getCustomerServiceInfo({ commit }) {
      if (isGettingCustomerServiceInfo) return
      isGettingCustomerServiceInfo = true
      //  设置当前的客服列表
      GetClientServices()
        .then(res => {
          if (res.errno === 0) {
            commit(SET_CUSTOMER_SERVICE_INFO, res.data)
          }
          isGettingCustomerServiceInfo = false
        })
        .catch(err => {
          console.log(err)
          isGettingCustomerServiceInfo = false
        })
    },
    playTypeGame({ commit, dispatch, getters }, paramsObj) {
      console.log("开始加载投注页面...");
      const lotteryBeforeId = getters.lotteryId || false
      // 取消进入游戏的登录状态拦截
      /*if (!getters.isLogin && !getToken()) {
        Vm && Vm.$loadingHide()
        router.push('/index')
        return Message({ message: '请先登录!', type: 'error' })
      }*/
      Vm && Vm.$loadingShow()
      if (paramsObj && paramsObj.isJump) {
        commit(SET_GETTING_DATA_STATUS, true)
        const { lotteryId } = paramsObj
        let menuLink
        let upperName
        let flag
        dispatch('isUpdateSZCPList')
          .then(() => {
            console.log("更新彩票列表完成");
            for (let i = 0; i < getters.szcpMenuList.length; i++) {
              if (flag) break
              for (
                let j = 0;
                j < getters.szcpMenuList[i]['lotteries'].length;
                j++
              ) {
                if (flag) break
                if (
                  +lotteryId ===
                  +getters.szcpMenuList[i]['lotteries'][j]['lottery_id']
                ) {
                  menuLink = getters.szcpMenuList[i]['lotteries'][j]
                  upperName =
                    upperRouterNameMap[
                      getters.szcpMenuList[i]['lotteries'][j][
                        'series_identifier'
                      ]
                    ]
                  flag = true
                }
              }
            }
            if (upperName === 'PK10') commit(SET_PK10_SHOW_STATUS, 0)
            return dispatch('getNowGameDetail', lotteryId)
          })
          .then(() => {
            console.log("获取正在玩的游戏详情");
            const {
              status,
              is_open_normal: isOpenNormal,
              is_open_handicap: isOpenHandicap
            } = getters.gameDetail
            dispatch('clearCurrentIssue')
            const { is_tester: isTester } = getters.userInfo
            if (status === 3 || (status === 1 && isTester)) {
              Vm &&
                Vm.$nextTick(() => {
                  commit(SET_LAST_WN_NUMBER, {
                    issue: 'loading...',
                    wn_number: ''
                  })
                })
              if (isOpenNormal === 1 && isOpenHandicap === 1) {
                return Promise.all([
                  dispatch('getWaySettings', lotteryId),
                  // 获取信用玩法的菜单列表
                  dispatch('getCreditWaySettings', lotteryId)
                  // 获取当前玩法的默认设置
                  // dispatch('getBetSettings', { queryLotteryId: lotteryId })
                ])
              } else if (isOpenNormal === 1 && isOpenHandicap !== 1) {
                return Promise.all([
                  dispatch('getWaySettings', lotteryId)
                  // dispatch('getBetSettings', { queryLotteryId: lotteryId })
                ])
              } else if (isOpenNormal !== 1 && isOpenHandicap === 1) {
                return Promise.all([
                  dispatch('getCreditWaySettings', lotteryId)
                  // dispatch('getBetSettings', { queryLotteryId: lotteryId })
                ])
              } else {
                commit(SET_GETTING_DATA_STATUS, false)
                return Promise.reject(
                  new Error(Vm.$t('errorMessage.errorMessage11'))
                )
              }
            } else {
              commit(SET_GETTING_DATA_STATUS, false)
              return Promise.reject(new Error(`试玩账号无法访问该游戏`))
            }
          })
          .then(menus => {
            console.log("获取玩法类型【官方|盘口】完成");
            console.log(menus);
            const {
              is_open_normal: isOpenNormal,
              is_open_handicap: isOpenHandicap
            } = getters.gameDetail
            const typeArr = []
            const creditMenuInfo =
              isOpenHandicap === 0
                ? isOpenNormal === 0
                  ? menus[1]
                  : menus[0]
                : null
            const menuInfo = isOpenNormal === 0 ? menus[0] : null
            /* if (menuInfo.errno !== 0 && creditMenuInfo.errno !== 0) {
              Message({
                message: '所选游戏已关闭',
                type: 'error'
              })
              return Promise.reject(new Error())
            } */
            // 取消双必需判定 如果后台未返回官方玩法菜单则判定当前彩种为非可玩彩种
            if (
              (!menuInfo || menuInfo.errno !== 0) &&
              (!creditMenuInfo || creditMenuInfo.errno !== 0)
            ) {
              return Promise.reject(
                new Error(Vm.$t('errorMessage.errorMessage'))
              )
            }
            menuInfo &&
              typeArr.push({
                label: this.$t('gameInfo.standard'),
                value: 'normal',
                toName: `${upperName}Normal`,
                icon: 'icon-16'
              })
            creditMenuInfo &&
              typeArr.push({
                label: this.$t('gameInfo.handicap'),
                value: 'handicap',
                toName: `${upperName}Handicap`,
                icon: 'icon-33'
              })
            commit(SET_PLAY_TYPE_LIST, typeArr)
            if (getters.gameDetail['is_instant']) {
              Vm &&
                Vm.$nextTick(() => {
                  commit(SET_LAST_WN_NUMBER, {
                    issue: 'loading...',
                    wn_number: ''
                  })
                })
              if (isOpenNormal === 0) {
                commit(SET_GETTING_DATA_STATUS, false)
                // dispatch('clearCurrentIssue')
                router.push({
                  name: upperName + '-IM',
                  params: { gameTypeName: menuLink.identifier }
                })
                return Promise.resolve(Vm.$t('errorMessage.errorMessage12'))
              } else {
                commit(SET_GETTING_DATA_STATUS, false)
                return Promise.reject(
                  new Error(Vm.$t('errorMessage.errorMessage13'))
                )
              }
            }
            Vm &&
              Vm.$nextTick(() => {
                commit(SET_LAST_WN_NUMBER, {
                  issue: 'loading...',
                  wn_number: ''
                })
                if (typeArr.length) {
                  if (menuInfo) {
                    const defaultWayId =
                      getters.defaultSetting['default_way_id']
                    const routeName = getRouteNameById(
                      defaultWayId,
                      getters.lotteryMenuList,
                      upperName
                    )
                    if (routeName) {
                      commit(SET_IS_FIRST_ENTER, false)
                      router.push({
                        name: routeName,
                        params: {
                          gameTypeName: paramsObj.menuLink
                            ? paramsObj.menuLink.identifier
                            : menuLink.identifier
                        }
                      })
                    } else {
                      commit(SET_IS_FIRST_ENTER, false)
                      router.push({
                        name: typeArr[0].toName,
                        params: {
                          gameTypeName: paramsObj.menuLink
                            ? paramsObj.menuLink.identifier
                            : menuLink.identifier
                        }
                      })
                    }
                    commit(SET_GETTING_DATA_STATUS, false)
                    return Promise.resolve()
                  } else {
                    commit(SET_LOTTERY_ID, lotteryId)
                    router.push({
                      name: typeArr[0].toName,
                      params: {
                        gameTypeName: paramsObj.menuLink
                          ? paramsObj.menuLink.identifier
                          : menuLink.identifier
                      }
                    })
                    return Promise.resolve()
                  }
                } else {
                  return Promise.reject(
                    new Error(Vm.$t('errorMessage.errorMessage9'))
                  )
                }
              })
          })
          .catch(err => {
            console.error("进入游戏失败");
            Message({
              message: err.message,
              type: 'error'
            })
            commit(SET_GETTING_DATA_STATUS, false)
            console.warn(`当前彩种不可玩，尝试跳转到之前彩种${lotteryBeforeId}`);
            if (lotteryBeforeId && +lotteryBeforeId !== +lotteryId) {
              return dispatch('playTypeGame', {
                lotteryId: lotteryBeforeId,
                isJump: true
              })
            } else {
              router.push('/')
              Vm && Vm.$loadingHide()
            }
            return Promise.reject(err)
          })
          .then(isIm => {
            // 清空冷热遗漏的相关数据
            commit(SET_LR_DATAS, [])
            commit(SET_YL_DATAS, [])
            if (isIm) {
              commit(SET_IS_GET_IM_GAME, true)
              return Promise.all([dispatch('getAvailablePrizeGroups')])
            }
            commit(GET_AN_DATA_IN_RANK_FLAG)
            return Promise.all([
              dispatch('getCurrentIssue'),
              dispatch('getAvailablePrizeGroups'),
              dispatch('getLastWnNumberList', { firstEnter: true }),
              dispatch('getLrylNumberList', { id: getters.lotteryId, type: 1 }),
              dispatch('getLrylNumberList', { id: getters.lotteryId, type: 2 })
            ])
          })
          .then(() => {
            commit(SET_GETTING_DATA_STATUS, false)
            Vm && Vm.$loadingHide()
          })
          .catch(err => {
            console.log(err)
            Vm && Vm.$loadingHide()
          })
      } else {
        Promise.all([
          dispatch('getCurrentIssue'),
          dispatch('getLastWnNumberList', { firstEnter: true }),
          dispatch('getAvailablePrizeGroups'),
          dispatch('getNowGameDetail', getters.lotteryId)
        ]).then(() => {
          Vm && Vm.$loadingHide()
        })
      }
    },
    getBettingRecordList(
      { commit, getters, dispatch },
      { isClick, isOpenType }
    ) {
      // commit(SET_RECORD_DATA_LIST, [])
      loaddingOptions.background = 'rgba(255,255,255,0.8)'
      switch (getters.bettingRecordShow) {
        case 0:
          dispatch('getNoteRecordList', { isClick, isOpenType })
          break
        case 1:
          dispatch('getChasingNumberList', { isClick })
          break
        case 2:
          dispatch('getUnopenedRecordList', { isClick })
          break
      }
    },
    getNoteRecordList({ commit, getters }, { isClick, isOpenType }) {
      loaddingOptions.target = document.querySelector('.record-table')
      if (isClick) var loadingInstance = Loading.service(loaddingOptions)
      return new Promise((resolve, reject) => {
        let lotteryId
        if (isOpenType === true) {
          lotteryId = getters.lotteryId
        } else {
          lotteryId = undefined
        }
        getProjectList(lotteryId, 1, bettingRecordTablePageSize.pageSize)
          .then(res => {
            if (isClick) loadingInstance.close()
            if (res.errno === 0) {
              let dataList = []
              for (let i in res.data.data) {
                dataList.push(res.data.data[i])
              }
              res.data.data = dataList
              commit(SET_RECORD_DATA_LIST, res.data)
              resolve(res)
            } else {
              reject(new Error())
            }
          })
          .catch(err => reject(err))
      })
    },
    getUnopenedRecordList({ commit }, { isClick }) {
      loaddingOptions.target = document.querySelector('.record-table')
      if (isClick) var loadingInstance = Loading.service(loaddingOptions)
      return new Promise((resolve, reject) => {
        getProjectList(undefined, 1, bettingRecordTablePageSize.pageSize, 0)
          .then(res => {
            if (isClick) loadingInstance.close()
            if (res.errno === 0) {
              let dataList = []
              for (let i in res.data.data) {
                dataList.push(res.data.data[i])
              }
              res.data.data = dataList
              commit(SET_RECORD_DATA_LIST, res.data)
              resolve(res)
            } else {
              reject(new Error())
            }
          })
          .catch(err => reject(err))
      })
    },
    getChasingNumberList({ commit }, { isClick }) {
      loaddingOptions.target = document.querySelector('.record-table')
      if (isClick) var loadingInstance = Loading.service(loaddingOptions)
      return new Promise((resolve, reject) => {
        getTraceList(undefined, 1, bettingRecordTablePageSize.pageSize)
          .then(res => {
            if (isClick) loadingInstance.close()
            if (res.errno === 0) {
              let dataList = []
              for (let i in res.data.data) {
                dataList.push(res.data.data[i])
              }
              res.data.data = dataList
              commit(SET_RECORD_DATA_LIST, res.data)
              resolve(res)
            } else {
              reject(new Error())
            }
          })
          .catch(err => reject(err))
      })
    },
    getLrylNumberList({ commit, dispatch, getters }, params) {
      // const nowGameTypeName = getters.gameDetail['group_name']
      // const nowGameTypeName = upperRouterNameMap[getters.gameDetail['group_name']]
      // let lrData = []
      // let ylData = []
      const { nowGameName } = params
      const { need_ommissions: hasMissHot } = getters.gameDetail
      if (+hasMissHot === 0) return
      if (
        getters.nowGameType === 'LHC' ||
        getters.nowGameType === 'K3' ||
        getters.nowGameType === 'KENO' ||
        getters.nowGameType === 'KLSF' ||
        getters.nowGameType === 'PK10'
      ) {
        return
      }
      return new Promise((resolve, reject) => {
        // dispatch('getGameTypeByLotteryId')
        //   .then(() => {
        // if (getters.nowGameType === 'PK10' || getters.nowGameType === 'LHC' || getters.nowGameType === 'KENO' || getters.nowGameType === 'KLSF' || getters.nowGameType === 'K3' || getters.nowGameType === 'KL12') {
        //   return resolve()
        // }
        // getLotteryAnData(id, type)
        const path = `${prefix}-${OMMISSIONS}-${nowGameName}.json`
        getLotteryAnData(path)
          .then(res => {
            const lrArry = setLrylDataFace(res.times, getters.nowGameType)
            const ylArry = setLrylDataFace(
              res['omissions'],
              getters.nowGameType
            )
            if (
              (lrArry === null || lrArry === []) &&
              (lrArry === null || ylArry === [])
            ) {
              return resolve()
            }
            /* var map = {}
            var dest = []
            console.log(lrylData)
            for (var i = 0; i < lrylData.length; i++) {
              var ai = lrylData[i]
              console.log(ai)
              if (!map[ai.position]) {
                dest.push({
                  position: ai.position,
                  data: [ai]
                })
                map[ai.position] = ai
              } else {
                for (var j = 0; j < dest.length; j++) {
                  var dj = dest[j]
                  if (dj.position === ai.position) {
                    dj.data.push(ai)
                    break
                  }
                }
              }
            }
            dest.map(item => {
              if (!lrYlMapConfigInfo[getters.nowGameType]) return
              lrYlMapConfigInfo[getters.nowGameType].map(itemInner => {
                if (itemInner.position === item.position) {
                  item.type = itemInner.type
                  item.value = itemInner.value
                  item.data = item.data.sort((a, b) => a.ball - b.ball)
                }
              })
            })
            if (type === 1) {
              lrData = dest
              commit(SET_LR_DATAS, lrData)
            } else if (type === 2) {
              ylData = dest
              commit(SET_YL_DATAS, ylData)
            } */
            commit(SET_LR_DATAS, lrArry)
            commit(SET_YL_DATAS, ylArry)
            resolve(res)
          })
          .catch(err => reject(err))
        // })
      })
    },
    getGameTypeByLotteryId({ commit, getters, dispatch }) {
      return new Promise((resolve, reject) => {
        dispatch('isUpdateSZCPList')
          .then(() => {
            getters.szcpMenuList.map(item => {
              for (let j = 0; j < item['lotteries'].length; j++) {
                if (
                  +item['lotteries'][j]['lottery_id'] === +getters.lotteryId
                ) {
                  resolve(item['lotteries'][j]['series_identifier'])
                  break
                }
              }
            })
            reject(new Error(Vm.$t('errorMessage.errorMessage10')))
          })
          .catch(err => reject(err))
      })
    },
    getUserRecentPrizeList({ commit, getters, dispatch }) {
      return new Promise((resolve, reject) => {
        // console.log('中奖提示开关', getters.winTipFlag)
        if (!getters.winTipFlag) return false
        let winList = []
        setTimeout(() => {
          getUserRecentPrizeList()
            .then(res => {
              if (res.errno === 0) {
                if (Object.keys(res.data).length === 0) return
                for (let i in res.data) {
                  winList.push(res.data[i])
                }
                // winList = res.data
                let winAudio = document.querySelector('.win-audio')
                winList.map((inner, index) => {
                  setTimeout(() => {
                    winVoice(winAudio)
                    Vm.$notify({
                      title: inner.title,
                      message: inner.content,
                      position: 'bottom-right',
                      duration: 3000
                    })
                  }, index * 350)
                })
              }
              resolve(res)
            })
            .catch(err => reject(err))
        }, 3000)
      })
    }
  }
}
