<template>
  <transition name="fade-in-linear">
    <div v-if="isShow" class="wrapper" @click.self="handleWrapperClick">
      <div class="container" ref="container">
        <a
          class="close"
          href="javascript:;"
          style="color: #000;"
          @click="show = false"
        >
          <i class="el-icon-close"></i>
        </a>
        <iframe id="frame" :src="src" frameborder="0"></iframe>
      </div>
    </div>
  </transition>
</template>

<script>
import { alertMixin } from "../mixin";
import { isOpenAnimation } from "../../../config/game-default-config";
// import { getLotteryRule } from '../../../api/json/json'
export default {
  name: "howToPlayAlert",
  mixins: [alertMixin],
  data() {
    return {
      alertType: "",
      gameName: "",
      src: "",
      prizeGroup: ""
    };
  },
  mounted() {},
  watch: {
    show(val) {
      if (val) {
        this.isShow = val;
        console.log(this.isShow);
        if (isOpenAnimation) {
          setTimeout(() => {
            this.$refs.container.style.transition = "all 0.5s";
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        } else {
          setTimeout(() => {
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        }
      } else {
        if (isOpenAnimation) {
          this.$refs.container.style.top = "40%";
          this.$refs.container.style.opacity = 0;
          setTimeout(() => {
            this.isShow = val;
          }, 500);
        } else {
          this.isShow = val;
        }
      }
    },
    alertType(val) {
      if (val === "rulesOfTheGame") {
        this.src = `/lottery-rule/way-rule-${this.gameName.toLowerCase()}.html?color=${localStorage.getItem(
          "theme"
        )}`;
        this.$nextTick(() => {
          let height = this.$refs.container.clientHeight;
          if (height % 2 === 0) {
            this.$refs.container.style.height = height + "px";
          } else {
            this.$refs.container.style.height = height - 1 + "px";
          }
        });
      } else if (val === "bonusPreview") {
        this.src = `/lottery-bonus/way-bonus-${this.gameName.toLowerCase()}.html?color=${localStorage.getItem(
          "theme"
        )}&prize_group=${this.prizeGroup}`;
        this.$nextTick(() => {
          let height = this.$refs.container.clientHeight;
          if (height % 2 === 0) {
            this.$refs.container.style.height = height + "px";
          } else {
            this.$refs.container.style.height = height - 1 + "px";
          }
        });
      }
    }
  }
};
</script>

<style scoped>
@import "../statics/css/transition.css";
.container {
  width: 1200px;
}
#frame {
  width: 100%;
  height: 85vh;
}
@media screen and (max-width: 1300px) {
  .container {
    width: 95%;
    min-width: 1000px;
  }
}
</style>
