export const lotteryNameKey = "LOTTERY_KEY";
export const nowPlayNameKey = "PLAY_TYPE_KEY";
export const nowCoefficientKey = "COEFFICIENT_KEY";

export function saveLotteryName(lotteryName) {
  return window.sessionStorage.setItem(lotteryNameKey, lotteryName);
}

export function getLotteryName() {
  return window.sessionStorage.getItem(lotteryNameKey);
}

export function saveNowPlayName(nowPlayName) {
  return window.sessionStorage.setItem(
    nowPlayNameKey,
    JSON.stringify(nowPlayName)
  );
}

export function getNowPlayName() {
  return JSON.parse(window.sessionStorage.getItem(nowPlayNameKey));
}

export function getCoefficient() {
  return JSON.parse(window.sessionStorage.getItem(nowCoefficientKey));
}

export function saveCoefficient(coefficient) {
  return window.sessionStorage.setItem(
    nowCoefficientKey,
    JSON.stringify(coefficient)
  );
}
