<template>
  <div class="lottery-record-lh-chart">
    <div class="lh-chart-main">
      <div class="lh-chart-title">
        <img :src="lhLogo" alt="lhLogo" />
        <h4>{{ $t("lotteryRecord.trendChart") }}</h4>
      </div>
      <div class="lh-chart-btn clear-fix">
        <div class="lh-chart-btn-left">
          <label class="group-label">{{
            $t("lotteryRecord.lotteryTypes")
          }}</label>
          <el-select
            v-model="selectLottery"
            :placeholder="$t('lotteryRecord.selectLotteryTypes')"
            @change="gameChange"
          >
            <el-option
              v-for="item in allGameList"
              :key="item.value"
              :label="item.label"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
        <div class="lh-chart-btn-right">
          <el-select
            v-model="selectModel2"
            @change="getNowShowDataList"
            class="lh-chart-now-type"
          >
            <el-option
              v-for="item in nowType"
              :key="item.key"
              :label="item.key"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="lh-chart-content">
        <div class="lh-chart-count">
          {{ $t("lotteryRecord.Statistics") }}:
          <span
            >{{ $t("lotteryRecord.long") }} <ins>{{ dragonCount }}</ins></span
          >
          <span
            >{{ $t("lotteryRecord.tiger") }} <ins>{{ tigerCount }}</ins></span
          >
          <span
            >{{ $t("lotteryRecord.and") }} <ins>{{ peaceCount }}</ins></span
          >
        </div>
        <div class="lh-chart-in-scroll" :style="{ height: lhHeight }">
          <div class="lh-chart-in clear-fix">
            <div
              class="chart-in-col white-col"
              :key="index"
              v-for="(item, index) in nowShowDataList"
            >
              <el-tooltip
                placement="bottom"
                v-for="(itemInner, indexInner) in item.list"
                :key="indexInner"
                :enterable="false"
              >
                <div class="tool-tip-game-rule" slot="content">
                  {{ itemInner.issue }}
                </div>
                <span
                  :class="{
                    red: itemInner.label === `${$t('lotteryRecord.long')}`,
                    blue: itemInner.label === `${$t('lotteryRecord.tiger')}`,
                    green: itemInner.label === `${$t('lotteryRecord.and')}`,
                    'under-line': (indexInner + 1) % 5 === 0
                  }"
                >
                  {{ itemInner.label }}
                </span>
              </el-tooltip>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { GetWnNumberForTrend } from "@/api/game/game";
import { getAllGames } from "../../api/userCenter/personalCenter/personal-center";
import lbLogo from "../../statics/img/header_logo_18_8_29.png";
import lhLogo from "../../statics/img/dt-logo.png";
import { mapGetters } from "vuex";

export default {
  name: "lh-chart",
  data() {
    return {
      lbLogo,
      lhLogo,
      selectLottery: {},
      allGameList: [],
      selectModel2: "",
      nowType: [
        {
          key: this.$t("lotteryRecord.wanQian"),
          value: "01"
        },
        {
          key: this.$t("lotteryRecord.wanBai"),
          value: "02"
        },
        {
          key: this.$t("lotteryRecord.wanShi"),
          value: "03"
        },
        {
          key: this.$t("lotteryRecord.wanGe"),
          value: "04"
        },
        {
          key: this.$t("lotteryRecord.qianBai"),
          value: "12"
        },
        {
          key: this.$t("lotteryRecord.qianShi"),
          value: "13"
        },
        {
          key: this.$t("lotteryRecord.qianGe"),
          value: "14"
        },
        {
          key: this.$t("lotteryRecord.baiShi"),
          value: "23"
        },
        {
          key: this.$t("lotteryRecord.baiGe"),
          value: "24"
        },
        {
          key: this.$t("lotteryRecord.shiGe"),
          value: "34"
        }
      ],
      nowShowDataList: [],
      wnNumberList: [],
      dragonCount: 0,
      tigerCount: 0,
      peaceCount: 0,
      lhHeight: ""
    };
  },
  props: ["containerHeight"],
  methods: {
    GetGameMenuFn() {
      return new Promise((resolve, reject) => {
        getAllGames()
          .then(res => {
            if (res.errno === 0) {
              let allGameList = [];
              for (let key in res.data) {
                if (res.data[key].series_id === 1) {
                  allGameList.push({
                    value: res.data[key].id,
                    label: res.data[key].name
                  });
                }
              }
              this.$set(this, "allGameList", allGameList);
              this.selectLottery.value = +this.lotteryId;
              resolve();
            } else {
              reject(new Error(this.$t("errorMessage.errorMessage3")));
            }
          })
          .catch(err => reject(err));
      });
    },
    gameChange(val) {
      this.$loadingShow();
      this.getLotteryRecordData().then(res => {
        if (res.errno === 0) {
          console.log("hide");
          this.$loadingHide();
        }
      });
    },
    getLotteryRecordData() {
      return new Promise((resolve, reject) => {
        GetWnNumberForTrend(this.selectLottery.value)
          .then(res => {
            const { errno } = res;
            if (errno === 0) {
              let wnNumberList = [];
              for (let key in res.data) {
                wnNumberList.push(res.data[key]);
              }
              this.$set(this, "wnNumberList", wnNumberList);
              this.getNowShowDataList();
              resolve(res);
            } else {
              reject(new Error(this.$t("errorMessage.errorMessage4")));
            }
          })
          .catch(err => {
            console.log(err);
            reject(err);
          });
      });
    },
    getNowShowDataList() {
      if (!this.selectModel2) this.selectModel2 = this.nowType[0].value;
      const data = [];
      let dragonCount = 0;
      let tigerCount = 0;
      let peaceCount = 0;
      this.wnNumberList.find((item, index) => {
        if (data.length >= 60) return true;
        const firstNumber = item["wn_number"][+this.selectModel2[0]];
        const secondNumber = item["wn_number"][+this.selectModel2[1]];
        if (index === 0) {
          data.push({
            lhValue: "",
            list: []
          });
        }
        if (firstNumber !== secondNumber) {
          const lhValue =
            firstNumber > secondNumber
              ? this.$t("lotteryRecord.long")
              : this.$t("lotteryRecord.tiger");
          lhValue === this.$t("lotteryRecord.long") && dragonCount++;
          lhValue === this.$t("lotteryRecord.tiger") && tigerCount++;
          if (index === 0) {
            if (data[data.length - 1].lhValue === "") {
              data[data.length - 1].lhValue = lhValue;
              data[data.length - 1].list.push({
                wnNumber: item["wn_number"],
                label: lhValue,
                issue: item.issue,
                officialTime: item["official_time"]
              });
            }
            return;
          }
          if (data[data.length - 1].lhValue === lhValue) {
            data[data.length - 1].list.push({
              wnNumber: item["wn_number"],
              issue: item.issue,
              label: lhValue,
              officialTime: item["official_time"]
            });
          } else {
            data.push({
              lhValue,
              list: [
                {
                  wnNumber: item["wn_number"],
                  issue: item.issue,
                  label: lhValue,
                  officialTime: item["official_time"]
                }
              ]
            });
          }
        } else {
          // 和
          peaceCount++;
          data[data.length - 1].list.push({
            wnNumber: item["wn_number"],
            issue: item.issue,
            label: this.$t("lotteryRecord.and"),
            officialTime: item["official_time"]
          });
        }
      });
      if (data.length < 60) {
        const length = 60 - data.length;
        for (let i = 0; i < length; i++) {
          data.push({
            lhValue: "",
            list: []
          });
        }
      }
      this.$set(this, "dragonCount", dragonCount);
      this.$set(this, "tigerCount", tigerCount);
      this.$set(this, "peaceCount", peaceCount);
      this.$set(this, "nowShowDataList", data);
    }
  },
  computed: {
    ...mapGetters(["gameDetail", "lotteryId"])
  },
  created() {
    this.$loadingShow();
    this.GetGameMenuFn()
      .then(() => {
        return this.getLotteryRecordData();
      })
      .then(() => {
        this.$nextTick(() => {
          this.$loadingHide();
        });
      });
  },
  mounted() {
    setTimeout(() => {
      if (this.containerHeight) {
        this.lhHeight = parseFloat(this.containerHeight) - 194 + "px";
        console.log(this.lhHeight);
      }
    }, 100);
  }
};
</script>

<style scoped>
/* body {
    overflow-y: scroll;
  } */
.lottery-record-lh-chart {
  height: 644px;
}
.lh-chart-main {
  background: #f7f7f7;
  margin: 0 auto;
  border-radius: 10px;
  height: 100%;
}
.lh-chart-logo {
  text-align: center;
}
.lh-chart-title {
  position: relative;
  text-align: center;
}
.lh-chart-title > h4 {
  font-size: 25px;
  font-weight: 600;
  vertical-align: middle;
  display: inline-block;
}
.lh-chart-title > img {
  vertical-align: middle;
}
.lh-chart-title::after,
.lh-chart-title::before {
  content: "";
  height: 1px;
  width: 200px;
  position: absolute;
  top: 50%;
}
.lh-chart-title::after {
  right: 50%;
  margin-right: -300px;
  background: linear-gradient(to left, #e9e5da 0%, #afacac 100%);
}
.lh-chart-title::before {
  margin-left: -300px;
  background: linear-gradient(to right, #e9e5da 0%, #afacac 100%);
  left: 50%;
}
.lh-chart-btn {
  padding: 0 20px;
}
.lh-chart-btn-left {
  float: left;
  margin-right: 20px;
}
.lh-chart-btn-right {
  float: left;
}
.group-label {
  font-size: 15px;
  color: #777;
  margin-right: 5px;
}
.btn-kj {
  display: inline-block;
  padding: 10px 20px;
  text-align: center;
  background-color: #efefef;
  color: #777777;
  border: 1px solid #e1e1e1;
  border-radius: 3px;
  font-size: 15px;
  margin-right: 15px;
}
.lh-chart-count {
  text-align: right;
  border-bottom: 1px solid #ddd;
  background: #efefef;
  font-size: 15px;
  padding: 0px 20px;
  line-height: 33px;
}
.lh-chart-count span {
  color: red;
}
.lh-chart-content {
  border: 1px solid #dddddd;
  max-width: 1403px;
  margin: 10px auto 0;
}
.lh-chart-btn,
.lh-chart-count {
  max-width: 1403px;
  width: 100%;
  margin: 0 auto;
}
.lh-chart-in-scroll {
  width: 100%;
  height: 500px;
  overflow: auto;
}
.lh-chart-content .lh-chart-in {
  width: 1380px;
  display: flex;
  align-items: stretch;
  display: -ms-flexbox;
  -ms-flex-align: stretch;
  min-height: 100%;
}
.chart-in-col {
  width: 52px;
  text-align: center;
  line-height: 21px;
  font-size: 15px;
  flex-shrink: 0;
}
.chart-in-col:nth-of-type(odd) {
  background: #efefef;
}
.chart-in-col:nth-of-type(even) {
  background: #e6e6e6;
}
.chart-in-col span {
  display: inline-block;
  width: 100%;
}
.chart-in-col span.red {
  color: red;
}
.chart-in-col span.under-line {
  border-bottom: 1px solid #a56503;
}
.chart-in-col span.green {
  color: green;
}
.chart-in-col span.blue {
  color: blue;
}
.dt-tab-col > span:nth-of-type(5n) {
  border-bottom: 1px solid #a56503;
}
</style>
<style>
.lh-chart-btn-right .el-select {
  width: 220px !important;
}
.lh-chart-btn-right .el-select .el-select-dropdown__item {
  text-align: center;
}
</style>
