              </th>
              <th colspan="6" class="cq-num">
                {{ $t("lotteryRecord.colorBallNumber") }}
              </th>
            </tr>
            <tr>
              <th colspan="3" rowspan="2" width="22%">
                {{ $t("lotteryRecord.lotteryNumber") }}
              </th>
              <th colspan="2">{{ $t("lotteryRecord.sumValue") }}</th>
              <th rowspan="2" width="8%">
                {{ $t("lotteryRecord.zuSanZuLiu") }}
              </th>
            </tr>
            <tr>
              <th width="8%">{{ $t("lotteryRecord.sumValue") }}</th>
              <th width="8%">{{ $t("lotteryRecord.sizeDanShuAng") }}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(recordItem, index) in recordList" :key="index">
              <td>{{ recordItem.issue }}</td>
              <td v-for="(item, indexs) in recordItem.wn_number" :key="indexs">
                {{ item }}
              </td>
              <td>
                {{
                  parseInt(recordItem.wn_number[0]) +
                    parseInt(recordItem.wn_number[1]) +
                    parseInt(recordItem.wn_number[2])
                }}
              </td>
              <td
                :class="[
                  recordItem.sum_bsde % 2 === 0 ? 'font-blue' : 'font-red'
                ]"
              >
                {{ recordItem.sum_bsde | bsdeFilter }}
              </td>
              <td
                :class="[
                  recordItem.sum_bsde % 2 === 0 ? 'font-blue' : 'font-red'
                ]"
              >
                {{ recordItem.shape | shape3Dfilter }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <Pagination :pageDate="pageDate" @jump="jump"></Pagination>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { GetLotteryShape } from "@/api/lotteryRecord/lotteryRecord";
import logoLB from "../../statics/img/header_logo_18_8_29.png";
import DateTimePicker from "./component/date-time-picker";
import Pagination from "./component/pagination";
import Vm from "../../main";
export default {
  name: "lottery-record",
  components: {
    DateTimePicker,
    Pagination
  },
  data() {
    return {
      logoLB,
      recordList: [],
      activeHz: true,
      activeDxds: 0,
      pageCount: 0,
      currentPage: 1,
      selectDate: "",
      defaultAllGameList: [],
      allGameList: [],
      currentGameListLottery: [],
      tableShow: "",
      selectLottery: "",
      selectLotteryGroup: "",
      beginDate: "", // 开始时间
      endDate: "", // 结束时间
      page: 1,
      gameId: "",
      groupName: [],
      nowGameDetail: "",
      test: "3D",
      pageDate: {
        // 分页参数
        page: 1, // 当前页
        pageSize: 20, // 页尺寸
        pageCount: 0, // 总页数
        totalCount: 0 // 总数量
      },
      lotteryRecord: ""
    };
  },
  props: ["containerHeight"],
  computed: {
    ...mapGetters([
      "gameDetail",
      "lotteryId",
      "isLogin",
      "nowGameType",
      "lotteryMenuList"
    ])
  },
  filters: {
    dxdsFilter: function(val) {
      if (val < 5) {
        if (val % 2 !== 0) {
          return Vm.$t("lotteryRecord.xiaoDan");
        } else {
          return Vm.$t("lotteryRecord.xiaoShuang");
        }
      } else {
        if (val % 2 !== 0) {
          return Vm.$t("lotteryRecord.daDan");
        } else {
          return Vm.$t("lotteryRecord.daShuang");
        }
      }
    },
    heZhiFilter: function(val) {
      if (val === "NaN") {
        return "";
      }
    },
    recordTotal: function(val) {
      if (val === "" || val === undefined) return;
      let total = 0;
      for (var x = 0; x < val.length; x++) {
        total += val[x];
      }
      return total;
    },
    bsFilter: function(val) {
      if (val === 0) {
        return Vm.$t("lotteryRecord.small");
      } else {
        return Vm.$t("lotteryRecord.big");
      }
    },
    deFilter: function(val) {
      if (val === 0) {
        return Vm.$t("lotteryRecord.single");
      } else {
        return Vm.$t("lotteryRecord.double");
      }
    },
    bsdeFilter: function(val) {
      if (val === 1) {
        return Vm.$t("lotteryRecord.xiaoDan");
      } else if (val === 2) {
        return Vm.$t("lotteryRecord.xiaoShuang");
      } else if (val === 3) {
        return Vm.$t("lotteryRecord.daDan");
      } else {
        return Vm.$t("lotteryRecord.daShuang");
      }
    },
    weibOrSFilter: function(val) {
      if (val === 1) {
        return Vm.$t("lotteryRecord.tailBig");
      } else if (val === 0) {
        return Vm.$t("lotteryRecord.tailSmall");
      } else {
        return Vm.$t("lotteryRecord.he");
      }
    },
    lhFilter: function(val) {
      if (val === 0) {
        return Vm.$t("lotteryRecord.tiger");
      } else if (val === 1) {
        return Vm.$t("lotteryRecord.long");
      }
    },
    shape3Dfilter: function(val) {
      if (val === 3) {
        return Vm.$t("lotteryRecord.groupSix");
      } else if (val === 2) {
        return Vm.$t("lotteryRecord.groupThree");
      } else {
        return Vm.$t("lotteryRecord.Leopard");
      }
    },
    xitFilter: function(val) {
      if (val === 2) {
        return Vm.$t("lotteryRecord.erBuTongHao");
      } else if (val === 3) {
        return Vm.$t("lotteryRecord.sanBuTongHao");
      }
    },
    sp_com_bsFilter: function(val) {
      if (val === 1) {
        return Vm.$t("lotteryRecord.heDao");
      } else if (val === 0) {
        return Vm.$t("lotteryRecord.heXiao");
      }
    },
    sp_com_deFilter: function(val) {
      if (val === 1) {
        return Vm.$t("lotteryRecord.heShuang");
      } else if (val === 0) {
        return Vm.$t("lotteryRecord.heDan");
      }
    }
  },
  methods: {
    getTime(e) {
      return (function(e) {
        const date = new Date();
        date.setTime(date.getTime() - 3600 * 1000 * 24 * e);
        return date.Format("yyyy-MM-dd");
      })(e);
    },
    getActiceDxds() {
      this.activeDxds++;
      if (this.activeDxds === 5) {
        this.activeDxds = 0;
      }
    },
    goBettingPage() {
      this.$store["dispatch"]("playTypeGame", {
        lotteryId: this.gameId,
        menuLink: {
          identifier: this.tableShow
        },
        isJump: true
      });
    },
    setCurrentGameListFn() {
      if (this.allGameList.length !== 0) {
        this.allGameList.map(item => {
          if (item.identifier === this.tableShow) {
            this.currentGameListLottery = item.lotteries;
          }
        });
      }
    },
    getAllGamesFn() {
      this.allGameList = this.lotteryMenuList.slice(1);
      this.lotteryMenuList.find((item, index) => {
        if (item.identifier === this.tableShow) {
          this.selectLotteryGroup = item; // 设置默认选中的彩系
          item.lotteries.find((inner, innerIndex) => {
            if (inner.lottery_id === +this.gameId) {
              this.selectLottery = inner; // 设置默认选中的彩种
            }
          });
        }
      });
    },
    gameGroupChange(val) {
      this.selectLottery = {};
      // 切换彩系改变彩种
      this.currentGameListLottery = val.lotteries;
      if (this.currentGameListLottery !== []) {
        this.gameId = this.currentGameListLottery[0].lottery_id;
        this.selectLottery = this.currentGameListLottery[0];
        this.GetWnNumberHistoriesFn(val.identifier, 1);
        // this.$store.dispatch('getNowGameDetail', this.gameId)
      }
    },
    gameChange(val) {
      this.gameId = val.lottery_id;
      this.selectLottery = val;
      this.GetWnNumberHistoriesFn(val.series_identifier, 1);
    },
    GetWnNumberHistoriesFn(tableShow, page) {
      this.$loadingShow();
      GetLotteryShape(
        this.gameId,
        this.beginDate,
        this.endDate,
        page,
        this.pageDate.pageSize
      ).then(res => {
        this.$loadingHide();
        let recordList = [];
        for (let key in res.data.data) {
          recordList.push(res.data.data[key]);
        }
        this.$set(this, "recordList", recordList);
        this.pageDate.page = res.data.page;
        this.pageDate.pageSize = res.data.pagesize;
        this.pageDate.totalCount = res.data.total_count;
        if (res.data.page_count === 0) {
          this.pageDate.pageCount = 1;
        } else {
          this.pageDate.pageCount = res.data.page_count;
        }
        if (tableShow !== undefined) this.tableShow = tableShow;
      });
    },
    // 点击跳转页数
    jump(pageNum) {
      this.GetWnNumberHistoriesFn(undefined, pageNum);
    },
    cellHtml(number) {
      let returnHtml = "";
      switch (Number(number)) {
        case 1:
          returnHtml = "<i></i>";
          break;
        case 2:
          returnHtml = "<i></i><i></i>";
          break;
        case 3:
          returnHtml = "<i></i><i></i><i></i>";
          break;
        case 4:
          returnHtml = "<div><i></i><i></i></div><div><i></i><i></i></div>";
          break;
        case 5:
          returnHtml =
            "<div><i></i><i></i></div><div><i></i></div><div><i></i><i></i></div>";
          break;
        case 6:
          returnHtml = "<i></i><i></i><i></i><i></i><i></i><i></i>";
          break;
      }
      return returnHtml;
    }
  },
  watch: {
    allGameList(val) {
      this.setCurrentGameListFn();
    },
    tableShow(val) {
      console.log(this.tableShow, val);
    }
  },
  created() {
    this.beginDate = this.getTime(0) + " 00:00:00";
    this.endDate = this.getTime(0) + " 23:59:59";
    this.tableShow = this.nowGameType;

    var lottery = null;
    let lotteryId = this.$route.params["lotteryId"] || +this.lotteryId || 0;
    if (lotteryId > 0) {
      this.lotteryMenuList.forEach(item => {
        item.lotteries.forEach(inner => {
          if (+inner.lottery_id === +lotteryId) {
            lottery = inner;
          }
        });
      });
    }
    if (lottery === null) {
      lottery = this.lotteryMenuList[0].lotteries[0];
      lotteryId = lottery.lottery_id;
    }
    lotteryId = lottery.lottery_id;
    this.gameId = lotteryId;
    this.tableShow = lottery.series_identifier;

    Promise.all([
      this.$store.dispatch("getNowGameDetail", {
        lotteryId: lotteryId,
        nowGameName: lottery.identifier
      })
    ]).then(nowGameName => {
      this.nowGameDetail = nowGameName[0]["identifier"];
      this.$loadingHide();
      this.getAllGamesFn();
    });

    this.GetWnNumberHistoriesFn(undefined, 1);
  },
  mounted() {
    setTimeout(() => {
      if (this.containerHeight) {
        this.lotteryRecord = parseFloat(this.containerHeight) - 56 + "px";
      }
    }, 100);
  }
};
</script>

