        box-shadow: 0 1px 1px 1px rgba(255, 255, 255, 0.5),
          inset 0 1px 3px 0 rgba(0, 0, 0, 0.5);
      }
      &.cell1 {
        justify-content: center;
        align-items: center;
        i {
          height: 10px;
          width: 10px;
          box-shadow: 0 1px 1px 1px rgba(255, 255, 255, 0.5),
            inset 0 1px 3px 0 rgba(0, 0, 0, 0.5);
        }
      }

      &.cell2 {
        justify-content: space-around;
        flex-direction: column;
        align-items: center;
      }
      &.cell3 {
        justify-content: space-between;
        flex-direction: column-reverse;
        i {
          &:nth-child(2) {
            align-self: center;
          }
          &:nth-child(3) {
            align-self: flex-end;
          }
        }
      }
      &.cell4 {
        flex-direction: column;
        justify-content: space-between;
        div {
          display: flex;
          flex-direction: row;
