import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import NProgress from "nprogress";

const routes = [
  {
    path: "/error",
    name: "Error",
    component: () => import("../views/errorPage/404.vue")
  },
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/index",
    redirect: { name: "Home" }
  },
  {
    path: "/signin",
    name: "SignIn",
    component: () => import("../views/SignIn.vue")
  },
  {
    path: "/game/:lotteryId",
    name: "Game",
    component: () => import("../views/game/game_8_30.vue")
  },
  {
    path: "/game-record/:lotteryId",
    name: "GameRecord",
    component: () => import("../views/LotteryRecord.vue")
  },
  {
    path: "/user/*",
    name: "User",
    component: () => import("../views/PersonalCenter.vue")
  },
  {
    path: "/financial/*",
    name: "Fund",
    component: () => import("../views/PersonalCenter.vue")
  },
  {
    path: "/notice",
    name: "Notice",
    component: () => import("../views/PersonalCenter.vue")
  },
  {
    path: "/user/amount-changing-record",
    name: "AmountChangingRecord",
    component: () => import("../views/PersonalCenter.vue")
  },
  {
    path: "/user/individual-statistics",
    name: "IndividualStatistics",
    component: () => import("../views/PersonalCenter.vue")
  },
  {
    path: "/user/dragon-tiger-tie",
    name: "DragonTigerTie",
    component: () => import("../views/PersonalCenter.vue")
  },
  {
    path: "/user/trend-chart",
    name: "TrendChart",
    component: () => import("../views/PersonalCenter.vue")
  },
  {
    path: "/download",
    name: "Download",
    component: () => import("../views/personal-center/download.vue")
  }
];

Vue.use(VueRouter);
const router = new VueRouter({
  //mode: "history",
  base: process.env.BASE_URL,
  routes
});

// 配置NProgress进度条选项  —— 动画效果
NProgress.configure({ ease: "ease", speed: 500 });
// 全局路由拦截-进入页面前执行
router.beforeEach((to, from, next) => {
  // NProgress开始进度条
  NProgress.start();
  /* 路由发生变化修改页面title */
  // window.scrollTo(0, 0)
  // if (to.meta.title) document.title = to.meta.title

  //拦截不存在的页面
  if (to.matched.length === 0) {
    console.error("未找到有效路由，停止处理");
    NProgress.done();
    from.name ? next(false) : next("/error");
  } else {
    next(); //如果匹配到正确跳转
  }
});

// 全局后置钩子-常用于结束动画等
router.afterEach(() => {
  NProgress.done();
});

export default router;
