import {
  SET_USERINFO,
  SET_TOKEN,
  SET_LOGIN_STATUS,
  SET_ACCOUNT_INFO,
  SET_TOP_USER_KEY,
  SET_BINDING_BANK_LIST
} from "../nameSpace";

import {
  loginByUsername,
  logout,
  getCurrentUserInfo,
  DemoPlayerLogin
} from "../../api/login/login";

import { GetPlatformSpreadRegisterUrl } from "../../api/user/user";

import {
  checkGoogleAuth,
  getBankCardList,
  bindBankCard,
  deleteBankCard,
  setUserInfo
} from "../../api/userCenter/personalCenter/personal-center";

import Vm from "../../main";

import { getAccount } from "@/api/user/user";

import { saveToken, logOut } from "../../util/auth";

import { encryptionPassword } from "../../util/encryption";

let getPlatformSpreadRegisterUrl = false;
export default {
  state: {
    userInfo: {
      username: "获取中",
      abalance: "获取中",
      isRightData: false
    },
    token: "",
    isLogin: false,
    accountInfo: {
      userBalance: "获取中",
      allBalance: "获取中",
      frozenAccount: "获取中",
      prohibitAmount: "获取中"
    },
    topUserKey: "",
    bindingBankCard: ""
  },
  mutations: {
    [SET_USERINFO](state, userInfo) {
      state.userInfo = userInfo;
      if (userInfo) state.userInfo.isRightData = true;
    },
    [SET_TOKEN](state, token) {
      state.token = token;
      saveToken(token);
    },
    [SET_LOGIN_STATUS](state, isLogin) {
      state.isLogin = isLogin;
    },
    [SET_ACCOUNT_INFO](state, accountInfo) {
      state.accountInfo = accountInfo;
    },
    [SET_TOP_USER_KEY](state, topUserKey) {
      state.topUserKey = topUserKey;
    },
    [SET_BINDING_BANK_LIST](state, bindingBankCard) {
      state.bindingBankCard = bindingBankCard;
    }
  },
  actions: {
    getPlatformSpreadRegisterUrl({ commit }) {
      return new Promise((resolve, reject) => {
        if (getPlatformSpreadRegisterUrl) {
          return reject(new Error("数据正在获取中, 请勿重复提交"));
        }
        GetPlatformSpreadRegisterUrl()
          .then(res => {
            if (res.errno === 0) {
              commit(SET_TOP_USER_KEY, res.data["register_url"]);
              resolve(res);
            } else {
              reject(new Error(res.message));
            }
          })
          .catch(err => {
            reject(err);
          });
      });
    },
    tryPlayLogin({ commit }) {
      return new Promise((resolve, reject) => {
        DemoPlayerLogin()
          .then(res => {
            if (res.errno === 0) {
              commit(SET_USERINFO, res.data);
              commit(SET_TOKEN, res.data.token);
              commit(SET_LOGIN_STATUS, true);
              resolve(res);
            } else {
              reject(res);
            }
          })
          .catch(err => reject(err));
      });
    },
    loginByUsername({ commit }, userInfo) {
      const { username, verificationCode, key } = userInfo;
      return new Promise((resolve, reject) => {
        loginByUsername(
          username,
          encryptionPassword(userInfo),
          verificationCode,
          key
        )
          .then(res => {
            if (res.errno === 0) {
              if (res.data.google_auth === true) {
                Vm.$eventHub.$emit("isCheckGoogleAuth");
              } else {
                commit(SET_USERINFO, res.data);
                commit(SET_TOKEN, res.data.token);
                commit(SET_LOGIN_STATUS, true);
                resolve(res);
              }
            } else {
              reject(res);
            }
          })
          .catch(err => reject(err));
      });
    },
    checkGoogleAuth({ commit }, data) {
      return new Promise((resolve, reject) => {
        checkGoogleAuth(data.username, data.googleAuth)
          .then(res => {
            if (res.errno === 0) {
              commit(SET_USERINFO, res.data);
              commit(SET_TOKEN, res.data.token);
              commit(SET_LOGIN_STATUS, true);
              resolve(res);
            } else {
              resolve(res);
            }
          })
          .catch(err => reject(err));
      });
    },
    loginOut({ commit }) {
      return new Promise((resolve, reject) => {
        logout()
          .then(res => {
            commit(SET_TOKEN, "");
            commit(SET_LOGIN_STATUS, false);
            logOut();
            setTimeout(() => {
              resolve(res);
            }, 0);
          })
          .catch(err => {
            commit(SET_TOKEN, "");
            commit(SET_LOGIN_STATUS, false);
            logOut();
            setTimeout(() => {
              reject(err);
            });
          });
      });
    },
    updateUserInfo({ commit }) {
      return new Promise((resolve, reject) => {
        getCurrentUserInfo()
          .then(res => {
            const { errno, data } = res;
            if (errno === 0) {
              commit(SET_USERINFO, data);
              commit(SET_LOGIN_STATUS, true);
              resolve(data);
            }
          })
          .catch(err => reject(err));
      });
    },
    getUserBalance({ commit }) {
      return new Promise((resolve, reject) => {
        getAccount()
          .then(res => {
            if (res.errno === 0) {
              const {
                balance: allBalance,
                frozen: frozenAccount,
                available: userBalance,
                prohibit_amount: prohibitAmount
              } = res.data;
              commit(SET_ACCOUNT_INFO, {
                allBalance,
                frozenAccount,
                userBalance,
                prohibitAmount
              });
              resolve(res);
            } else {
              reject(new Error());
            }
          })
          .catch(err => reject(err));
      });
    },

    //新增
    getBankCardList({ commit }) {
      return new Promise((resolve, reject) => {
        getBankCardList().then(data => {
          if (data.errno === 0) {
            const res = data.data.map(item => {
              return {
                cardId: item.id,
                bankName: item.bank || "",
                accountName: item.account_name || "",
                bankCardNumber: item.account || "",
                bankBranch: item.branch_address || "",
                status: item.status || 1,
                locked: item.locked || 0
              };
            });
            commit(SET_BINDING_BANK_LIST, res);
            resolve(res);
          } else {
            reject(new Error(data.message));
          }
        });
      });
    },
    bindBankCard({ commit, dispatch, getters }, data) {
      return new Promise((resolve, reject) => {
        bindBankCard(data).then(data => {
          if (data.errno === 0) {
            dispatch("getBankCardList");
            resolve();
          } else {
            reject(data.message);
          }
        });
      });
    },
    deleteBankCard({ commit, dispatch, getters }, data) {
      return new Promise((resolve, reject) => {
        deleteBankCard(data.cardId, data.checkedToken).then(data => {
          if (data.errno === 0) {
            dispatch("getBankCardList");
            resolve();
          } else {
            reject(data.message);
          }
        });
      });
    },
    setUserNickname({ commit, dispatch, getters }, nickname) {
      return new Promise((resolve, reject) => {
        const data = {};
        data.name = getters.userInfo.name || "";
        data.nickname = nickname;
        data.email = getters.userInfo.email || "";
        data.skype = getters.userInfo.skype || "";
        data.mobile = getters.userInfo.mobile || "";
        data.betConfirm = getters.userInfo.bet_confirm || 0;
        data.openPromptVoice = getters.userInfo.open_prompt_voice || 0;
        data.wonNoticeEnabled = getters.userInfo.won_notice_enabled || 0;
        data.qq = getters.userInfo.qq || "";
        setUserInfo(data).then(data => {
          if (data.errno === 0) {
            dispatch("updateUserInfo");
            resolve();
          } else {
            reject(data.message);
          }
        });
      });
    },
    setUserInfo({ commit, dispatch, getters }, data) {
      return new Promise((resolve, reject) => {
        setUserInfo(data).then(data => {
          if (data.errno === 0) {
            dispatch("updateUserInfo");
            resolve();
          } else {
            reject(data.message);
          }
        });
      });
    }
  }
};
