<template>
  <div>
    <div class="account-profit centerBox">
      <div class="condition">
        <DateTimePicker
          :begin_date.sync="begin_date"
          :end_date.sync="end_date"
        ></DateTimePicker>
        <el-button class="change_btn_bg" @click="query()">{{
          $t("gameRecord.query")
        }}</el-button>
      </div>
      <div class="content">
        <UserTable
          class="table1"
          :tablehade="tableHead"
          :userActive="userActive"
          style="overflow-y: auto;"
          :style="{ height: userTableHeight }"
        >
          <li v-for="(item, index) in tableMsg" :key="index">
            <div>
              <p>{{ item.username }}</p>
            </div>
            <div style="width: 160px">
              <p>{{ item.date }}</p>
            </div>
            <div>
              <p>{{ item.turnover }}</p>
            </div>
            <div>
              <p>{{ item.prize }}</p>
            </div>
            <div>
              <p
                :style="{
                  color: parseFloat(item.profit) < 0 ? '#008000' : '#ff0000'
                }"
              >
                {{ item.profit }}
              </p>
            </div>
          </li>
          <li v-show="tableMsg.length !== 0" style="background: #d8eaf6">
            <div></div>
            <div style="width: 160px">
              <p>{{ $t("alertComponent.subtotal") }}</p>
            </div>
            <div>
              <p
                :style="{
                  color:
                    parseFloat(getTotalNum(tableMsg, 'turnover', 4)) < 0
                      ? '#008000'
                      : '#ff0000'
                }"
              >
                {{ getTotalNum(tableMsg, "turnover", 4) }}
              </p>
            </div>
            <div>
              <p
                :style="{
                  color:
                    parseFloat(getTotalNum(tableMsg, 'prize', 4)) < 0
                      ? '#008000'
                      : '#ff0000'
                }"
              >
                {{ getTotalNum(tableMsg, "prize", 4) }}
              </p>
            </div>
            <div>
              <p
                :style="{
                  color:
                    parseFloat(getTotalNum(tableMsg, 'profit', 4)) < 0
                      ? '#008000'
                      : '#ff0000'
                }"
              >
                {{ getTotalNum(tableMsg, "profit", 4) }}
              </p>
            </div>
          </li>
          <li v-show="tableMsg.length !== 0" style="background: #dddddd">
            <div></div>
            <div style="width: 160px">
              <p>{{ $t("alertComponent.total") }}</p>
            </div>
            <div>
              <p
                :style="{
                  color:
                    parseFloat(totally.turnover) < 0 ? '#008000' : '#ff0000'
                }"
              >
                {{ totally.turnover }}
              </p>
            </div>
            <div>
              <p
                :style="{
                  color: parseFloat(totally.prize) < 0 ? '#008000' : '#ff0000'
                }"
              >
                {{ totally.prize }}
              </p>
            </div>

            <div>
              <p
                :style="{
                  color: parseFloat(totally.profit) < 0 ? '#008000' : '#ff0000'
                }"
              >
                {{ totally.profit }}
              </p>
            </div>
          </li>
          <li v-show="tableMsg === false">
            <span class="nomsg">{{ $t("alertComponent.noData") }}</span>
          </li>
        </UserTable>
        <Pagination :pageDate="pageDate" @jump="jump"></Pagination>
      </div>
    </div>
  </div>
</template>

<script>
import DateTimePicker from "./component/date-time-picker";
import UserTable from "./component/user-table";
import Pagination from "./component/pagination";
import { GetUserProfitData } from "../../api/userCenter/personalCenter/personal-center";
import { getTotalNum } from "../../common/js/operation";

export default {
  name: "account-profit",
  data() {
    return {
      begin_date: "", // 开始时间
      end_date: "", // 结束时间
      gameTypes: [{ value: "", label: "全部" }], // 游戏种类
      gameType: "",
      tableHead: [
        { title: this.$t("alertComponent.UserName") },
        { title: this.$t("alertComponent.time") },
        { title: this.$t("alertComponent.SalesVolume") },
        { title: this.$t("alertComponent.winningAmount") },
        { title: this.$t("alertComponent.ProfitAndLoss") }
      ],
      userActive: 1,
      tableMsg: [],
      totally: {},
      pageDate: {
        // 分页参数
        page: 1, // 当前页
        pageSize: 14, // 页尺寸
        pageCount: 0, // 总页数
        totalCount: 0 // 总数量
      },
      userTableHeight: "516px"
    };
  },
  props: ["containerHeight"],
  components: {
    DateTimePicker,
    UserTable,
    Pagination
  },
  created() {},
  mounted() {
    this.begin_date = this.getTime(30) + " 00:00:00";
    this.end_date = this.getTime(0) + " 23:59:59";
    this.getIntervalStatistics(1);
    if (this.containerHeight) {
      this.userTableHeight = parseFloat(this.containerHeight) - 180 + "px";
      let height = parseFloat(this.containerHeight) - 270;
      this.pageDate.pageSize = Math.floor(height / 30);
    }
  },
  methods: {
    getTime(e) {
      return (function(e) {
        const date = new Date();
        date.setTime(date.getTime() - 3600 * 1000 * 24 * e);
        return date.Format("yyyy-MM-dd");
      })(e);
    },
    // 个人区间统计数据
    getIntervalStatistics(page) {
      this.$loadingShow();
      GetUserProfitData(
        this.begin_date,
        this.end_date,
        this.gameType,
        page,
        this.pageDate.pageSize
      ).then(res => {
        if (res.errno === 0) {
          let tableMsg = [];
          for (let key in res.data.lists.data) {
            tableMsg.push(res.data.lists.data[key]);
          }
          this.$set(this, "tableMsg", tableMsg);
          console.log(tableMsg);
          this.totally = res.data.summary;
          this.pageDate.page = res.data.lists.page;
          this.pageDate.pageSize = res.data.lists.pagesize;
          this.pageDate.totalCount = res.data.lists.total_count;
          if (res.data.lists.page_count === 0) {
            this.pageDate.pageCount = 1;
          } else {
            this.pageDate.pageCount = res.data.lists.page_count;
          }
        }
        this.$loadingHide();
      });
    },
    query() {
      this.getIntervalStatistics(1);
    },
    // 点击跳转页数
    jump(pageNum) {
      this.getIntervalStatistics(pageNum);
    },
    // 本页小结
    getTotalNum(data, str, num) {
      return getTotalNum(data, str, num);
    }
  }
};
</script>

<style scoped>
@import "../../statics/css/userCenterCommon.css";
.account-profit .content .user-table .table-body li {
  padding: 6px 0;
}
</style>
