<template>
  <div class="page-index">
    <div class="index-banner">
      <el-carousel height="400px">
        <el-carousel-item v-for="(item, index) in bannerList" :key="index">
          <el-image :src="item.path" fit="scale-down" height></el-image>
        </el-carousel-item>
      </el-carousel>
    </div>

    <div class="main">
      <module1 />
      <module2 />
      <module3 />
      <module4 />
    </div>
    <el-row class="thank-main">
      <el-col :span="24">{{ $t("home.thankAllPlayer") }}</el-col>
    </el-row>
  </div>
</template>

<script>
import module1 from "@/views/home/Module1.vue";
import module2 from "@/views/home/Module2.vue";
import module3 from "@/views/home/Module3.vue";
import module4 from "@/views/home/Module4.vue";
import { getBannerList } from "../api/basic/basic";
import baseUrl from "../config/config";

export default {
  name: "Home",
  data() {
    return {
      bannerList: []
    };
  },
  components: {
    module1,
    module2,
    module3,
    module4
  },
  computed: {},
  methods: {},
  created() {
    getBannerList().then(res => {
      let { errno, data } = res;
      if (errno === 0) {
        this.bannerList = [];
        let array = [];
        data.forEach(item => {
          array.push({
            path: `${baseUrl}${item.path}`,
            url: item.url,
            text: item.text,
            notice_id: item.notice_id
          });
        });
        this.bannerList = array;
      }
    });
  }
};
</script>
