import request from "@/util/request";
import terminalId from "../terminalId";

/**
 * 获取客服链接
 */
export const GetClientServices = () => {
  return request({
    url: "",
    method: "get",
    params: {
      packet: "Basic",
      action: "GetClientServices",
      terminal_id: terminalId
    }
  });
};

/**
 * 获取广告信息
 */
export const getBannerList = () => {
  return request({
    url: "",
    method: "get",
    params: {
      packet: "Notice",
      action: "getBannerList",
      terminal_id: terminalId
    }
  });
};
