import Vue from "vue";
import cbcAlert from "./src/check-bank-card";

import { merge } from "../../util";
import store from "../../store";
import i18n from "../../lang";

let instanceCbc;
let FundCbcAlertConstructor = Vue.extend(cbcAlert);

let initInstanceCheckBankCard = () => {
  instanceCbc = new FundCbcAlertConstructor({
    el: document.createElement("div"),
    store,
    i18n
  });
  document.body.appendChild(instanceCbc.$el);
};
export const fundAlertCheckBankCard = (options = {}) => {
  initInstanceCheckBankCard();
  merge(instanceCbc.$data, options);
  return new Promise((resolve, reject) => {
    instanceCbc.show = true;
    let success = instanceCbc.success;
    let cancel = instanceCbc.cancel;
    instanceCbc.success = () => {
      success();
      resolve("ok");
    };
    instanceCbc.cancel = err => {
      cancel();
      reject(err);
    };
  });
};
