// import getScrollBarWidth from 'element-ui/src/utils/scrollbar-width'
import {
  closeOnClickModal,
  closeOnPressEscape
} from "../../config/game-default-config";
// import getScrollBarWidth from 'element-ui/src/utils/scrollbar-width'
// import getScrollBarWidth from '@/components/dialog/src/scrollbar-width'

export const alertMixin = {
  data() {
    return {
      show: false,
      rootEl: null,
      isShow: false
    };
  },
  methods: {
    closeAlertBox() {
      this.show = false;
    },
    handleWrapperClick() {
      if (!closeOnClickModal) return;
      this.closeAlertBox();
    },
    escKeydown() {
      const _this = this;
      window.addEventListener("keydown", () => {
        if (event.keyCode === 27) {
          _this.closeAlertBox();
        }
      });
      this.$once("hook:beforeDestroy", () => {
        window.removeEventListener("keydown", () => {});
      });
    }
  },
  mounted() {
    if (closeOnPressEscape) {
      this.escKeydown();
    }
  },
  watch: {
    show(val) {
      if (val) {
        this.rootEl =
          this.rootEl ||
          document.querySelector("body > div:nth-of-type(1)") ||
          document.getElementsByTagName("div")[0];
        this.rootEl.className += " blur";
        document.body.style.overflow = "hidden";
        // document.body.style.paddingRight = `${getScrollBarWidth()}px`
      } else {
        setTimeout(() => {
          this.rootEl.className = this.rootEl.className.replace("blur", "");
          if (this.isSecondAlert) return;
          // document.body.style.paddingRight = `0px`
          document.body.style.overflow = "auto";
          this.$el.parentNode.removeChild(this.$el);
        }, 300);
      }
    }
  },
  computed: {}
};
