import { getGameDetail } from "../../api/game/game";

const mixin = {
  data: function() {
    return {
      lotteries: []
    };
  },
  methods: {
    getLastestIssue: function(lotteryId) {
      getGameDetail(lotteryId).then(res => {
        let { errno, data } = res;
        if (errno === 0) {
          let { current_issue, last_issue, identifier } = data;
          identifier = identifier.toLowerCase();
          if (identifier === "ggk3") identifier = "luckydice";

          if (this[identifier] && this[identifier].timer)
            clearInterval(this[identifier].timer);
          this[identifier].codes = last_issue.wn_number;
          this[identifier].color = last_issue.color;
          this[identifier].issue = last_issue.issue;
          this[identifier].second_left = current_issue.remain_time;
          this.formatTime(identifier, this[identifier].second_left);

          this[identifier].timer = setInterval(() => {
            this[identifier].second_left--;
            if (this[identifier].second_left < 0) {
              clearInterval(this[identifier].timer);
              this.getLastestIssue(lotteryId);
              return;
            }
            this.formatTime(identifier, this[identifier].second_left);
          }, 1000);

          if (this.lotteries.indexOf(identifier) < 0) {
            this.lotteries.push(identifier);
          }
        }
      });
    },
    formatTime: function(identifier, seconds) {
      this[identifier].time_left.day = parseInt(seconds / (24 * 3600));
      this[identifier].time_left.hour = parseInt(
        (seconds - this[identifier].time_left.day * 24 * 3600) / 3600
      );
      this[identifier].time_left.minute = parseInt(
        (seconds -
          this[identifier].time_left.day * 24 * 3600 -
          this[identifier].time_left.hour * 3600) /
          60
      );
      this[identifier].time_left.second = parseInt(
        seconds -
          this[identifier].time_left.day * 24 * 3600 -
          this[identifier].time_left.hour * 3600 -
          this[identifier].time_left.minute * 60
      );
    }
  },
  beforeDestroy: function() {
    this.lotteries.forEach(identifier => {
      clearInterval(this[identifier].timer);
    });
  }
};

export default mixin;
