<template>
  <div class="recharge-box">
    <div class="title">{{ $t("top.recharge") }}</div>
    <el-form ref="form" class="form-recharge" :model="form" label-width="140px">
      <el-form-item :label="$t('recharge.rechargeChannel')" prop="resource">
        <el-radio-group v-model="form.resource">
          <el-radio label="EGF"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        :label="$t('recharge.rechargeInstructions')"
        prop="description"
      >
        <table class="description-table">
          <tr>
            <th>{{ $t("recharge.minimumLimit") }}</th>
            <th>{{ $t("recharge.MaximumLimit") }}</th>
            <th>{{ $t("recharge.rechargeTimeLimit") }}</th>
          </tr>
          <tr>
            <td class="blue">1.00</td>
            <td class="blue">70,000.00</td>
            <td>30{{ $t("recharge.minute") }}</td>
          </tr>
          <tr>
            <td class="blue" colspan="3" style="font-size: 15px">
              {{ $t("recharge.noCreditCard") }}
            </td>
          </tr>
        </table>
      </el-form-item>
      <el-form-item :label="$t('recharge.rechargeAmount')">
        <el-input-number
          v-model="form.amount"
          :ref="`recharge-input-number`"
          controls-position="right"
          @change="handleChange"
          :max="70000"
          :min="1"
        ></el-input-number>
        <div class="recharge-range">{{ $t("recharge.rechargeRange") }}</div>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">{{
          $t("recharge.rechargeNow")
        }}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  closeOnClickModal,
  closeOnPressEscape
} from "../../config/game-default-config";

export default {
  data() {
    return {
      form: {
        amount: "",
        resource: "EGF"
      }
    };
  },
  methods: {
    onSubmit() {
      console.log("提交数据");
      this.$message_box
        .confirm(
          `充值渠道: ${this.form.resource}, 充值金额: ${this.form.amount}`,
          `确认充值`,
          {
            confirmButtonText: this.$t("popup.confirm"),
            cancelButtonClass: "el-btn-close",
            closeOnClickModal: closeOnClickModal,
            closeOnPressEscape: closeOnPressEscape
          }
        )
        .then(() => {
          var f = document.createElement("form");
          f.action = `/deposit/quick/1`;
          f.method = "POST"; // 指定为post
          f.innerHTML = `<input type='hidden' name='amount' value='${this.form.amount}'/>
            <input type='hidden' name='step' value='2'/>
            <input type='hidden' name='dodespoit' value='1'/>`;
          document.body.appendChild(f);
          f.submit();
        });
      /* .then(res => {
          window.open('about:blank').document.write(res)
        }) */
    },
    handleChange() {
      console.log(1111);
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs[`recharge-input-number`].currentValue = "";
      // this.$refs[`recharge-input-number`].currentValue = this.form.amount
    });
  }
};
</script>

<style scoped>
.title {
  line-height: 40px;
  font-size: 18px;
  color: #666666;
  text-indent: 20px;
  font-weight: bold;
  background-color: rgb(242, 242, 242);
  border-bottom: 1px solid rgb(204, 204, 204);
}
.form-recharge {
  margin-top: 20px;
}
.description-table {
  width: 640px;
  border-left: 1px solid rgb(218, 223, 227);
  border-right: 1px solid rgb(218, 223, 227);
}
.description-table > tr > th {
}
.description-table > tr:nth-of-type(2n) {
  background-color: #fff;
}
.blue {
  color: rgb(25, 79, 162);
}
.description-table > tr:nth-of-type(2n + 1) {
  background-color: rgb(242, 242, 242);
}
.description-table > tr > td {
  text-align: center;
  font-weight: 700;
}
.recharge-range {
  margin-top: 20px;
  font-weight: 600;
  font-size: 15px;
  color: rgb(251, 75, 85);
}
</style>
