export const siteDomain = window["siteDomain"];

function GetUrlRelativePath() {
  var url = document.location.toString();
  var arrUrl = url.split("//");

  var start = arrUrl[1].indexOf("/");
  var relUrl = arrUrl[1].substring(start); // stop省略，截取从start开始到结尾的所有字符

  if (relUrl.indexOf("?") !== -1) {
    relUrl = relUrl.split("?")[0];
  }
  return relUrl;
}

function getAllPath() {
  return window.sessionStorage.getItem("ALL-PATH");
}

export function setAllPath(allPath) {
  allPath = allPath || GetUrlRelativePath();
  return window.sessionStorage.setItem("ALL-PATH", allPath);
}

export function initIsFirstEnter() {
  const path = getAllPath() || GetUrlRelativePath();
  if (path.indexOf(nowGameName) !== -1) {
    setAllPath(window.location.href.replace(`/${nowGameName}`, ""));
    return true;
  } else {
    return false;
  }
}

export function getNowGameName() {
  return GetUrlRelativePath().split("/")[2];
}

export function syncLocation(changName) {
  const nowGameName = GetUrlRelativePath().split("/")[2];
  window.location.href = window.location.href.replace(nowGameName, changName);
}
