<template>
  <div class="user-table">
    <div class="table-head change_bg_color">
      <div
        :class="{ userActive: userActive === index }"
        v-for="(item, index) in tablehade"
        :key="index"
      >
        {{ item.title }}
      </div>
    </div>
    <ul class="table-body">
      <slot></slot>
    </ul>
    <div class="change_bg_color" style="height: 2px"></div>
  </div>
</template>

<script>
export default {
  name: "user-table",
  props: ["tablehade", "userActive"]
};
</script>

<style scoped>
.user-table {
  width: 100%;
  text-align: center;
}
.table-head {
  width: 100%;
  height: 33px;
  display: flex;
  justify-content: space-between;
  display: -ms-flexbox;
  -ms-flex-pack: justify;
}
.table-head div {
  display: inline-block;
  width: 100px;
  font-size: 12px;
  font-weight: 500;
  line-height: 33px;
  overflow: hidden;
  color: #fff;
}
.table-head .userActive {
  width: 160px;
}
.table-body {
  width: 100%;
}
.table-body li {
  padding: 2px 0;
  border-bottom: 1px solid #e7e7e7;
  display: flex;
  justify-content: space-between;
  display: -ms-flexbox;
  -ms-flex-pack: justify;
}
.table-body li div {
  display: inline-block;
  width: 100px;
  font-size: 12px;
  font-weight: 500;
  color: #777;
  overflow: hidden;
}
.table-body li div p {
  line-height: 17px;
  margin: 0 5px;
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.table-body li:nth-of-type(odd) {
  background-color: #f9f9f9;
}
.actions {
  cursor: pointer;
  margin: 0 5px;
}
.green {
  color: green;
}
.red {
  color: red;
}
.nomsg {
  display: inline-block;
  width: 100%;
  line-height: 32px;
  text-align: center;
}
.account-bonus .content .user-table .table-head div:nth-child(1) {
  width: 30px;
}
.account-bonus .content .user-table .table-head div:nth-child(2) {
  width: 30px;
}
.account-bonus .content .user-table .table-head div:nth-child(5) {
  width: 30px;
}
.account-bonus .content .user-table .table-body li div:nth-child(1) {
  width: 30px;
}
.account-bonus .content .user-table .table-body li div:nth-child(2) {
  width: 30px;
}
.account-bonus .content .user-table .table-body li div:nth-child(5) {
  width: 30px;
}
</style>

