<template>
  <transition name="fade-in-linear">
    <div v-if="isShow" class="wrapper" @click.self="handleWrapperClick">
      <div class="container" ref="container">
        <a class="close" @click="closeAlertBox" href="javascript:;">
          <i class="el-icon-close"></i>
        </a>
        <div class="title change_bg_color" v-if="!isSpecimen">
          {{ $t("alertComponent.Merchandiser") }}
        </div>
        <div class="title change_bg_color" v-if="isSpecimen">
          {{ $t("alertComponent.DocumentaryNote") }}
        </div>
        <div class="modal-body" :class="{ 'padding14-5': !isSpecimen }">
          <fieldset :class="{ 'padding10-5': isSpecimen }">
            <legend>{{ $t("alertComponent.NoteContent") }}</legend>
            <table class="table table-hover table-striped" v-if="!isSpecimen">
              <tbody>
                <tr>
                  <th>{{ $t("alertComponent.Game") }}</th>
                  <th>{{ $t("alertComponent.wanFa") }}</th>
                  <th>{{ $t("alertComponent.BetContent") }}</th>
                  <th>{{ $t("alertComponent.multiple") }}</th>
                </tr>
                <tr
                  id="seek_"
                  v-for="(item, index) in shoppingCarList"
                  :key="index"
                >
                  <td>
                    <span>{{ item.lotteryName }}</span>
                  </td>
                  <td>
                    <span>{{ item.tabName }}_{{ item.typeName }}</span>
                  </td>
                  <td class="gdNumber">
                    <el-tooltip placement="top" :enterable="false">
                      <div slot="content">{{ showNumberList(item) }}</div>
                      <div class="choice-number-show">
                        {{ showNumberList(item) }}
                      </div>
                    </el-tooltip>
                  </td>
                  <td>
                    <span
                      >{{ item.multiple }}({{ item.coefficientLabel }})</span
                    >
                  </td>
                </tr>
              </tbody>
            </table>
            <table class="table table-hover table-striped" v-if="isSpecimen">
              <tbody>
                <tr>
                  <th>{{ $t("alertComponent.Game") }}</th>
                  <th>{{ $t("alertComponent.wanFa") }}</th>
                  <th>{{ $t("alertComponent.injectionNumber") }}</th>
                  <th>{{ $t("alertComponent.amountOfMoney") }}</th>
                  <th>{{ $t("alertComponent.Commission") }}</th>
                </tr>
                <tr
                  id="seek_2"
                  v-for="(item, index) in shoppingCarList"
                  :key="index"
                >
                  <td>
                    <span>{{ item.lottery }}</span>
                  </td>
                  <td>
                    <span>{{ item.way }}</span>
                  </td>
                  <td>
                    <span>{{ item.count }}</span>
                  </td>
                  <td>
                    <span>{{ item.amount }}</span>
                  </td>
                  <td>
                    <span>{{ item.rate * 100 }}%</span>
                  </td>
                </tr>
              </tbody>
            </table>
          </fieldset>
          <div class="is-specimen" v-if="isSpecimen">
            <p>
              {{ $t("alertComponent.TotalSum") }}:<span>{{
                shoppingCarList[0]["amount"]
              }}</span>
            </p>
            <p>
              {{ $t("alertComponent.WarmTip") }}10％{{
                $t("alertComponent.TopPrize")
              }}10000{{ systemSettingConfig.currency }}
            </p>
          </div>
          <fieldset v-if="!isSpecimen">
            <legend>{{ $t("alertComponent.DocumentarySetting") }}</legend>
            {{ $t("alertComponent.CommissionRate") }}：
            <el-select
              id="follow_commission_percent"
              class="selectMoney"
              v-model="optionsValue"
              @change="changeYJ"
            >
              <el-option
                v-for="item in Options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </fieldset>
          <table class="table table-hover table-striped" v-if="!isSpecimen">
            <tfoot>
              <tr v-for="(item, index) in shoppingCarList" :key="index">
                <td class="text-center">
                  {{ $t("alertComponent.TotalSum") }}:{{ item.selectedMoney }}
                </td>
              </tr>
              <tr class="Tips">
                <td>
                  {{ $t("alertComponent.WarmTip") }}10％{{
                    $t("alertComponent.TopPrize")
                  }}10000{{ $t("alertComponent.currency") }}
                </td>
              </tr>
              <tr class="rule">
                <td>
                  <br />
                  <b>{{ $t("alertComponent.DocumentaryRules") }}</b
                  ><br />
                  <ul>
                    <li>{{ $t("alertComponent.DocumentaryRules1") }}</li>
                    <li>{{ $t("alertComponent.DocumentaryRules2") }}</li>
                    <li>{{ $t("alertComponent.DocumentaryRules3") }}</li>
                    <li>{{ $t("alertComponent.DocumentaryRules4") }}</li>
                    <li>{{ $t("alertComponent.DocumentaryRules5") }}</li>
                  </ul>
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
        <div class="modal-footer">
          <!--<button class="btn btn-sm submit" :loading="isSubmiting" type="button" @click="submitBetList">提 交</button>
            <button class="btn btn-sm cancel" type="button" @click="closeAlertBox" data-dismiss="modal">取 消</button>-->
          <el-button
            size="mini"
            class="submit"
            :loading="isSubmiting"
            @click="submitBetList"
            >{{ $t("alertComponent.confirm") }}</el-button
          >
          <el-button size="mini" class="cancel" @click="closeAlertBox">{{
            $t("alertComponent.cancel")
          }}</el-button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import closeIcon from "../../../statics/img/close_icon_game_alert_zhtz.png";
import { alertMixin } from "../mixin";
import { showNumberList } from "../../../util";
import { mapGetters } from "vuex";
import { betInCommon } from "../statics/js/util";
import { FollowProjects } from "@/api/game/game";
import { SET_MARQUEE_INTERVAL_FLAG } from "../../../store/nameSpace";
import { isOpenAnimation } from "../../../config/game-default-config";
export default {
  name: "fqgd-alert",
  mixins: [alertMixin],
  data() {
    return {
      closeIcon,
      showNumberList,
      isCreateNumber: false,
      chasingInfo: null,
      mode: "normal",
      name: "fqgd-alert",
      shoppingCarList: [],
      lotteryId: "",
      Options: [],
      optionsValue: "0",
      enAbled: 0,
      maxRate: 0,
      minRate: 0,
      stepRate: 0,
      isSpecimen: false,
      isSubmiting: false
    };
  },
  computed: {
    ...mapGetters({
      menuList: "menu",
      selectedCounts: "sscSelectedCounts",
      myLotteryId: "lotteryId"
    }),
    ...mapGetters(["defaultSetting", "systemSettingConfig"])
  },
  methods: {
    changeYJ(val) {
      this.optionsValue = val;
    },
    submitBetList() {
      if (this.isSpecimen) {
        this.isSubmiting = true;
        FollowProjects(this.shoppingCarList[0].specimen_id)
          .then(res => {
            if (res.errno === 0) {
              this.$message({
                message: this.$t("successMessage.successMessage4"),
                type: "success"
              });
            } else if (res.errno === 10301) {
              this.$message({
                message: res.message,
                type: "error"
              });
            }
            this.isSubmiting = false;
            this.show = false;
          })
          .catch(err => {
            console.log(err);
            this.$message({
              message: err.message,
              type: "error"
            });
            this.isSubmiting = false;
            this.show = false;
          });
      } else {
        this.isSubmiting = true;
        betInCommon(
          {
            lotteryId: this.myLotteryId,
            shoppingCarList: this.shoppingCarList,
            mode: this.mode,
            chasingInfo: this.chasingInfo
          },
          1,
          this.optionsValue
        )
          .then(res => {
            const { errno } = res;
            if (errno === 0) {
              this.$message({
                message: this.$t("successMessage.successMessage3"),
                type: "success"
              });
            } else if (res.errno === 4063) {
              // 对玩法关闭的错误码进行单独的流程处理
              this.$message({
                message: res.message,
                type: "error"
              });
            }
            this.$store.dispatch("getUserBalance");
            this.$store.dispatch("clearSelectedBalls");
            this.isSubmiting = false;
            this.show = false;
          })
          .catch(err => {
            console.log(err);
            this.$message({
              message: err.message,
              type: "error"
            });
            this.isSubmiting = false;
            this.show = false;
          });
      }
    },
    getOptions() {
      let map = [];
      this.$nextTick(() => {
        if (this.isSpecimen) return;
        this.enAbled = this.defaultSetting["fp_enabled"];
        this.maxRate = parseFloat(this.defaultSetting["fp_max_rate"]);
        this.minRate = parseFloat(this.defaultSetting["fp_min_rate"]);
        this.stepRate = parseFloat(this.defaultSetting["fp_rate_step"]);
        for (let i = this.minRate; i <= this.maxRate; i += this.stepRate) {
          map.push({
            value: i,
            label: Math.floor(i * 100) + "%"
          });
        }
      });
      this.Options = map;
    }
  },
  watch: {
    show(val) {
      if (val) {
        this.$store.commit(SET_MARQUEE_INTERVAL_FLAG, false);
        this.isShow = val;
        if (isOpenAnimation) {
          setTimeout(() => {
            this.$refs.container.style.transition = "all 0.5s";
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        } else {
          setTimeout(() => {
            this.$refs.container.style.top = "50%";
            this.$refs.container.style.opacity = 1;
          }, 20);
        }
      } else {
        this.$store.commit(SET_MARQUEE_INTERVAL_FLAG, true);
        if (isOpenAnimation) {
          this.$refs.container.style.top = "40%";
          this.$refs.container.style.opacity = 0;
          setTimeout(() => {
            this.isShow = val;
          }, 500);
        } else {
          this.isShow = val;
        }
      }
    }
  },
  created() {
    this.getOptions();
  }
};
</script>

