<template>
  <div :class="footercss">
    <div class="desc" v-show="showTime">
      <p>{{ $t("home.timeTo") }}</p>
      <p>{{ $t("home.nextDraw") }}</p>
    </div>
    <countdown
      v-show="showTime"
      :day="day"
      :hour="hour"
      :minute="minute"
      :second="second"
    />
    <div class="play">
      <a :href="url" :class="buttoncss">{{ $t("home.playLottoNow") }}</a>
    </div>
  </div>
</template>

<script>
import countdown from "@/views/home/CountDown.vue";

export default {
  components: {
    countdown
  },
  props: [
    "type",
    "buttonType",
    "showTime",
    "day",
    "hour",
    "minute",
    "second",
    "url"
  ],
  computed: {
    footercss: function() {
      return this.type === "normal"
        ? "ft"
        : this.type === "small"
        ? "ft ft-small"
        : "ft ft-middle";
    },
    buttoncss: function() {
      return this.buttonType === "normal"
        ? "button-yellow"
        : this.buttonType === "small"
        ? "button-yellow button-yellow-small"
        : "button-yellow button-yellow-middle";
    }
  },
  mounted: function() {}
};
</script>
