<template>
  <div class="module-1">
    <div class="l">
      <lotteryCard
        lottery-name="Manx MEGA Millions"
        lottery-type="lhc"
        root-css="col-1"
        logo-css="logo-maxnmega"
        foot-type="normal"
        button-type="normal"
        :lotteryNameOnTop="true"
        :time-on-buttom="true"
        :show-operation="true"
        :day="manxmegamillions.time_left.day"
        :hour="manxmegamillions.time_left.hour"
        :minute="manxmegamillions.time_left.minute"
        :second="manxmegamillions.time_left.second"
        :issue="manxmegamillions.issue"
        :codes="manxmegamillions.codes"
        :colors="manxmegamillions.color"
        :lottery-id="manxmegamillions.lotteryId"
        :prize="manxmegamillions.prize"
      />

      <lotteryCard
        lottery-name="Gold 60s"
        root-css="col-2"
        logo-css="logo-gold60"
        foot-type="normal"
        button-type="normal"
        :lotteryNameOnTop="false"
        :time-on-buttom="false"
        :show-operation="false"
        :day="gold60s.time_left.day"
        :hour="gold60s.time_left.hour"
        :minute="gold60s.time_left.minute"
        :second="gold60s.time_left.second"
        :issue="gold60s.issue"
        :codes="gold60s.codes"
        :lottery-id="gold60s.lotteryId"
        :prize="gold60s.prize"
      />

      <div class="col-all">
        <a href="#/" class="btn button-white">{{ $t("home.learnMore") }}</a>
        <div class="step">
          <div class="operation">
            <span
              :class="active === 0 ? 'active' : ''"
              v-on:click="changeCarousel(0)"
              >{{ $t("home.step1") }}</span
            >
            <span
              :class="active === 1 ? 'active' : ''"
              v-on:click="changeCarousel(1)"
              >{{ $t("home.step2") }}</span
            >
            <span
              :class="active === 2 ? 'active' : ''"
              v-on:click="changeCarousel(2)"
              >{{ $t("home.step3") }}</span
            >
            <span
              :class="active === 3 ? 'active' : ''"
              v-on:click="changeCarousel(3)"
              >{{ $t("home.step4") }}</span
            >
          </div>
          <div class="imgs">
            <el-carousel
              ref="carousel"
              indicator-position="none"
              @change="carouselChanged"
            >
              <el-carousel-item v-for="item in urls" :key="item">
                <el-image :src="item" fit="scale-down" />
              </el-carousel-item>
            </el-carousel>
          </div>
        </div>
      </div>
    </div>

    <div class="r">
      <div class="desc">
        {{ $t("home.checkYou") }} <br />{{ $t("home.result") }}
      </div>
      <p>{{ $t("home.youWinBig") }}</p>
      <a href="#/" class="button-white">{{ $t("home.checkYouInfo") }}</a>
      <a :href="'#/game-record/' + lotteryId" class="button-white">{{
        $t("home.checkAllResult")
      }}</a>
    </div>
  </div>
</template>

<script>
import mixins from "@/common/mixin/home";
import lotteryCard from "@/views/home/LotteryCard.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  mixins: [mixins],
  components: {
    lotteryCard
  },
  data: function() {
    return {
      active: 0,
      urls: [
        require("../home/static/img/m1-step-1.png"),
        require("../home/static/img/m1-step-2.png"),
        require("../home/static/img/m1-step-3.png"),
        require("../home/static/img/m1-step-4.png")
      ],
      manxmegamillions: {
        lotteryId: 19,
        prize: 1090,
        codes: [],
        color: [],
        issue: "",
        time_left: {
          day: 0,
          hour: 0,
          minute: 0,
          second: 0
        },
        second_left: 0,
        timer: null
      },
      gold60s: {
        lotteryId: 12,
        prize: 400,
        codes: [],
        issue: "",
        time_left: {
          day: 0,
          hour: 0,
          minute: 0,
          second: 0
        },
        second_left: 0,
        timer: null
      }
    };
  },
  computed: {
    ...mapGetters(["lotteryId"])
  },
  methods: {
    changeCarousel(index) {
      this.active = index;
      this.$refs["carousel"].setActiveItem(index);
    },
    carouselChanged(index) {
      this.active = index;
    }
  },
  created: function() {
    this.getLastestIssue(this.manxmegamillions.lotteryId); //manxmegamillions
    this.getLastestIssue(this.gold60s.lotteryId); //gold60s
  }
};
</script>

<style>
.page-index .main .module-1 .col-all .step .el-carousel__indicators,
.page-index .main .module-1 .col-all .step .el-carousel__arrow,
.page-index .main .module-1 .col-all .step .el-carousel__item,
.page-index .main .module-1 .col-all .step .el-carousel__mask {
  position: absolute;
}
</style>

<style lang="sass">
@import ../../common/css/color.sass

.page-index
  .main
    .module-1
      height: 601px
      display: -webkit-box
      display: flex
      -webkit-box-pack: justify
      justify-content: space-between
      margin-top: 25px
      .l
        display: -webkit-box
        display: flex
        width: 936px
        flex-wrap: wrap
        -webkit-box-pack: justify
        justify-content: space-between
        align-content: space-between
        .logo-maxnmega
          left: 16px
          top: 32px
        .top_prize
          line-height: 32px
          font-size: 14px
      .col-1
        background-image: url(../home/static/img/m1-col1-bg.png)
        width: 553px
        height: 335px
        border: solid 1px #ffffff
        .hd
          margin-top: 30px
          margin-left: 130px
          h3
            font-size: 22px
            line-height: 32px
          p
            font-size: 16px
        .balls
          margin-top: 35px
          span
            display: -webkit-box
            display: flex
            -webkit-box-pack: center
            justify-content: center
            -webkit-box-align: center
            align-items: center
            color: #ffffff
            font-size: 40px
          div
            width: 56px
            height: 56px
            border: 6px solid #2585F9
            box-sizing: border-box
          div.green
            border-color: #37DC2D
          div.red
            border-color: #DE1717
          div.blue
            border-color: #2585F9
        .operation
          display: -webkit-box
          display: flex
          justify-content: space-around
          padding: 0 30px
          margin-top: 25px
          .desc
            font-size: 14px
            color: #ffffff
            p
              line-height: 20px
            span
              color: #FFD22B
              padding-left: 5px
      .col-2
        background-image: url(../home/static/img/m1-col2-bg.png)
        width: 365px
        height: 335px
        border: solid 1px #ffffff
        .balls
          margin-top: 15px
        .time
          display: -webkit-box
          display: flex
          -webkit-box-pack: center
          justify-content: center
          -webkit-box-align: center
          align-items: center
          margin-top: 14px
          .desc
            color: #fff
            padding-right: 12px
        .hd
          h3
            font-size: 22px
            line-height: 32px
          p
            font-size: 16px
      .r
        background-image: url(../home/static/img/m1-r-bg.png)
        width: 293px
        height: 601px
        border-radius: 10px
        padding: 40px 35px
        box-sizing: border-box
        .desc
          font-size: 32px
          font-weight: bold
          color: #fff
        a
          width: 100%
          margin-top: 15px
          height: 50px
        p
          font-size: 20px
          color: #fff
          line-height: 50px
      .col-all
        background: -webkit-gradient(linear, left top, left bottom, from(#0561C0), to(#2484F8))
        background: linear-gradient(#0561C0, #2484F8)
        background-image: url(../home/static/img/How-It-Works.png)
        width: 936px
        height: 245px
        border-radius: 10px
        position: relative
        .btn
          position: absolute
          top: 188px
          left: 45px
          width: 196px
          height: 42px
        .step
          position: absolute
          width: 623px
          height: 217px
          left: 297px
          top: 15px
          display: -webkit-box
          display: flex
          background: #fff
          .operation
            padding: 12px
            box-sizing: border-box
            span
              display: block
              background: #d9d9d9
              color: #888
              border-radius: 5px
              width: 220px
              line-height: 38px
              padding: 0 10px
              cursor: pointer
              + span
                margin-top: 12px
              &.active
                background-color: #0568A0
                color: #fff
          .el-carousel
            .el-carousel__container
              width: 380px
              height: 217px
</style>
