import routerNameMap from "../../config/route-name-map";
import vm from "../../main";

export function getRouteNameById(wayId, menus, upperName) {
  if (!wayId || !menus || !menus.length || !upperName) return "index";
  let routeStr;
  upperName = routerNameMap[upperName];
  menus.find(item => {
    return item.children.find(itemChild => {
      return itemChild.children.find(itemInner => {
        if (+itemInner.id === +wayId) {
          routeStr = `${upperName}_${item["name_en"]}_${itemChild["name_en"]}_${itemInner["name_en"]}`;
          return true;
        }
      });
    });
  });
  return routeStr;
}

export const handleUnhandledException = exception => {
  console.error(exception, "global exception");
  vm.$loadingHide();
};
