import request from "@/util/request-json";
import { siteDomain } from "../../util/require";
import { prefix } from "../../config/configVersionConfig";

/**
 *
 *  获取彩种列表文件
 */
export function getLotteryList(path, merchantName) {
  return request({
    url: `${siteDomain}/json/configs/${merchantName}/` + path,
    method: "GET",
    params: {}
  });
}
