/**
 *
 *  获取版本控制文件
 **/
export function getConfigInfoList(isCache) {
  const url = `${siteDomain}/json/configs/${prefix}-lottery-config-list.json${
    isCache ? `?${new Date().getTime()}` : ``
  }`;
  return request({
    url: url,
    method: "GET",
    params: {}
  });
}
/**
 *
 *  获取商户自定义配置对象
 **/
export function getMerchantConfigInfoList(isCache, merchantName) {
  const url = `${siteDomain}/json/configs/${merchantName}/${merchantName}-lottery-config-list.json${
    isCache ? `?${new Date().getTime()}` : ``
  }`;
  return request({
    url: url,
    method: "GET",
    params: {}
  });
}
