export function processWayInFirst(objInfo, defaultId) {
  if (!objInfo) return [];
  let nowPlayName;
  if (defaultId) {
    nowPlayName = getNowPlayName() || [`${defaultId}`];
  } else {
    nowPlayName = getNowPlayName() || [];
  }
  const returnArr = [];
  let isSelected = false;
  objInfo.map(item => {
    const wayInfo = {};
    wayInfo.label = item.name;
    // wayInfo.isSelected = false
    wayInfo.isSelected = nowPlayName[0] === item.name;
    wayInfo.children = [];
    item.children.map(itemInner => {
      const wayInfoInner = {};
      wayInfoInner.label = itemInner.name;
      wayInfoInner.children = [];
      itemInner.children.map(itemThree => {
        const nowLabel = itemThree[1];
        if (!isSelected) {
          isSelected =
            nowPlayName[2] === nowLabel &&
            nowPlayName[1] === itemInner.name &&
            nowPlayName[0] === item.name;
        }
        wayInfoInner.children.push({
          label: nowLabel,
          isSelected:
            nowPlayName[2] === nowLabel &&
            nowPlayName[1] === itemInner.name &&
            nowPlayName[0] === item.name,
          id: itemThree[0]
        });
      });
      wayInfo.children.push(wayInfoInner);
    });
    returnArr.push(wayInfo);
  });
  // debugger
  /* for (let key in objInfo) {
    const wayInfo = {}
    wayInfo.label = key
    // wayInfo.isSelected = false
    wayInfo.isSelected = nowPlayName[0] === key
    wayInfo.children = []
    for (let keyInner in objInfo[key]) {
      const wayInfoInner = {}
      wayInfoInner.label = keyInner
      wayInfoInner.children = []
      for (let keyInnerTwo in objInfo[key][keyInner]) {
        const nowLabel = objInfo[key][keyInner][keyInnerTwo]
        if (!isSelected) isSelected = (nowPlayName[2] === nowLabel && nowPlayName[1] === keyInner && nowPlayName[0] === key)
        wayInfoInner.children.push({
          label: nowLabel,
          isSelected: nowPlayName[2] === nowLabel && nowPlayName[1] === keyInner && nowPlayName[0] === key,
          id: keyInnerTwo
        })
      }
      wayInfo.children.push(wayInfoInner)
    }
    returnArr.push(wayInfo)
  } */
  if (!isSelected && returnArr.length) {
    for (let i = 0; i < returnArr.length; i++) {
      returnArr[i].isSelected = false;
    }
    returnArr[0].isSelected = true;
    returnArr[0].children[0].children[0].isSelected = true;
  }
  return returnArr;
}

