export function processCreditWayInFirst(objInfo, menuInfo, nowGameType) {
  if (!objInfo || !menuInfo || !nowGameType) return [];
  const returnArr = [];
  // const nowHandicapInfo = handicapMenus[`${topTypeNameMap[nowGameType]}HandicapMenus`]
  const nowHandicapInfo = menuInfo;
  nowHandicapInfo &&
    nowHandicapInfo.map(item => {
      const wayInfo = {};
      wayInfo.label = item.label;
      wayInfo.type = item.type;
      wayInfo.isSelected = item.isDefault;
      wayInfo.children = [];
      item.children.map(itemInner => {
        const wayInner = {};
        wayInner.label = itemInner.label;
        if (
          item.type === `integration` &&
          itemInner.type === `doubleLayer` &&
          itemInner.children
        ) {
          wayInner.inline = true;
          itemInner.lineShowCount &&
            (wayInner.lineShowCount = itemInner.lineShowCount);
          wayInner.configInfo = [];
          itemInner.children.map(itemThree => {
            const configInfo = {};
            configInfo.label = itemThree.label;
            configInfo.configInfo = [];
            itemThree[`nameList`].map(itemFour => {
              const configInfoInner = {};
              configInfoInner.id = itemFour.id;
              configInfoInner.label = itemFour.title;
              configInfoInner.configTemplate = objInfo[itemFour.id];
              configInfo.configInfo.push(configInfoInner);
            });
            wayInner.configInfo.push(configInfo);
          });
          wayInfo.children.push(wayInner);
          return true;
        }
        wayInner.configInfo = [];
        if (item.type === "dragonTiger") {
          for (let infoKey in itemInner.children) {
            // itemInner.nameList
            const wayInfoThreeInner = {};
            wayInfoThreeInner.configInfo = [];
            wayInfoThreeInner.label = itemInner.label;
            // wayInfoThreeInner.type = infoKey
            wayInfoThreeInner.type = itemInner.children[infoKey].label;
            for (let infoKeyInner in itemInner.children[infoKey].nameList) {
              // itemInner.nameList[infoKey]
              const configInfo = {};
              // configInfo.id = infoKeyInner
              // configInfo.label = itemInner.nameList[infoKey][infoKeyInner]
              // configInfo.configTemplate = objInfo[infoKeyInner]
              configInfo.id =
                itemInner.children[infoKey].nameList[infoKeyInner].id;
              configInfo.label =
                itemInner.children[infoKey].nameList[infoKeyInner].title;
              configInfo.configTemplate =
                objInfo[itemInner.children[infoKey].nameList[infoKeyInner].id];
              wayInfoThreeInner.configInfo.push(configInfo);
            }
            wayInner.configInfo.push(wayInfoThreeInner);
          }
          /* for (let infoKey in itemInner.nameList) {
          const wayInfoThreeInner = {}
          wayInfoThreeInner.configInfo = []
          wayInfoThreeInner.label = itemInner.label
          wayInfoThreeInner.type = infoKey
          for (let infoKeyInner in itemInner.nameList[infoKey]) {
            const configInfo = {}
            configInfo.id = infoKeyInner
            configInfo.label = itemInner.nameList[infoKey][infoKeyInner]
            configInfo.configTemplate = objInfo[infoKeyInner]
            wayInfoThreeInner.configInfo.push(configInfo)
          }
          wayInner.configInfo.push(wayInfoThreeInner)
        } */
          wayInfo.children.push(wayInner);
          return true;
        }
        itemInner.nameList.map(itemThree => {
          const configInfo = {};
          configInfo.id = itemThree.id;
          configInfo.label = itemThree.title;
          configInfo.configTemplate = objInfo[itemThree.id];
          wayInner.configInfo.push(configInfo);
        });
        wayInfo.children.push(wayInner);
      });
      returnArr.push(wayInfo);
    });

  if (returnArr.length) {
    return returnArr;
  } else {
    return [];
  }
}

export function processLotteryMenuFirst(list) {
  if (!list) return [];
  const lotteryMenuList = [];
  for (let key in list) {
    lotteryMenuList.push(list[key]);
    list[key].key = key;
    const lotteries = [];
    for (let keyInner in list[key]["lotteries"]) {
      lotteries.push(list[key]["lotteries"][keyInner]);
    }
    list[key].lotteries = lotteries;
    for (let icon in lotteryListIcons) {
      if (list[key]["identifier"] === lotteryListIcons[icon].name) {
        list[key].icon = lotteryListIcons[icon].icon;
        list[key].iconFont = lotteryListIcons[icon].iconFont;
      }
    }
  }
  return lotteryMenuList;
}

export function setLrylDataFace(res, nowGameType) {
  let resLens = Object.keys(res);
  if (resLens.length === 0) return;
  /* const lryl = []
  for (let lrylItem in res.data) {
    lryl.push(res.data[lrylItem]) // 重新设置冷热遗漏数据格式为数组
  }
  let lrylData = lryl
   */
  let lrylData = res;
  const lrylArry = [];
  for (let i in lrylData) {
    let lrylItems = [];
    for (let lrylInner in lrylData[i]) {
      lrylItems.push({
        position: +i,
        ball: +lrylInner,
        count: lrylData[i][lrylInner]
      });
    }
    lrylArry.push({
      position: +i,
      data: lrylItems
    });
  }
  lrylArry.map(item => {
    if (!lrYlMapConfigInfo[nowGameType]) return;
    lrYlMapConfigInfo[nowGameType].map(itemInner => {
      if (itemInner.position === item.position) {
        item.type = itemInner.type;
        item.value = itemInner.value;
        item.data = item.data.sort((a, b) => a.ball - b.ball);
      }
    });
  });
  return lrylArry;
}

