<template>
  <div class="countdown">
    <div>
      <span>{{ $t("home.day") }}</span
      >{{ day }}
    </div>
    <div>
      <span>{{ $t("home.hour") }}</span
      >{{ hour }}
    </div>
    <div>
      <span>{{ $t("home.minute") }}</span
      >{{ minute }}
    </div>
    <div>
      <span>{{ $t("home.second") }}</span
      >{{ second }}
    </div>
  </div>
</template>

<script>
export default {
  props: ["day", "hour", "minute", "second"]
};
</script>
