import request from "@/util/request";
import terminalId from "../terminalId";
/**
 * 用户登录
 * @param username 用户账号
 * @param password 用户密码
 * @param captcha 验证码
 * @param key 登录的key值
 */
export function loginByUsername(username, password, captcha, key) {
  return request({
    url: "",
    method: "get",
    params: {
      packet: "User",
      action: "Login",
      username: username,
      password: password,
      terminal_id: terminalId,
      key,
      captcha
    }
  });
}
/**
 * 用户登出
 */
export function logout() {
  return request({
    url: "",
    method: "get",
    params: {
      action: "logout",
      terminal_id: terminalId
    }
  });
}
/**
 * 获取当前当前用户信息
 */
export function getCurrentUserInfo() {
  return request({
    url: "",
    method: "get",
    params: {
      action: "getCurrentUserInfo",
      terminal_id: terminalId
    }
  });
}

/**
 * 用户试玩
 */
export function DemoPlayerLogin() {
  return request({
    url: "",
    method: "post",
    data: {
      action: "DemoPlayerLogin",
      terminal_id: terminalId
    }
  });
}
