<template>
  <div id="app">
    <div class="layout" v-if="isRouterAlive">
      <header-bar />
      <div class="page-main page-index">
        <router-view />
      </div>
      <footer-bar />
    </div>
  </div>
</template>

<script>
import HeaderBar from "./components/HeaderBar";
import FooterBar from "@/components/FooterBar.vue";
import { mapActions, mapGetters } from "vuex";
import { screenShowMap } from "./config/game-default-config";
import Auxiliary from "./common/Auxiliary/auxiliary/index";
import { SET_SCREEN_STATUS, SET_DEVICE } from "./store/nameSpace";

export default {
  name: "App",
  provide() {
    return {
      reload: this.reload
    };
  },
  components: {
    HeaderBar,
    FooterBar
  },
  data() {
    return {
      isRouterAlive: true
    };
  },
  computed: {
    ...mapGetters(["token"])
  },
  methods: {
    ...mapActions([
      "selectLottery",
      "getSystemSettingConfig",
      "getMerchantConfigInfoList",
      "getLotteryMenuList"
    ]),
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(function() {
        this.isRouterAlive = true;
      });
    },
    getScreenStatus() {
      const that = this;
      if (window.addEventListener) {
        let changNowStatusFunc = () => {
          const windowWidth = document.body.clientWidth;
          if (windowWidth > screenShowMap.first) {
            // 设置为状态0
            that.$store.commit(SET_SCREEN_STATUS, 0);
            that.$store.commit(SET_DEVICE, "PC");
          } else if (
            windowWidth < screenShowMap.first &&
            windowWidth > screenShowMap.second
          ) {
            // 状态2
            that.$store.commit(SET_SCREEN_STATUS, 1);
            that.$store.commit(SET_DEVICE, "PC");
          } else if (
            windowWidth < screenShowMap.second &&
            windowWidth > screenShowMap.third
          ) {
            // 状态3
            that.$store.commit(SET_SCREEN_STATUS, 2);
            that.$store.commit(SET_DEVICE, "PC");
          } else if (windowWidth < screenShowMap.third) {
            // 状态4
            that.$store.commit(SET_SCREEN_STATUS, 3);
            that.$store.commit(SET_DEVICE, "H5");
          }
        };
        let windowResizeFunc = Auxiliary.throttle(changNowStatusFunc, 100);
        window.addEventListener("resize", windowResizeFunc);
        changNowStatusFunc();
      }
    }
  },
  created() {
    this.getMerchantConfigInfoList().then(() => {
      this.getSystemSettingConfig();
      this.getLotteryMenuList();
    });
  }
};
</script>

<style lang="sass">
#app
  height: 100vh
  width: 100%
  .layout
    min-height: 100%
    min-width: 1240px
    display: flex
    -webkit-box-orient: vertical
    -webkit-box-direction: normal
    flex-direction: column
    box-sizing: border-box
    .page-main
      margin-top: 60px
      -webkit-box-flex: 1
      flex: auto
      background-color: transparent
      background-size: cover
      display: -webkit-box
      display: flex
      -webkit-box-orient: vertical
      -webkit-box-direction: normal
      flex-direction: column
      position: relative
</style>
